    <div class="zero-code-image-content">
      <!-- å…±é€šç”»åƒã‚¿ãƒ– -->
      <div v-if="activeImageTab === 'common'" class="zero-code-image-section">
        <div class="zero-code-section-header">
          <h4>å…±é€šç”»åƒ ({{ commonImages.length }}ä»¶)</h4>
          <button class="zero-code-img-upload-btn common" @click="openUploadModal('common')">
            å…±é€šç”»åƒã‚’ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰
          </button>
        </div>
        <div class="zero-code-img-grid">
          <div
            v-for="image in commonImages"
            :key="image.id"
            class="zero-code-img-item"
            @click="showImageDetail(image)"
          >
            <img :src="getImageSrc(image)" :alt="getImageAlt(image)">
            <div v-if="isImageUpdated(image)" class="zero-code-img-updated-badge">
              å·®ã—æ›¿ãˆæ¸ˆã¿
            </div>
            <div class="zero-code-img-info">
              <p class="zero-code-img-name">{{ image.name }}</p>
              <p class="zero-code-img-size">{{ formatFileSize(image.fileData?.size || image.size || 0) }}</p>
            </div>
            <button class="zero-code-img-delete-btn" @click.stop="deleteImage(image.id, 'common')">
              å‰Šé™¤
            </button>
          </div>
        </div>
        <div v-if="commonImages.length === 0" class="zero-code-empty-state">
          <p>å…±é€šç”»åƒãŒç™»éŒ²ã•ã‚Œã¦ã„ã¾ã›ã‚“ã€‚</p>
        </div>
      </div>

      <!-- å€‹åˆ¥ç”»åƒã‚¿ãƒ– -->
      <div v-if="activeImageTab === 'individual'" class="zero-code-image-section">
        <div class="zero-code-section-header">
          <h4>å€‹åˆ¥ç”»åƒ ({{ individualImages.length }}ä»¶)</h4>
          <button class="zero-code-img-upload-btn individual" @click="openUploadModal('individual')">
            å€‹åˆ¥ç”»åƒã‚’ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰
          </button>
        </div>
        <div class="zero-code-img-grid">
          <div
            v-for="image in individualImages"
            :key="image.id"
            class="zero-code-img-item"
            @click="showImageDetail(image)"
          >
            <img :src="getImageSrc(image)" :alt="getImageAlt(image)">
            <div v-if="isImageUpdated(image)" class="zero-code-img-updated-badge">
              å·®ã—æ›¿ãˆæ¸ˆã¿
            </div>
            <div class="zero-code-img-info">
              <p class="zero-code-img-name">{{ image.name }}</p>
              <p class="zero-code-img-size">{{ formatFileSize(image.fileData?.size || image.size || 0) }}</p>
            </div>
            <button class="zero-code-img-delete-btn" @click.stop="deleteImage(image.id, 'individual')">
              å‰Šé™¤
            </button>
          </div>
        </div>
        <div v-if="individualImages.length === 0" class="zero-code-empty-state">
          <p>å€‹åˆ¥ç”»åƒãŒç™»éŒ²ã•ã‚Œã¦ã„ã¾ã›ã‚“ã€‚</p>
        </div>
      </div>

      <!-- ãƒ‡ãƒ¼ã‚¿è¡¨ç¤ºã‚¿ãƒ– -->
      <div v-if="activeImageTab === 'data'" class="zero-code-image-section">
        <div class="zero-code-data-display-section">
          <!-- ç”»åƒã‚¿ã‚¤ãƒ—ã‚µãƒ–ã‚¿ãƒ– -->
          <div class="zero-code-parts-sub-tabs">
            <button
              :class="['zero-code-parts-sub-tab-button', { active: activeDataSubTab === 'common' }]"
              @click="activeDataSubTab = 'common'"
            >
              å…±é€šç”»åƒãƒ‡ãƒ¼ã‚¿
            </button>
            <button
              :class="['zero-code-parts-sub-tab-button', { active: activeDataSubTab === 'individual' }]"
              @click="activeDataSubTab = 'individual'"
            >
              å€‹åˆ¥ç”»åƒãƒ‡ãƒ¼ã‚¿
            </button>
          </div>

          <!-- ã‚µãƒ–ã‚¿ãƒ–ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ -->
          <div class="zero-code-parts-sub-content">
            <!-- å…±é€šç”»åƒãƒ‡ãƒ¼ã‚¿ -->
            <div v-if="activeDataSubTab === 'common'" class="zero-code-parts-sub-section">
              <div v-if="commonImages && commonImages.length > 0">
                <pre class="zero-code-data-display-pre">{{ formatToReadableJson(commonImages) }}</pre>
              </div>
              <div v-else>
                <p>å…±é€šç”»åƒãƒ‡ãƒ¼ã‚¿ãŒã‚ã‚Šã¾ã›ã‚“</p>
              </div>
            </div>

            <!-- å€‹åˆ¥ç”»åƒãƒ‡ãƒ¼ã‚¿ -->
            <div v-if="activeDataSubTab === 'individual'" class="zero-code-parts-sub-section">
              <div v-if="individualImages && individualImages.length > 0">
                <pre class="zero-code-data-display-pre">{{ formatToReadableJson(individualImages) }}</pre>
              </div>
              <div v-else>
                <p>å€‹åˆ¥ç”»åƒãƒ‡ãƒ¼ã‚¿ãŒã‚ã‚Šã¾ã›ã‚“</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- ç”»åƒæƒ…å ± -->
    <div class="zero-code-image-stats">
      <strong>ç”»åƒæƒ…å ±:</strong>
      å…±é€šç”»åƒ: {{ commonImages.length }}ä»¶, å€‹åˆ¥ç”»åƒ: {{ individualImages.length }}ä»¶, åˆè¨ˆ:
      {{ totalImagesCount }}ä»¶
    </div>

    <!-- ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰ãƒ¢ãƒ¼ãƒ€ãƒ« -->
    <ImageUploadModal
      v-if="showUploadModal"
      :category="currentUploadType"
      @close="closeUploadModal"
      @upload="handleImageUpload"
    />

    <!-- ç”»åƒè©³ç´°ãƒ¢ãƒ¼ãƒ€ãƒ« -->
    <ImageDetailModal
      v-if="showDetailModal && selectedImage"
      :image="selectedImage"
      @close="closeDetailModal"
      @delete="handleImageDeleteFromDetail"
      @replace="handleImageReplace"
    />
  </div>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'
  import ImageUploadModal from '@/components/images/ImageUploadModal.vue'
  import ImageDetailModal from '@/components/images/ImageDetailModal.vue'
  import type { ImageData } from '@types'
  import { useImageDisplay } from '@/composables/useImageDisplay'

  type ImageTab = 'common' | 'individual' | 'data'
  type UploadType = 'common' | 'individual'

  // Propså®šç¾©
  interface Props {
    commonImages: ImageData[]
    individualImages: ImageData[]
  }

  // Emitså®šç¾©
  interface Emits {
    (e: 'updateCommonImages', images: ImageData[]): void
    (e: 'updateIndividualImages', images: ImageData[]): void
    (e: 'sendCommonImagesData'): void
    (e: 'sendIndividualImagesData'): void
  }

  const props = defineProps<Props>()
  const emit = defineEmits<Emits>()
  
  // ç”»åƒè¡¨ç¤ºç”¨ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
  const { getImageSrc, getImageAlt, isImageUpdated } = useImageDisplay()

  // ãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãƒ‡ãƒ¼ã‚¿
  const activeImageTab = ref<ImageTab>('common')
  const activeDataSubTab = ref<'common' | 'individual'>('common')
  const showUploadModal = ref(false)
  const showDetailModal = ref(false)
  const currentUploadType = ref<UploadType>('common')
  const selectedImage = ref<ImageData | null>(null)

  // ã‚³ãƒ³ãƒ”ãƒ¥ãƒ¼ãƒ†ãƒƒãƒ‰ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£
  const totalImagesCount = computed(() => props.commonImages.length + props.individualImages.length)

  // ã‚¿ãƒ–ã®ä¸€è¦§
  const imageTabs: ImageTab[] = ['common', 'individual', 'data']

  // ã‚¿ãƒ–ã®ãƒ©ãƒ™ãƒ«ã‚’å–å¾—
  const getImageTabLabel = (tab: ImageTab): string => {
    const labels: Record<ImageTab, string> = {
      common: 'å…±é€šç”»åƒ',
      individual: 'å€‹åˆ¥ç”»åƒ',
      data: 'ãƒ‡ãƒ¼ã‚¿è¡¨ç¤º',
    }
    return labels[tab]
  }

  // ãƒ•ã‚¡ã‚¤ãƒ«ã‚µã‚¤ã‚ºã®ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
  const formatFileSize = (bytes: number | undefined): string => {
    if (!bytes || isNaN(bytes)) return 'ã‚µã‚¤ã‚ºä¸æ˜Ž'
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  // ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‹ã
  const openUploadModal = (type: UploadType) => {
    currentUploadType.value = type
    showUploadModal.value = true
  }

  // ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‰ã˜ã‚‹
  const closeUploadModal = () => {
    showUploadModal.value = false
  }

  // ç”»åƒè©³ç´°ã‚’è¡¨ç¤º
  const showImageDetail = (image: ImageData) => {
    selectedImage.value = image
    showDetailModal.value = true
  }

  // ç”»åƒè©³ç´°ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‰ã˜ã‚‹
  const closeDetailModal = () => {
    showDetailModal.value = false
    selectedImage.value = null
  }

  // ç”»åƒã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰å‡¦ç†
  const handleImageUpload = (imageData: ImageData) => {
    if (currentUploadType.value === 'common') {
      const updatedImages = [...props.commonImages, imageData]
      emit('updateCommonImages', updatedImages)
    } else {
      const updatedImages = [...props.individualImages, imageData]
      emit('updateIndividualImages', updatedImages)
    }
    closeUploadModal()
  }


  // èª­ã¿ã‚„ã™ã„JSONå½¢å¼ã«å¤‰æ›
  const formatToReadableJson = (data: unknown): string => {
    const formatValue = (obj: unknown, depth: number = 0): string => {
      const indent = '  '.repeat(depth)
      const nextIndent = '  '.repeat(depth + 1)

      if (Array.isArray(obj)) {
        if (obj.length === 0) return '[]'
        const items = obj.map(item => nextIndent + formatValue(item, depth + 1)).join(',\n')
        return `[\n${items}\n${indent}]`
      }

      if (typeof obj === 'object' && obj !== null) {
        const entries = Object.entries(obj as Record<string, unknown>)
        if (entries.length === 0) return '{}'
        
        const items = entries.map(([key, value]) => {
          let formattedValue: string
          
          if (typeof value === 'string') {
            // æ–‡å­—åˆ—ã®å ´åˆã¯ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—æ–‡å­—ã‚’å®Ÿéš›ã®æ–‡å­—ã«å¤‰æ›ã—ã¦ã‹ã‚‰è¡¨ç¤º
            const cleanString = value
              .replace(/\\n/g, '\n')
              .replace(/\\t/g, '\t')
              .replace(/\\"/g, '"')
              .replace(/\\'/g, "'")
              .replace(/\\\\/g, '\\')
            
            // æ”¹è¡ŒãŒå«ã¾ã‚Œã‚‹å ´åˆã¯ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆãƒªãƒ†ãƒ©ãƒ«é¢¨ã«è¡¨ç¤º
            if (cleanString.includes('\n')) {
              const lines = cleanString.split('\n')
              const formattedLines = lines.map((line, index) => {
                if (index === 0) return `\`${line}`
                if (index === lines.length - 1) return `${nextIndent}${line}\``
                return `${nextIndent}${line}`
              }).join('\n')
              formattedValue = formattedLines
            } else {
              formattedValue = `"${cleanString}"`
            }
          } else {
            formattedValue = formatValue(value, depth + 1)
          }
          
          return `${nextIndent}"${key}": ${formattedValue}`
        }).join(',\n')
        
        return `{\n${items}\n${indent}}`
      }

      if (typeof obj === 'string') {
        return `"${obj.replace(/"/g, '\\"')}"`
      }

      return JSON.stringify(obj)
    }

    return formatValue(data)
  }

  // ç”»åƒå‰Šé™¤å‡¦ç†ï¼ˆè©³ç´°ãƒ¢ãƒ¼ãƒ€ãƒ«ã‹ã‚‰ï¼‰
  const handleImageDeleteFromDetail = (imageId: string) => {
    if (!selectedImage.value) return
    
    const category = selectedImage.value.category as UploadType
    if (category === 'common') {
      const updatedImages = props.commonImages.filter(img => img.id !== imageId)
      emit('updateCommonImages', updatedImages)
    } else {
      const updatedImages = props.individualImages.filter(img => img.id !== imageId)
      emit('updateIndividualImages', updatedImages)
    }
    closeDetailModal()
  }

  // ç”»åƒå·®ã—æ›¿ãˆå‡¦ç†ï¼ˆè©³ç´°ãƒ¢ãƒ¼ãƒ€ãƒ«ã‹ã‚‰ï¼‰
  const handleImageReplace = (imageId: string, newImageData: ImageData) => {
    const category = newImageData.category as UploadType
    if (category === 'common') {
      const updatedImages = props.commonImages.map(img => 
        img.id === imageId ? newImageData : img
      )
      emit('updateCommonImages', updatedImages)
    } else {
      const updatedImages = props.individualImages.map(img => 
        img.id === imageId ? newImageData : img
      )
      emit('updateIndividualImages', updatedImages)
    }
    closeDetailModal()
  }

  // ç”»åƒå‰Šé™¤
  const deleteImage = (imageId: string, type: UploadType) => {
    if (confirm('ã“ã®ç”»åƒã‚’å‰Šé™¤ã—ã¾ã™ã‹ï¼Ÿ')) {
      if (type === 'common') {
        const updatedImages = props.commonImages.filter(img => img.id !== imageId)
        emit('updateCommonImages', updatedImages)
      } else {
        const updatedImages = props.individualImages.filter(img => img.id !== imageId)
        emit('updateIndividualImages', updatedImages)
      }
    }
  }

  // ã‚¤ãƒ™ãƒ³ãƒˆãƒãƒ³ãƒ‰ãƒ©ãƒ¼
  const sendCommonImagesData = () => emit('sendCommonImagesData')
  const sendIndividualImagesData = () => emit('sendIndividualImagesData')
</script>

