/**
 * Vue.js ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ä¿è­·ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£
 * ç¹°ã‚Šè¿”ã—è¨˜æ³•å‡¦ç†æ™‚ã«v-è¨˜æ³•ã‚’ç¢ºå®Ÿã«ä¿æŒ
 */

import { logger } from '@shared/utils/logger'
import { errorHandler, safeExecute, ErrorType } from '@shared/utils/errorHandler'

/**
 * JavaScriptæ–‡å­—åˆ—å†…ã§å®‰å…¨ã«ä½¿ç”¨ã™ã‚‹ãŸã‚ã®ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—å‡¦ç†
 * @param str ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã™ã‚‹æ–‡å­—åˆ—
 * @returns ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã•ã‚ŒãŸæ–‡å­—åˆ—
 */
function escapeJSString(str: string): string {
  if (!str) return ''
  return str
    .replace(/\\/g, '\\\\')    // ãƒãƒƒã‚¯ã‚¹ãƒ©ãƒƒã‚·ãƒ¥
    .replace(/'/g, "\\'")      // ã‚·ãƒ³ã‚°ãƒ«ã‚¯ã‚©ãƒ¼ãƒˆ
    .replace(/"/g, '\\"')      // ãƒ€ãƒ–ãƒ«ã‚¯ã‚©ãƒ¼ãƒˆ
    .replace(/\n/g, '\\n')     // æ”¹è¡Œ
    .replace(/\r/g, '\\r')     // ã‚­ãƒ£ãƒªãƒƒã‚¸ãƒªã‚¿ãƒ¼ãƒ³
    .replace(/\t/g, '\\t')     // ã‚¿ãƒ–
}

/**
 * v-è¨˜æ³•ã®æ¤œå‡ºãƒ‘ã‚¿ãƒ¼ãƒ³
 */
const VUE_DIRECTIVE_PATTERNS = [
  // v-if, v-show, v-for, v-modelç­‰
  /v-[a-zA-Z-]+(?::[a-zA-Z-\.]+)?="[^"]*"/g,
  // @click, @inputç­‰ã®ã‚¤ãƒ™ãƒ³ãƒˆãƒãƒ³ãƒ‰ãƒ©
  /@[a-zA-Z-]+(?:\.[a-zA-Z-]+)*="[^"]*"/g,
  // :class, :styleç­‰ã®ãƒã‚¤ãƒ³ãƒ‡ã‚£ãƒ³ã‚°
  /:[a-zA-Z-]+(?:\.[a-zA-Z-]+)*="[^"]*"/g,
  // v-slotè¨˜æ³•
  /v-slot:[a-zA-Z-]+="[^"]*"/g,
  // #slotNameè¨˜æ³•
  /#[a-zA-Z-]+="[^"]*"/g,
]

/**
 * Vueè£œé–“è¨˜æ³•ã®æ¤œå‡ºãƒ‘ã‚¿ãƒ¼ãƒ³
 */
const VUE_INTERPOLATION_PATTERNS = [
  // {{ variable }}
  /\{\{[^}]+\}\}/g,
  // v-text, v-html ã®å†…å®¹
  /v-text="[^"]*"/g,
  /v-html="[^"]*"/g,
]

/**
 * ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ä¿è­·æƒ…å ±
 */
interface DirectivePreservation {
  html: string
  directives: Map<string, string>
  interpolations: Map<string, string>
}

/**
 * v-è¨˜æ³•ã‚’ä¸€æ™‚çš„ãªãƒ—ãƒ¬ãƒ¼ã‚¹ãƒ›ãƒ«ãƒ€ãƒ¼ã«ç½®æ›ã—ã¦ä¿è­·
 */
export function preserveVueDirectives(html: string): DirectivePreservation {
  return (
    safeExecute(
      () => {
        const directives = new Map<string, string>()
        const interpolations = new Map<string, string>()
        let protectedHtml = html

        logger.debug('Vue.jsãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ä¿è­·é–‹å§‹', {
          htmlLength: html.length,
        })

        // 1. ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ã®ä¿è­·
        VUE_DIRECTIVE_PATTERNS.forEach((pattern, patternIndex) => {
          protectedHtml = protectedHtml.replace(pattern, match => {
            const placeholder = `__VUE_DIR_${patternIndex}_${directives.size}__`
            directives.set(placeholder, match)
            logger.debug(`ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ä¿è­·: ${match} â†’ ${placeholder}`)
            return placeholder
          })
        })

        // 2. è£œé–“è¨˜æ³•ã®ä¿è­·
        VUE_INTERPOLATION_PATTERNS.forEach((pattern, patternIndex) => {
          protectedHtml = protectedHtml.replace(pattern, match => {
            const placeholder = `__VUE_INT_${patternIndex}_${interpolations.size}__`
            interpolations.set(placeholder, match)
            logger.debug(`è£œé–“è¨˜æ³•ä¿è­·: ${match} â†’ ${placeholder}`)
            return placeholder
          })
        })

        logger.success('Vue.jsãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ä¿è­·å®Œäº†', {
          directiveCount: directives.size,
          interpolationCount: interpolations.size,
          resultLength: protectedHtml.length,
        })

        return {
          html: protectedHtml,
          directives,
          interpolations,
        }
      },
      errorHandler,
      'Vue.jsãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ä¿è­·ä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
      ErrorType.TEMPLATE
    ) || {
      html,
      directives: new Map(),
      interpolations: new Map(),
    }
  )
}

/**
 * ä¿è­·ã•ã‚ŒãŸv-è¨˜æ³•ã‚’å¾©å…ƒ
 */
export function restoreVueDirectives(html: string, preservation: DirectivePreservation): string {
  return (
    safeExecute(
      () => {
        let restoredHtml = html

        logger.debug('Vue.jsãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–å¾©å…ƒé–‹å§‹', {
          directiveCount: preservation.directives.size,
          interpolationCount: preservation.interpolations.size,
        })

        // 1. ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ã®å¾©å…ƒ
        preservation.directives.forEach((directive, placeholder) => {
          restoredHtml = restoredHtml.replace(new RegExp(placeholder, 'g'), directive)
          logger.debug(`ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–å¾©å…ƒ: ${placeholder} â†’ ${directive}`)
        })

        // 2. è£œé–“è¨˜æ³•ã®å¾©å…ƒ
        preservation.interpolations.forEach((interpolation, placeholder) => {
          restoredHtml = restoredHtml.replace(new RegExp(placeholder, 'g'), interpolation)
          logger.debug(`è£œé–“è¨˜æ³•å¾©å…ƒ: ${placeholder} â†’ ${interpolation}`)
        })

        logger.success('Vue.jsãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–å¾©å…ƒå®Œäº†', {
          resultLength: restoredHtml.length,
        })

        return restoredHtml
      },
      errorHandler,
      'Vue.jsãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–å¾©å…ƒä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
      ErrorType.TEMPLATE
    ) || html
  )
}

/**
 * å®‰å…¨ãªDOMParserï¼ˆv-è¨˜æ³•ä¿è­·ä»˜ãï¼‰
 */
export function safeDOMParse(html: string): { doc: Document; preservation: DirectivePreservation } {
  return (
    safeExecute(
      () => {
        // 1. v-è¨˜æ³•ã‚’ä¿è­·
        const preservation = preserveVueDirectives(html)

        // 2. å®‰å…¨ã«ãƒ‘ãƒ¼ã‚¹
        const parser = new DOMParser()
        const doc = parser.parseFromString(preservation.html, 'text/html')

        logger.debug('å®‰å…¨DOMãƒ‘ãƒ¼ã‚¹å®Œäº†', {
          originalLength: html.length,
          protectedLength: preservation.html.length,
          parsedElementCount: doc.body.children.length,
        })

        return { doc, preservation }
      },
      errorHandler,
      'å®‰å…¨DOMãƒ‘ãƒ¼ã‚¹ä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
      ErrorType.TEMPLATE
    ) || {
      doc: new DOMParser().parseFromString('<div></div>', 'text/html'),
      preservation: { html, directives: new Map(), interpolations: new Map() },
    }
  )
}

/**
 * v-è¨˜æ³•å¯¾å¿œã®z-forå‡¦ç†
 */
export function processZForWithVueSupport(
  html: string,
  component: any,
  arrayName: string,
  arrayData: any[]
): string {
  return (
    safeExecute(
      () => {
        logger.debug('Vueå¯¾å¿œz-forå‡¦ç†é–‹å§‹', {
          arrayName,
          arrayLength: arrayData.length,
        })

        // v-è¨˜æ³•ã‚’ä¿è­·ã—ã¦ã‹ã‚‰z-forå‡¦ç†
        const preservation = preserveVueDirectives(html)

        // z-forè¦ç´ ã®ãƒ‘ã‚¿ãƒ¼ãƒ³ãƒžãƒƒãƒãƒ³ã‚°ï¼ˆä¿è­·ã•ã‚ŒãŸHTMLç”¨ãƒ»ä»»æ„è¦ç´ å¯¾å¿œï¼‰
        const zForPattern = new RegExp(
          `<(\\w+)([^>]*z-for="${arrayName}"[^>]*)>([\\s\\S]*?)</\\1>`,
          'g'
        )
        const match = preservation.html.match(zForPattern)

        if (!match || arrayData.length === 0) {
          // ç©ºé…åˆ—ã®å ´åˆã¯è¿½åŠ ãƒœã‚¿ãƒ³
          return `
          <div class="add-array-item-button-container">
            <button class="add-array-item-button" onclick="window.addArrayItem('${escapeJSString(component.id)}', '${escapeJSString(arrayName)}')">
              <span class="plus-icon">ï¼‹</span>
              <span class="button-text">${arrayName}ã‚’è¿½åŠ </span>
            </button>
          </div>
        `
        }

        // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°
        const itemTemplate = match[0].replace(zForPattern, '$2')
        const renderedItems = arrayData
          .map((item: any) => {
            let itemHtml = itemTemplate

            // ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ç½®æ›
            Object.keys(item).forEach(key => {
              if (key !== 'id') {
                const value = item[key]
                const patterns = [
                  new RegExp(`\\{\\$${key}:([^}]+)\\}`, 'g'),
                  new RegExp(`\\(\\$${key}:([^)]+)\\)`, 'g'),
                ]
                patterns.forEach(pattern => {
                  itemHtml = itemHtml.replace(pattern, String(value))
                })
              }
            })

            return itemHtml
          })
          .join('')

        // v-è¨˜æ³•ã‚’å¾©å…ƒ
        const finalHtml = restoreVueDirectives(renderedItems, preservation)

        logger.success('Vueå¯¾å¿œz-forå‡¦ç†å®Œäº†', {
          arrayName,
          resultLength: finalHtml.length,
        })

        return finalHtml
      },
      errorHandler,
      `Vueå¯¾å¿œz-forå‡¦ç†ä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ (arrayName: ${arrayName})`,
      ErrorType.TEMPLATE
    ) || html
  )
}

/**
 * v-è¨˜æ³•ã®æ¤œè¨¼
 */
export function validateVueDirectives(html: string): {
  isValid: boolean
  issues: string[]
  directives: string[]
} {
  const issues: string[] = []
  const directives: string[] = []

  // ãƒ‡ã‚£ãƒ¬ã‚¯ãƒ†ã‚£ãƒ–ã®æŠ½å‡º
  VUE_DIRECTIVE_PATTERNS.forEach(pattern => {
    const matches = html.match(pattern)
    if (matches) {
      directives.push(...matches)
    }
  })

  // ä¸€èˆ¬çš„ãªå•é¡Œã®æ¤œå‡º
  if (html.includes('v-for') && html.includes('z-for')) {
    issues.push('v-forã¨z-forãŒæ··åœ¨ã—ã¦ã„ã¾ã™ã€‚ç«¶åˆã®å¯èƒ½æ€§ãŒã‚ã‚Šã¾ã™ã€‚')
  }

  if (html.includes('{{') && html.includes('{$')) {
    issues.push('Vueè£œé–“è¨˜æ³•{{}}ã¨ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•{$}ãŒæ··åœ¨ã—ã¦ã„ã¾ã™ã€‚')
  }

  return {
    isValid: issues.length === 0,
    issues,
    directives,
  }
}

/**
 * çµ±è¨ˆæƒ…å ±ã®å–å¾—
 */
export function getDirectiveStats(html: string): {
  directiveCount: number
  interpolationCount: number
  directiveTypes: Record<string, number>
} {
  let directiveCount = 0
  let interpolationCount = 0
  const directiveTypes: Record<string, number> = {}

  VUE_DIRECTIVE_PATTERNS.forEach(pattern => {
    const matches = html.match(pattern)
    if (matches) {
      directiveCount += matches.length
      matches.forEach(match => {
        const type = match.split('=')[0].split(':')[0]
        directiveTypes[type] = (directiveTypes[type] || 0) + 1
      })
    }
  })

  VUE_INTERPOLATION_PATTERNS.forEach(pattern => {
    const matches = html.match(pattern)
    if (matches) {
      interpolationCount += matches.length
    }
  })

  return {
    directiveCount,
    interpolationCount,
    directiveTypes,
  }
}
