  import PartsManagementPanel from './components/panels/PartsManagementPanel.vue'
  import PreviewPanel from './components/panels/PreviewPanel.vue'
  import ImageManagementPanel from './components/panels/ImageManagementPanel.vue'

  // ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
  import { useEditorTabs } from './composables/useEditorTabs'
  import { useZeroCode } from './composables/useZeroCode'
  import { useComponentUtils } from './composables/useComponentUtils'
  import { useCollapsibleItems } from './composables/useCollapsibleItems'
  import { useDataLoader } from './composables/useDataLoader'
  import { useImageDisplay } from './composables/useImageDisplay'

  // å…±é€šã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆ
  import TabNavigation from '@shared/components/TabNavigation.vue'
  import ErrorBoundary from '@shared/components/ErrorBoundary.vue'
  import RichTextToolbar from './components/editor/RichTextToolbar.vue'
  import ImageSelectorModal from './components/editor/ImageSelectorModal.vue'

  // ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£
  import { logger } from '@shared/utils/logger'
  import { cleanupHtmlContent } from '@shared/utils/htmlHelpers'
  import { editorEventBus } from '@shared/utils/eventBus'

  // åž‹å®šç¾©
  import type { Component, PartSection, ImageData } from '@types'

  // Propså®šç¾©
  export interface Props {
    parts?: PartSection[]
    commonParts?: PartSection[]
    individualParts?: PartSection[]
    page?: Component[]
    commonImages?: ImageData[]
    individualImages?: ImageData[]
  }

  const props = withDefaults(defineProps<Props>(), {
    parts: () => [],
    commonParts: () => [],
    individualParts: () => [],
    page: () => [],
    commonImages: () => [],
    individualImages: () => [],
  })

  // Emitså®šç¾©
  const emit = defineEmits<{
    'update:parts': [parts: PartSection[]]
    'update:commonParts': [parts: PartSection[]]
    'update:individualParts': [parts: PartSection[]]
    'update:page': [page: Component[]]
    'update:commonImages': [images: ImageData[]]
    'update:individualImages': [images: ImageData[]]
  }>()

  // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå›ºæœ‰IDï¼ˆã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹åˆ†é›¢ç”¨ï¼‰
  const componentInstanceId = ref(`zero-code-${Math.random().toString(36).substr(2, 9)}`)
  
  // ãƒ‡ãƒ¼ã‚¿ãƒ­ãƒ¼ãƒ€ãƒ¼ã®åˆæœŸåŒ–ï¼ˆæœ€é©åŒ–ã•ã‚ŒãŸãƒ‡ãƒ¼ã‚¿ç®¡ç†ï¼‰
  const dataLoader = useDataLoader()
  
  // ç”»åƒè¡¨ç¤ºç”¨ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
  const { getImageSrc } = useImageDisplay()
  
  // å…¨ç”»åƒãƒ‡ãƒ¼ã‚¿ã®çµ±åˆ
  const allImages = computed(() => [...commonImages.value, ...individualImages.value])
  
  // HTMLã®ç”»åƒURLã‚’è§£æ±ºã™ã‚‹é–¢æ•°ï¼ˆè¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã¨ã—ã¦å®Ÿè£…ï¼‰
  const resolveImageUrlsInHTML = (html: string): string => {
    if (!html) return html
    
    // allImages.valueã®å¤‰æ›´ã‚’æ¤œçŸ¥ã™ã‚‹ãŸã‚ã€ã“ã“ã§ã‚¢ã‚¯ã‚»ã‚¹
    const currentImages = allImages.value
    if (currentImages.length === 0) return html

    let processedHtml = html
    
    // imgè¦ç´ ã®srcå±žæ€§ã‚’ç½®æ›
    processedHtml = processedHtml.replace(
      /<img([^>]*?)src=["']([^"']+)["']([^>]*?)>/gi,
      (match, beforeSrc, originalSrc, afterSrc) => {
        // ç”»åƒIDã¾ãŸã¯URLã‚’ä½¿ã£ã¦ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’æ¤œç´¢
        const imageData = currentImages.find(img => {
          // å®Œå…¨ä¸€è‡´ãƒã‚§ãƒƒã‚¯
          if (img.id === originalSrc || img.url === originalSrc || img.name === originalSrc) {
            return true
          }
          
          // URLãƒ‘ã‚¹ã®ä¸€è‡´ãƒã‚§ãƒƒã‚¯ï¼ˆä¾‹: /img/hero_image.jpg ã¨ hero_image.jpgï¼‰
          if (originalSrc.endsWith('/' + img.name) || img.url.endsWith(originalSrc)) {
            return true
          }
          
          // ãƒ•ã‚¡ã‚¤ãƒ«åéƒ¨åˆ†ã®ãƒžãƒƒãƒãƒ³ã‚°
          const originalFileName = originalSrc.split('/').pop()
          if (originalFileName === img.name) {
            return true
          }
          
          return false
        })
        
        if (imageData) {
          const resolvedSrc = getImageSrc(imageData)
          return `<img${beforeSrc}src="${resolvedSrc}"${afterSrc}>`
        }
        
        return match
      }
    )

    return processedHtml
  }
  
  // ç”»åƒã‚’å…ƒã®çŠ¶æ…‹ã«ãƒªã‚»ãƒƒãƒˆã™ã‚‹é–¢æ•°
  const resetImageToOriginal = (imageId: string) => {
    // å…±é€šç”»åƒã‚’ãƒã‚§ãƒƒã‚¯
    const commonImageIndex = commonImages.value.findIndex(img => img.id === imageId)
    if (commonImageIndex !== -1) {
      const updatedImages = [...commonImages.value]
      updatedImages[commonImageIndex] = {
        ...updatedImages[commonImageIndex],
        fileData: {
          ...updatedImages[commonImageIndex].fileData,
          base64: '' // Base64ãƒ‡ãƒ¼ã‚¿ã‚’ã‚¯ãƒªã‚¢
        }
      }
      updateCommonImages(updatedImages)
      return
    }
    
    // å€‹åˆ¥ç”»åƒã‚’ãƒã‚§ãƒƒã‚¯
    const individualImageIndex = individualImages.value.findIndex(img => img.id === imageId)
    if (individualImageIndex !== -1) {
      const updatedImages = [...individualImages.value]
      updatedImages[individualImageIndex] = {
        ...updatedImages[individualImageIndex],
        fileData: {
          ...updatedImages[individualImageIndex].fileData,
          base64: '' // Base64ãƒ‡ãƒ¼ã‚¿ã‚’ã‚¯ãƒªã‚¢
        }
      }
      updateIndividualImages(updatedImages)
    }
  }
  
  // ãƒ‡ãƒ¼ã‚¿ãƒ­ãƒ¼ãƒ€ãƒ¼åˆæœŸåŒ–ï¼ˆéžåŒæœŸï¼‰
  onMounted(async () => {
    try {
      // æœ¬ç•ªãƒ“ãƒ«ãƒ‰ã§ã¯å†…éƒ¨ã‚µãƒ³ãƒ—ãƒ«ãƒ‡ãƒ¼ã‚¿ã‚’èª­ã¿è¾¼ã¾ãªã„
      const shouldLoadSampleData = process.env.NODE_ENV === 'development' || process.env.LOAD_SAMPLE_DATA === 'true'
      
      if (shouldLoadSampleData) {
        logger.info('ðŸš€ é–‹ç™ºãƒ¢ãƒ¼ãƒ‰ï¼šã‚µãƒ³ãƒ—ãƒ«ãƒ‡ãƒ¼ã‚¿ã‚’èª­ã¿è¾¼ã¿ä¸­...')
        await dataLoader.initialize()
        
        // ãƒ‡ãƒ¼ã‚¿ãƒ­ãƒ¼ãƒ€ãƒ¼ã‹ã‚‰å–å¾—ã—ãŸãƒ‘ãƒ¼ãƒ„ã‚’localPartsã«è¨­å®š
        if (dataLoader.combinedParts.value && dataLoader.combinedParts.value.length > 0) {
          localParts.value = dataLoader.combinedParts.value
          logger.success(`âœ… ãƒ‘ãƒ¼ãƒ„ãƒ‡ãƒ¼ã‚¿è¨­å®šå®Œäº† (${dataLoader.combinedParts.value.length}ä»¶)`)
        } else {
          logger.warn('âš ï¸ ãƒ‘ãƒ¼ãƒ„ãƒ‡ãƒ¼ã‚¿ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“')
        }
        
        // ãƒšãƒ¼ã‚¸ãƒ‡ãƒ¼ã‚¿ã‚‚è¨­å®š
        if (dataLoader.defaultPageData.value && dataLoader.defaultPageData.value.length > 0) {
          editablePageData.value = dataLoader.defaultPageData.value
          logger.success(`âœ… ãƒšãƒ¼ã‚¸ãƒ‡ãƒ¼ã‚¿è¨­å®šå®Œäº† (${dataLoader.defaultPageData.value.length}ä»¶)`)
        } else {
          logger.warn('âš ï¸ ãƒšãƒ¼ã‚¸ãƒ‡ãƒ¼ã‚¿ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“')
        }
        
        // ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚‚è¨­å®š
        if (dataLoader.images.value) {
          commonImages.value = dataLoader.images.value.common as any[]
          individualImages.value = dataLoader.images.value.individual as any[]
          logger.success(`âœ… ç”»åƒãƒ‡ãƒ¼ã‚¿è¨­å®šå®Œäº† (å…±é€š:${dataLoader.images.value.common.length}ä»¶, å€‹åˆ¥:${dataLoader.images.value.individual.length}ä»¶)`)
        } else {
          logger.warn('âš ï¸ ç”»åƒãƒ‡ãƒ¼ã‚¿ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“')
        }
        
        logger.success('âœ… ãƒ‡ãƒ¼ã‚¿ãƒ­ãƒ¼ãƒ€ãƒ¼åˆæœŸåŒ–å®Œäº†')
      } else {
        logger.info('ðŸ­ æœ¬ç•ªãƒ¢ãƒ¼ãƒ‰ï¼šå¤–éƒ¨ãƒ‡ãƒ¼ã‚¿å¾…æ©Ÿä¸­ï¼ˆå†…éƒ¨ã‚µãƒ³ãƒ—ãƒ«ãƒ‡ãƒ¼ã‚¿ã¯èª­ã¿è¾¼ã¿ã¾ã›ã‚“ï¼‰')
        // æœ¬ç•ªãƒ¢ãƒ¼ãƒ‰ã§ã¯ç©ºã®çŠ¶æ…‹ã§é–‹å§‹
        localParts.value = []
        editablePageData.value = []
        commonImages.value = []
        individualImages.value = []
        logger.success('âœ… æœ¬ç•ªãƒ¢ãƒ¼ãƒ‰åˆæœŸåŒ–å®Œäº†ï¼ˆç©ºã®çŠ¶æ…‹ï¼‰')
      }
      
      // åˆæœŸåŒ–å®Œäº†ã‚¤ãƒ™ãƒ³ãƒˆã‚’ç™ºç«ï¼ˆindex.htmlã®ã‚¿ã‚¤ãƒ ã‚¢ã‚¦ãƒˆé˜²æ­¢ï¼‰
      nextTick(() => {
        const event = new CustomEvent('zero-code-initialized', {
          detail: {
            partsCount: localParts.value?.length || 0,
            pageCount: editablePageData.value?.length || 0
          }
        })
        document.dispatchEvent(event)
        logger.debug('ðŸŽ‰ zero-code-initialized ã‚¤ãƒ™ãƒ³ãƒˆã‚’ç™ºç«')
      })
      
    } catch (error) {
      logger.error('âŒ ãƒ‡ãƒ¼ã‚¿ãƒ­ãƒ¼ãƒ€ãƒ¼åˆæœŸåŒ–å¤±æ•—:', error)
    }
  })

  // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼ã‚³ã‚¢ã®åˆæœŸåŒ–
  const editor = useZeroCode(props, emit)

  // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼çŠ¶æ…‹ã®å–å¾—
  const selectedComponent = editor.state.selectedComponent
  const selectedComponentIndex = editor.state.selectedComponentIndex
  const editablePageData = editor.state.editablePageData
  const localParts = editor.state.localParts
  const dataTab = editor.state.dataTab

  // ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼ï¼ˆã‚·ãƒ³ãƒ—ãƒ«å®Ÿè£…ï¼‰
  const showRichTextToolbar = ref(false)
  const richTextPosition = ref({ top: 0, left: 0 })
  const currentEditingElement = ref<HTMLElement | null>(null)
  const originalContent = ref('')
  const currentComponentId = ref('')
  const currentParamName = ref('')

  // ãƒ†ã‚­ã‚¹ãƒˆç·¨é›†ä¸­ã‹ã©ã†ã‹ã®çŠ¶æ…‹ï¼ˆãƒ‰ãƒ©ãƒƒã‚°ç„¡åŠ¹åŒ–ç”¨ï¼‰
  const isTextEditing = computed(() => currentEditingElement.value !== null)

  // ç”»åƒé¸æŠžãƒ¢ãƒ¼ãƒ€ãƒ«é–¢é€£
  const showGlobalImageSelector = ref(false)
  const currentGlobalImageValue = ref('')
  const currentGlobalImageComponent = ref<Component | null>(null)
  const currentGlobalImageField = ref('')

  const richTextEditor = reactive({
    showToolbar: computed(() => showRichTextToolbar.value),
    position: computed(() => richTextPosition.value),
    actions: [
      { name: 'bold', icon: 'B', title: 'å¤ªå­—', command: 'bold' },
      { name: 'italic', icon: 'I', title: 'æ–œä½“', command: 'italic' },
      { name: 'underline', icon: 'U', title: 'ä¸‹ç·š', command: 'underline' },
      { name: 'separator' },
      { name: 'link', icon: 'ðŸ”—', title: 'ãƒªãƒ³ã‚¯', command: 'link' },
    ],
    editingState: {},
    executeCommand: (command: string) => {
      logger.debug('ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆã‚³ãƒžãƒ³ãƒ‰å®Ÿè¡Œ:', command)
      executeRichTextCommand(command)
    },
    stopEdit: (save: boolean) => {
      logger.debug('ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆç·¨é›†çµ‚äº†:', save)
      stopRichTextEdit(save)
    },
  })

  // ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆã‚³ãƒžãƒ³ãƒ‰å®Ÿè¡Œï¼ˆé¸æŠžç¯„å›²ä¿è­·ç‰ˆï¼‰
  const executeRichTextCommand = (command: string) => {
    if (!currentEditingElement.value) return

    logger.debug('ðŸ”§ æ›¸å¼è¨­å®šã‚³ãƒžãƒ³ãƒ‰å®Ÿè¡Œ:', command)

    try {
      if (command === 'link') {
        // ãƒªãƒ³ã‚¯ä½œæˆã®ç‰¹åˆ¥å‡¦ç†
        const url = prompt('ãƒªãƒ³ã‚¯ã®URLã‚’å…¥åŠ›ã—ã¦ãã ã•ã„:', 'https://')
        if (url && url.trim()) {
          const success = document.execCommand('createLink', false, url)
          logger.debug('ðŸ”§ ãƒªãƒ³ã‚¯ä½œæˆ:', success ? 'æˆåŠŸ' : 'å¤±æ•—')
        }
      } else {
        // é€šå¸¸ã®æ›¸å¼è¨­å®šã‚³ãƒžãƒ³ãƒ‰
        const success = document.execCommand(command, false, undefined)
        logger.debug('ðŸ”§ æ›¸å¼è¨­å®š:', command, success ? 'æˆåŠŸ' : 'å¤±æ•—')
      }

      // ãƒ‡ãƒ¼ã‚¿æ›´æ–°ã¯ä¸€æ™‚çš„ã«ç„¡åŠ¹åŒ–ï¼ˆé¸æŠžç¯„å›²ä¿è­·ã®ãŸã‚ï¼‰
      logger.debug('ðŸ”§ ãƒ‡ãƒ¼ã‚¿æ›´æ–°ã‚’ã‚¹ã‚­ãƒƒãƒ—ï¼ˆé¸æŠžç¯„å›²ä¿è­·ï¼‰')
      // updateComponentDataSync()
    } catch (error) {
      logger.error('ðŸš¨ execCommandã‚¨ãƒ©ãƒ¼:', error)
    }
  }


  // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãƒ‡ãƒ¼ã‚¿ã®åŒæœŸæ›´æ–°ï¼ˆHTMLã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ä»˜ãï¼‰
  const updateComponentDataSync = () => {
    if (!currentEditingElement.value || !currentComponentId.value || !currentParamName.value) return

    const rawContent = currentEditingElement.value.innerHTML
    const cleanedContent = cleanupHtmlContent(rawContent)

    // z-forä»¥å¤–ã®é€šå¸¸ç·¨é›†ã®ãƒ­ã‚°ã¯çœç•¥

    // editablePageDataã‹ã‚‰è©²å½“ã™ã‚‹ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’æ¤œç´¢ã—ã¦æ›´æ–°ï¼ˆå†å¸°çš„æ¤œç´¢ï¼‰
    const updateComponent = (components: Component[]): boolean => {
      // z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®ç‰¹æ®ŠIDå½¢å¼ã‚’ãƒã‚§ãƒƒã‚¯ï¼ˆãƒã‚¹ãƒˆå¯¾å¿œç‰ˆï¼‰
      
      // IDã‚’åˆ†è§£ã—ã¦z-foræ§‹é€ ã‚’ç‰¹å®šï¼ˆæ—¢çŸ¥ã®é…åˆ—åãƒ‘ã‚¿ãƒ¼ãƒ³ã‚’ä½¿ç”¨ï¼‰
      const parseZForId = (id: string) => {
        const parts = id.split('_')
        
        if (parts.length < 4) return null
        
        // æœ€å¾Œã®è¦ç´ ãŒparamName
        const paramName = parts[parts.length - 1]
        
        // æœ€å¾Œã‹ã‚‰2ç•ªç›®ãŒæ•°å­—ï¼ˆindexï¼‰ã‹ãƒã‚§ãƒƒã‚¯
        const lastIndex = parts[parts.length - 2]
        if (!/^\d+$/.test(lastIndex)) return null
        
        // æ—¢çŸ¥ã®é…åˆ—åãƒ‘ã‚¿ãƒ¼ãƒ³ã‚’ãƒã‚§ãƒƒã‚¯
        const knownArrayNames = ['outer_items', 'inner_items', 'items', 'categories', 'tags', 'list']
        
        // é…åˆ—åã‚’ç‰¹å®š
        for (const arrayName of knownArrayNames) {
          const targetPattern = `_${arrayName}_${lastIndex}_${paramName}`
          if (id.endsWith(targetPattern)) {
            const parentId = id.slice(0, -(targetPattern.length))
            return {
              parentId,
              arrayName,
              arrayIndex: parseInt(lastIndex),
              paramName
            }
          }
        }
        
        return null
      }

      const zForInfo = parseZForId(currentComponentId.value)
      
      // z-forã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†ã®æ¤œè¨¼ãƒ­ã‚°
      if (zForInfo) {
        console.log(`ðŸ”§ [Z-FORç·¨é›†] IDè§£æžæˆåŠŸ:`, {
          originalId: currentComponentId.value,
          parentId: zForInfo.parentId,
          arrayName: zForInfo.arrayName,
          arrayIndex: zForInfo.arrayIndex,
          paramName: zForInfo.paramName,
          newValue: cleanedContent
        })
      }

      if (zForInfo) {
        const { parentId, arrayName, arrayIndex, paramName } = zForInfo

        // å†å¸°çš„ã«ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã¨é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã‚’æ¤œç´¢
        const findTargetArrayItem = (components: Component[], targetParentId: string): any => {
          for (const comp of components) {
            // ç›´æŽ¥ãƒžãƒƒãƒ
            if (comp.id === targetParentId) {
              if (Array.isArray(comp[arrayName])) {
                return comp[arrayName][arrayIndex]
              }
            }
            
            // nested_partså†…ã‚’æ¤œç´¢
            if (comp.nested_parts && Array.isArray(comp.nested_parts)) {
              const found = findTargetArrayItem(comp.nested_parts, targetParentId)
              if (found) return found
            }
            
            // å…¨é…åˆ—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£å†…ã®é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã‚’æ¤œç´¢ï¼ˆz-foré…åˆ—å«ã‚€ï¼‰
            for (const [propKey, propValue] of Object.entries(comp)) {
              if (Array.isArray(propValue) && propKey !== 'nested_parts') {
                // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ãŒè¦ªIDã«ãªã£ã¦ã„ã‚‹å¯èƒ½æ€§ã‚’ãƒã‚§ãƒƒã‚¯
                for (let i = 0; i < propValue.length; i++) {
                  const arrayItem = propValue[i]
                  if (arrayItem && typeof arrayItem === 'object') {
                    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ IDã‚’æ§‹ç¯‰ã—ã¦ãƒã‚§ãƒƒã‚¯
                    const itemId = `${comp.id}_${propKey}_${i}`
                    if (itemId === targetParentId) {
                      if (Array.isArray(arrayItem[arrayName])) {
                        return arrayItem[arrayName][arrayIndex]
                      }
                    }
                    
                    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å†…ã®ãƒã‚¹ãƒˆã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚‚æ¤œç´¢
                    for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                      if (Array.isArray(slotValue)) {
                        const found = findTargetArrayItem(slotValue, targetParentId)
                        if (found) return found
                      }
                    }
                  }
                }
              }
            }
          }
          return null
        }

        const targetArrayItem = findTargetArrayItem(components, parentId)
        if (targetArrayItem && typeof targetArrayItem === 'object') {
          targetArrayItem[paramName] = cleanedContent
          console.log(`âœ… [Z-FORç·¨é›†] æ›´æ–°æˆåŠŸ:`, {
            path: `${parentId}.${arrayName}[${arrayIndex}].${paramName}`,
            oldValue: targetArrayItem[paramName],
            newValue: cleanedContent
          })
          return true
        }
        
        console.log(`âŒ [Z-FORç·¨é›†] é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ãŒè¦‹ã¤ã‹ã‚‰ãªã„:`, {
          targetId: currentComponentId.value,
          searchedParentId: parentId
        })
        return false
      }

      for (const component of components) {
        if (component.id === currentComponentId.value) {
          ;(component as any)[currentParamName.value] = cleanedContent
          return true
        }

        // nested_partså†…ã‚’å†å¸°çš„ã«æ¤œç´¢
        if (component.nested_parts && Array.isArray(component.nested_parts)) {
          logger.debug(`ðŸ” nested_partså†…ã‚’æ¤œç´¢: ${component.id}`)
          if (updateComponent(component.nested_parts)) {
            return true
          }
        }

        // å…¨ã¦ã®ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã‹ã‚‰é…åˆ—ã‚’æ¤œç´¢ï¼ˆz-foré…åˆ—å†…ã‚‚å«ã‚€ï¼‰
        for (const [key, value] of Object.entries(component)) {
          if (Array.isArray(value) && key !== 'nested_parts') {
            logger.debug(`ðŸ” é…åˆ—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£æ¤œç´¢: ${component.id}.${key}`)
            // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å†…ã®nested_parts/nested_slotså†…ã‚’æ¤œç´¢
            for (const arrayItem of value) {
              if (arrayItem && typeof arrayItem === 'object') {
                if ((arrayItem as any).id === currentComponentId.value) {
                  ;(arrayItem as any)[currentParamName.value] = cleanedContent
                  logger.debug('ðŸ”§ é…åˆ—å†…ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆæ›´æ–°å®Œäº†:', arrayItem)
                  return true
                }
                // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å†…ã®å…¨ã‚¹ãƒ­ãƒƒãƒˆã‚’æ¤œç´¢
                for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                  if (Array.isArray(slotValue)) {
                    logger.debug(`ðŸ” ã‚¹ãƒ­ãƒƒãƒˆæ¤œç´¢: ${component.id}.${key}[].${slotKey}`)
                    if (updateComponent(slotValue)) {
                      return true
                    }
                  }
                }
              }
            }
          }
        }
      }
      logger.debug(`âŒ ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“ã§ã—ãŸ: ${currentComponentId.value}`)
      return false
    }

    if (updateComponent(editablePageData.value)) {
      // ç·¨é›†è¦ç´ ã®HTMLã‚‚ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ï¼ˆè¡¨ç¤ºã®ä¸€è²«æ€§ã®ãŸã‚ï¼‰
      if (currentEditingElement.value && rawContent !== cleanedContent) {
        currentEditingElement.value.innerHTML = cleanedContent
      }

      // Vue.jsãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ“ãƒ†ã‚£ã‚’å¼·åˆ¶çš„ã«ãƒˆãƒªã‚¬ãƒ¼ï¼ˆtriggerRefã§ç¢ºå®Ÿã«æ›´æ–°ï¼‰
      nextTick(() => {
        const deepCopy = JSON.parse(JSON.stringify(editablePageData.value))
        editablePageData.value = deepCopy
        
        // triggerRefã§ãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ“ãƒ†ã‚£ã‚’å¼·åˆ¶å®Ÿè¡Œ
        triggerRef(editablePageData)
        
        nextTick(() => {
          emit('update:page', editablePageData.value)
          console.log('ðŸ”„ [Z-FORç·¨é›†] triggerRefå¼·åˆ¶æ›´æ–°å®Œäº†:', {
            dataLength: editablePageData.value.length,
            hasUpdatedContent: JSON.stringify(editablePageData.value).includes('ã‚ã‚ã‚')
          })
        })
      })
    }
  }

  // ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆç·¨é›†çµ‚äº†
  const stopRichTextEdit = (save: boolean) => {
    if (!currentEditingElement.value) return

    logger.debug('ðŸ”§ ç·¨é›†çµ‚äº†:', save ? 'ä¿å­˜' : 'ã‚­ãƒ£ãƒ³ã‚»ãƒ«')

    if (save) {
      // å¤‰æ›´ã‚’ä¿å­˜
      updateComponentDataSync()
      logger.debug('ðŸ”§ æœ€çµ‚ä¿å­˜å®Œäº†')
    } else {
      // å¤‰æ›´ã‚’ç ´æ£„
      currentEditingElement.value.innerHTML = originalContent.value
      logger.debug('ðŸ”§ å¤‰æ›´ã‚’ç ´æ£„ã—ã¾ã—ãŸ')
    }

    // ç·¨é›†ãƒ¢ãƒ¼ãƒ‰ã‚’çµ‚äº†
    currentEditingElement.value.contentEditable = 'false'
    currentEditingElement.value.classList.remove('editing')

    // user-selectã‚¹ã‚¿ã‚¤ãƒ«ã‚’ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—
    currentEditingElement.value.style.userSelect = ''
    currentEditingElement.value.style.webkitUserSelect = ''
    logger.debug('ðŸ”§ ç·¨é›†çµ‚äº†ã€ã‚¹ã‚¿ã‚¤ãƒ«ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—å®Œäº†')

    logger.debug('ðŸ”§ ç·¨é›†ãƒ¢ãƒ¼ãƒ‰çµ‚äº†å‡¦ç†å®Œäº†')

    // ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—
    if ((currentEditingElement.value as any)._cleanup) {
      ;(currentEditingElement.value as any)._cleanup()
      delete (currentEditingElement.value as any)._cleanup
    }

    // ç·¨é›†çŠ¶æ…‹ã‚’ã‚¯ãƒªã‚¢
    currentEditingElement.value = null
    originalContent.value = ''
    currentComponentId.value = ''
    currentParamName.value = ''

    // ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ã‚’éžè¡¨ç¤º
    showRichTextToolbar.value = false
    logger.debug('ðŸ”§ ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ã‚’éžè¡¨ç¤ºã«ã—ã¾ã—ãŸ')

    // Tiptapç·¨é›†çµ‚äº†ï¼ˆç¾åœ¨ã¯ç„¡åŠ¹åŒ–ä¸­ï¼‰
    // editor.actions.stopTiptapEdit()
  }

  // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼æ“ä½œé–¢æ•°
  const openEditor = (index: number) => {
