  // ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã®ä¸¦ã¹æ›¿ãˆå‡¦ç†
  const reorderNestedParts = (pathString: string, newOrder: Component[]) => {
    logger.debug('ðŸ”§ ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ä¸¦ã¹æ›¿ãˆ:', { pathString, newOrderCount: newOrder.length })
    editor.actions.reorderNestedParts(pathString, newOrder)
  }

  // ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã®ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—æ©Ÿèƒ½ã®ã‚»ãƒƒãƒˆã‚¢ãƒƒãƒ—
  const setupNestedDragAndDrop = () => {
    const container = document.querySelector(`.${componentInstanceId.value}`)
    if (!container) return

    let draggedElement: HTMLElement | null = null
    let draggedData: {
      componentId: string
      path: string
      parentPath: string
      index: number
    } | null = null

    // ã‚¤ãƒ™ãƒ³ãƒˆãƒãƒ³ãƒ‰ãƒ©ãƒ¼é–¢æ•°ã‚’å®šç¾©ï¼ˆã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ã®ãŸã‚ï¼‰
    const handleDragStart = (e: Event) => {
      const event = e as DragEvent
      const target = event.target as HTMLElement

      if (target.classList.contains('draggable-nested')) {
        // ãƒ†ã‚­ã‚¹ãƒˆç·¨é›†ä¸­ã¯ãƒ‰ãƒ©ãƒƒã‚°ã‚’ç„¡åŠ¹åŒ–
        if (currentEditingElement.value) {
          logger.debug('ðŸ”§ ãƒ†ã‚­ã‚¹ãƒˆç·¨é›†ä¸­ã®ãŸã‚ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ãƒ‰ãƒ©ãƒƒã‚°ã‚’ç„¡åŠ¹åŒ–')
          event.preventDefault()
          return
        }

        // z-forå†…ã®ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã¯z-forå°‚ç”¨ã®ãƒãƒ³ãƒ‰ãƒ©ãƒ¼ã«ä»»ã›ã‚‹
        if (target.classList.contains('z-for-nested-part')) {
          logger.debug('ðŸ”§ z-forå†…ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ - z-forå°‚ç”¨ãƒãƒ³ãƒ‰ãƒ©ãƒ¼ã«å§”è­²')
          return
        }

        draggedElement = target

        const componentId = target.dataset.componentId || ''
        const path = target.dataset.path || ''
        const parentPath = target.dataset.parentPath || ''
        const index = parseInt(target.dataset.index || '0')

        draggedData = { componentId, path, parentPath, index }

        if (event.dataTransfer) {
          event.dataTransfer.effectAllowed = 'move'
          event.dataTransfer.setData('text/plain', 'nested-component')
        }

        target.style.opacity = '0.5'
        logger.debug('ðŸ”§ ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ãƒ‰ãƒ©ãƒƒã‚°é–‹å§‹:', draggedData)
      }
    }

    const handleDragOver = (e: Event) => {
      const event = e as DragEvent
      event.preventDefault()
      if (event.dataTransfer) {
        event.dataTransfer.dropEffect = 'move'
      }
    }

    const handleDrop = (e: Event) => {
      const event = e as DragEvent
      event.preventDefault()

      if (!draggedElement || !draggedData) return

      const target = event.target as HTMLElement
      const dropTarget = target.closest('.draggable-nested') as HTMLElement

      if (dropTarget && dropTarget !== draggedElement) {
        const targetParentPath = dropTarget.dataset.parentPath || ''
        const targetIndex = parseInt(dropTarget.dataset.index || '0')

        logger.debug('ðŸ”§ ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ãƒ‰ãƒ­ãƒƒãƒ—:', {
          draggedParentPath: draggedData.parentPath,
          draggedIndex: draggedData.index,
          targetParentPath,
          targetIndex,
        })

        // åŒã˜è¦ªå†…ã§ã®ä¸¦ã¹æ›¿ãˆã®ã¿å¯¾å¿œ
        if (draggedData.parentPath === targetParentPath) {
          logger.debug('ðŸ”§ åŒã˜è¦ªå†…ã§ã®ä¸¦ã¹æ›¿ãˆå®Ÿè¡Œ')
          // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆIDãƒ™ãƒ¼ã‚¹ã®ä¸¦ã¹æ›¿ãˆã‚’ä½¿ç”¨
          editor.actions.moveNestedPartById(draggedData.parentPath, draggedData.index, targetIndex)
        } else {
          logger.debug('ðŸ”§ ç•°ãªã‚‹è¦ªé–“ã®ç§»å‹•ã¯æœªå¯¾å¿œ')
        }
      }

      // ãƒ‰ãƒ©ãƒƒã‚°çµ‚äº†å‡¦ç†
      if (draggedElement) {
        draggedElement.style.opacity = '1'
      }
      draggedElement = null
      draggedData = null
    }

    const handleDragEnd = (e: Event) => {
      const target = e.target as HTMLElement
      if (target.classList.contains('draggable-nested')) {
        target.style.opacity = '1'
      }
      draggedElement = null
      draggedData = null
    }

    // ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚’ç™»éŒ²
    container.addEventListener('dragstart', handleDragStart)
    container.addEventListener('dragover', handleDragOver)
    container.addEventListener('drop', handleDrop)
    container.addEventListener('dragend', handleDragEnd)

    logger.debug('ðŸ”§ ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—ãƒªã‚¹ãƒŠãƒ¼è¨­å®šå®Œäº†')

    // ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—é–¢æ•°ã‚’è¿”ã™
    return () => {
      container.removeEventListener('dragstart', handleDragStart)
      container.removeEventListener('dragover', handleDragOver)
      container.removeEventListener('drop', handleDrop)
      container.removeEventListener('dragend', handleDragEnd)
      logger.debug('ðŸ”§ ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—ãƒªã‚¹ãƒŠãƒ¼å‰Šé™¤å®Œäº†')
    }
  }
  // ãƒ‡ãƒ¼ã‚¿è¡¨ç¤ºé–¢é€£ã®çŠ¶æ…‹
  const displayedData = ref<any>(null)
  const displayedDataTitle = ref('')
  const displayedDataType = ref('')

  // ãƒ‡ãƒ¼ã‚¿è¡¨ç¤ºå‡¦ç†
  const handleDataDisplay = (dataType: string) => {
