        if (!data || data.length === 0) {
          data = props.parts?.map(section => ({
            ...section,
            module: section.module?.filter(part => !part.category || part.category === 'common')
          })).filter(section => section.module && section.module.length > 0) || []
        }
        title = 'å…±é€šPartsãƒ‡ãƒ¼ã‚¿'
        console.log('ðŸ” æœ€çµ‚å…±é€šãƒ‘ãƒ¼ãƒ„ãƒ‡ãƒ¼ã‚¿:', JSON.stringify(data, null, 2))
        break
      case 'individualParts':
        data = editor.state.localIndividualParts.value
        // ç©ºã®å ´åˆã¯localPartsã‹ã‚‰å€‹åˆ¥ãƒ‘ãƒ¼ãƒ„ã‚’æŠ½å‡º
        if (!data || data.length === 0) {
          data = localParts.value?.map(section => ({
            ...section,
            module: section.module?.filter(part => part.category === 'individual')
          })).filter(section => section.module && section.module.length > 0) || []
        }
        // ãã‚Œã§ã‚‚ç©ºã®å ´åˆã¯propsã‹ã‚‰å–å¾—
        if (!data || data.length === 0) {
          data = props.parts?.map(section => ({
            ...section,
            module: section.module?.filter(part => part.category === 'individual')
          })).filter(section => section.module && section.module.length > 0) || []
        }
        title = 'å€‹åˆ¥Partsãƒ‡ãƒ¼ã‚¿'
        console.log('ðŸ” æœ€çµ‚å€‹åˆ¥ãƒ‘ãƒ¼ãƒ„ãƒ‡ãƒ¼ã‚¿:', JSON.stringify(data, null, 2))
        break
      case 'commonImages':
        data = commonImages.value
        title = 'å…±é€šç”»åƒãƒ‡ãƒ¼ã‚¿'
        break
      case 'individualImages':
        data = individualImages.value
        title = 'å€‹åˆ¥ç”»åƒãƒ‡ãƒ¼ã‚¿'
        break
      default:
        return
    }
    
    displayedData.value = data
    displayedDataTitle.value = title
    displayedDataType.value = dataType
  }

  // ãƒ‡ãƒ¼ã‚¿è¡¨ç¤ºã‚¨ãƒªã‚¢ã‚’é–‰ã˜ã‚‹
  const closeDataDisplay = () => {
    displayedData.value = null
    displayedDataTitle.value = ''
