  const componentRenderCache = ref<Map<string, string>>(new Map())
  
  // ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°é–¢æ•° - ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã®UIä»˜ããƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°
  const renderComponentWithNested = (component: any, componentIndex?: number) => {
    console.log(`ðŸ”¥ [EDITOR] renderComponentWithNestedå‘¼ã³å‡ºã— - componentId: ${component.id}, type: ${component.type}/${component.module_name}`);
    // ãƒ‡ãƒãƒƒã‚°ç”¨ç›´æŽ¥ç¢ºèª
    if (component.id.includes('div_nested_zfor')) {
      // é‡è¦ï¼šå­z-forã®ãƒ‡ãƒ¼ã‚¿ãŒå­˜åœ¨ã™ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
    }
    // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã®ã‚¤ãƒ³ãƒ‡ãƒƒã‚¯ã‚¹ã‚’å–å¾—ï¼ˆæœªæŒ‡å®šã®å ´åˆã¯é…åˆ—ã‹ã‚‰æ¤œç´¢ï¼‰
    let actualIndex = componentIndex
    if (actualIndex === undefined) {
      actualIndex = editablePageData.value.findIndex(c => c.id === component.id)
      if (actualIndex === -1) actualIndex = 0
    }

    // æœ€æ–°ã®editablePageDataã‹ã‚‰è©²å½“ã™ã‚‹ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’å–å¾—ï¼ˆä¸¦ã³æ›¿ãˆå¾Œã®ãƒ‡ãƒ¼ã‚¿ã‚’ç¢ºä¿ï¼‰
    const latestComponent = editablePageData.value[actualIndex] || component
    
    // é…åˆ—ãƒ‡ãƒ¼ã‚¿ã®ãƒãƒƒã‚·ãƒ¥ã‚’ç”Ÿæˆã—ã¦å¤‰æ›´æ¤œå‡ºï¼ˆãƒã‚¹ãƒˆé…åˆ—ã‚‚å«ã‚€ï¼‰
    const arrayKeys = Object.keys(latestComponent).filter(k => Array.isArray(latestComponent[k]))
    // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id} - é…åˆ—ã‚­ãƒ¼:`, arrayKeys)
    
    const generateDeepArrayHash = (obj: any, prefix = ''): string[] => {
      const hashes: string[] = []
      
      for (const [key, value] of Object.entries(obj)) {
        if (Array.isArray(value)) {
          const fullKey = prefix ? `${prefix}.${key}` : key
          // é…åˆ—ã®é †åºã‚‚å«ã‚ãŸãƒãƒƒã‚·ãƒ¥ã‚’ç”Ÿæˆ
          const arrayHash = `${fullKey}:${value.length}:${JSON.stringify(value.map((item: any, index: number) => ({
            i: index, // é †åºæƒ…å ±ã‚’å«ã‚ã‚‹
            id: item?.id || item?.name || `item_${index}`,
            // ãƒã‚¹ãƒˆé…åˆ—ã‚‚å«ã‚ã¦ãƒãƒƒã‚·ãƒ¥ç”Ÿæˆï¼ˆé…åˆ—é™¤å¤–ãƒ•ã‚£ãƒ«ã‚¿ã‚’å‰Šé™¤ï¼‰
            ...Object.fromEntries(Object.entries(item || {}).slice(0, 3))
          })))}`
          hashes.push(arrayHash)
          // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id} - ${fullKey}ãƒãƒƒã‚·ãƒ¥:`, arrayHash)
          
          // ãƒã‚¹ãƒˆé…åˆ—ã‚‚å†å¸°çš„ã«å‡¦ç†
          value.forEach((item: any, index: number) => {
            if (item && typeof item === 'object') {
              const nestedHashes = generateDeepArrayHash(item, `${fullKey}[${index}]`)
              hashes.push(...nestedHashes)
            }
          })
        }
      }
      
      return hashes
    }
    
    const allHashes = generateDeepArrayHash(latestComponent)
    const currentArrayHash = allHashes.join('|')
    
    // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id} - å…¨ä½“ãƒãƒƒã‚·ãƒ¥: ${currentArrayHash}`)
    
    const cacheKey = `${latestComponent.id}-${actualIndex}`
    const previousHash = componentRenderCache.value.get(cacheKey)
    const arrayDataChanged = previousHash !== currentArrayHash
    
    if (arrayDataChanged) {
      componentRenderCache.value.set(cacheKey, currentArrayHash)
      // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id}ã®é…åˆ—ãƒ‡ãƒ¼ã‚¿ãŒå¤‰æ›´ã•ã‚Œã¾ã—ãŸ:`)
      
      // å¤‰æ›´ã•ã‚ŒãŸé…åˆ—ã®è©³ç´°ã‚’ãƒ­ã‚°å‡ºåŠ›
      const currentArrays = Object.keys(latestComponent).filter(k => Array.isArray(latestComponent[k]))
      currentArrays.forEach(arrayName => {
        const array = latestComponent[arrayName] as any[]
        if (array.length > 0) {
          // console.log(`[Z-FORæ¤œè¨¼] - ${arrayName} (${array.length}é …ç›®):`, array.slice(0, 3).map((item: any, idx: number) => ({ 
          //   index: idx, 
          //   id: item.id, 
          //   ...Object.fromEntries(Object.entries(item).filter(([k]) => k !== 'id').slice(0, 2)) 
          // })))
        }
      })
      
    }

    // console.log(`[Z-FORæ¤œè¨¼] renderComponentWithNested: ${latestComponent.id}, arrayDataChanged: ${arrayDataChanged}`)
    // console.log(`[Z-FORæ¤œè¨¼] - ç¾åœ¨ã®ãƒãƒƒã‚·ãƒ¥: ${currentArrayHash.substring(0, 200)}...`)
    // console.log(`[Z-FORæ¤œè¨¼] - å‰å›žã®ãƒãƒƒã‚·ãƒ¥: ${(previousHash || 'undefined').substring(0, 200)}...`)
    // console.log(`[Z-FORæ¤œè¨¼] - ãƒãƒƒã‚·ãƒ¥æ¯”è¼ƒçµæžœ: ${previousHash !== currentArrayHash}`)

    // renderNestedComponentsWithUIã‚³ãƒ¼ãƒ«ãƒãƒƒã‚¯ã‚’æ¸¡ã—ã¦ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã®ãƒ‰ãƒ©ãƒƒã‚°æ©Ÿèƒ½ã‚’æœ‰åŠ¹åŒ–
    const renderedResult = editor.templateProcessor.renderComponentWithNested(
      latestComponent, // æœ€æ–°ãƒ‡ãƒ¼ã‚¿ã‚’ä½¿ç”¨
      actualIndex, // å®Ÿéš›ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚¤ãƒ³ãƒ‡ãƒƒã‚¯ã‚¹ã‚’æ¸¡ã™
      (type: string, moduleName: string) =>
        editor.utils.template.getTemplate(type, moduleName, localParts.value),
      // ãƒã‚¹ãƒˆã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ç”¨ãƒ©ãƒƒãƒ‘ãƒ¼é–¢æ•°
      (
        components: Component[],
        currentPath: number[],
        stringifyHierarchyPath: (path: number[]) => string,
        getTemplate?: (type: string, moduleName: string) => string
      ) => {
        // console.log(`[Z-FORæ¤œè¨¼] renderNestedComponentsWithUI called for path: ${currentPath.join('-')}, components: ${components.length}`)
        return editor.templateProcessor.renderNestedComponentsWithUI(
          components,
          currentPath,
          (path: number[]) => path.join('-'), // ãƒ‘ã‚¹é…åˆ—ã‚’æ–‡å­—åˆ—ã«å¤‰æ›
          getTemplate ||
            ((type: string, moduleName: string) =>
              editor.utils.template.getTemplate(type, moduleName, localParts.value))
        )
      }
    )
    
    // ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼çµæžœã‚’ãƒ­ã‚°å‡ºåŠ›ï¼ˆãƒ‡ãƒãƒƒã‚°ç”¨ï¼‰
    if (latestComponent.id === 'div_nested_zfor_001') {
      // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id} - ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°çµæžœ:`, renderedResult)
      // ãƒã‚¹ãƒˆé…åˆ—ã®å†…å®¹ãŒHTMLã«å«ã¾ã‚Œã¦ã„ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
      // const hasInnerItems = renderedResult.includes('inner_items') || renderedResult.includes('ãƒã‚¹ãƒˆ1-1')
      // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id} - å­z-forå«æœ‰ãƒã‚§ãƒƒã‚¯:`, hasInnerItems)
      // z-forè¦ç´ ãŒå«ã¾ã‚Œã¦ã„ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
      // const hasZForElements = renderedResult.includes('z-for')
      // console.log(`[Z-FORæ¤œè¨¼] ${latestComponent.id} - z-forè¦ç´ æ®‹å­˜ãƒã‚§ãƒƒã‚¯:`, hasZForElements)
    }
    
    console.log(`ðŸ”¥ [EDITOR] ${latestComponent.id} - æœ€çµ‚ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°çµæžœ:`, {
      resultLength: renderedResult?.length || 0,
      resultPreview: renderedResult?.substring(0, 300) + '...',
      hasInnerItems: renderedResult?.includes('inner_items'),
      hasNestedContent: renderedResult?.includes('ãƒã‚¹ãƒˆ1-1')
    });
    
    
    // ç„¡é™ãƒ«ãƒ¼ãƒ—ã‚’å›žé¿ã—ã¦é€šå¸¸å‡¦ç†ã«æˆ»ã™
    
    // ç”»åƒURLè§£æ±ºå‡¦ç†ã‚’è¿½åŠ 
    const resolvedResult = resolveImageUrlsInHTML(renderedResult)
    
    return resolvedResult
  }

