  const deletePartFromManager = (partInfo: { type: string; title: string }) => {
    console.log('ðŸ”§ deletePartFromManagerå‘¼ã³å‡ºã—:', partInfo)
    const { type, title } = partInfo
    
    if (confirm(`ãƒ‘ãƒ¼ãƒ„ã€Œ${title}ã€ã‚’å‰Šé™¤ã—ã¾ã™ã‹ï¼Ÿ`)) {
      // ãƒ‘ãƒ¼ãƒ„ã‚’å‰Šé™¤ã™ã‚‹å…±é€šé–¢æ•°
      const deletePartFromArray = (partsArray: any[]) => {
        return partsArray.map(section => {
          if (section.type === type) {
            return {
              ...section,
              module: section.module.filter((module: any) => module.title !== title)
            }
          }
          return section
        }).filter(section => section.module.length > 0)
      }

      // localPartsã‚’æ›´æ–°
      const updatedParts = deletePartFromArray(localParts.value)
      localParts.value = updatedParts
      emit('update:parts', updatedParts)

      // localCommonPartsãŒå­˜åœ¨ã™ã‚‹å ´åˆã¯æ›´æ–°
      if (editor.state.localCommonParts.value.length > 0) {
        const updatedCommonParts = deletePartFromArray(editor.state.localCommonParts.value)
        editor.state.localCommonParts.value = updatedCommonParts
        emit('update:commonParts', updatedCommonParts)
      }

      // localIndividualPartsãŒå­˜åœ¨ã™ã‚‹å ´åˆã¯æ›´æ–°
