  const saveNewPart = (partData: any) => {
    const { type, title, body, category = 'common' } = partData
    const updatedParts = [...localParts.value]

    // æ—¢å­˜ã‚»ã‚¯ã‚·ãƒ§ãƒ³ã‚’æ¤œç´¢
    let targetSection = updatedParts.find(section => section.type === type)

    if (!targetSection) {
      // æ–°ã—ã„ã‚»ã‚¯ã‚·ãƒ§ãƒ³ã‚’ä½œæˆ
      targetSection = { type, module: [] }
      updatedParts.push(targetSection)
    }

    // ã‚«ãƒ†ã‚´ãƒªæƒ…å ±ã‚’å«ã‚€ãƒ‘ãƒ¼ãƒ„ãƒ‡ãƒ¼ã‚¿ã‚’ä½œæˆ
    const partWithCategory = { title, body, category }

    // ç·¨é›†ä¸­ã®ãƒ‘ãƒ¼ãƒ„ã‹ã©ã†ã‹ã‚’ç¢ºèª
    if (editingPart.value) {
      // æ—¢å­˜ãƒ‘ãƒ¼ãƒ„ã®æ›´æ–°
      const moduleIndex = targetSection.module.findIndex(
        module => module.title === editingPart.value!.module.title
      )
      if (moduleIndex !== -1) {
        targetSection.module[moduleIndex] = partWithCategory
      }
    } else {
      // æ–°è¦ãƒ‘ãƒ¼ãƒ„ã®è¿½åŠ 
      targetSection.module.push(partWithCategory)
    }

    localParts.value = updatedParts
    emit('update:parts', updatedParts)

    // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼ã‚’é–‰ã˜ã‚‹
    closePartRegistration()
  }

