        const arrayIndex = (component as any)._arrayIndex

        logger.debug(
          `ðŸ”„ z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ãƒªã‚¢ãƒ«ã‚¿ã‚¤ãƒ æ›´æ–°: ${parentComponentId}.${arrayName}[${arrayIndex}].${key} = ${value}`
        )

        // findComponentRecursivelyé–¢æ•°ã‚’ä½¿ç”¨ã—ã¦è¦ªã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’æ¤œç´¢
        const findComponentRecursively = (components: any[], targetId: string): any => {
          for (const comp of components) {
            if (comp.id === targetId) {
              return comp
            }
            // nested_partså†…ã‚’æ¤œç´¢
            if (comp.nested_parts && Array.isArray(comp.nested_parts)) {
              const found = findComponentRecursively(comp.nested_parts, targetId)
              if (found) return found
            }
            // å…¨ã¦ã®ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã‹ã‚‰é…åˆ—ã‚’æ¤œç´¢ï¼ˆz-foré…åˆ—å†…ã‚‚å«ã‚€ï¼‰
            for (const [propKey, propValue] of Object.entries(comp)) {
              if (Array.isArray(propValue) && propKey !== 'nested_parts') {
                for (const arrayItem of propValue) {
                  if (arrayItem && typeof arrayItem === 'object') {
                    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å†…ã®å…¨ã‚¹ãƒ­ãƒƒãƒˆã‚’æ¤œç´¢
                    for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                      if (Array.isArray(slotValue)) {
                        const found = findComponentRecursively(slotValue, targetId)
                        if (found) return found
                      }
                    }
                  }
                }
              }
            }
          }
          return null
        }

        const parentComponent = findComponentRecursively(editablePageData.value, parentComponentId)
        if (parentComponent && Array.isArray(parentComponent[arrayName])) {
          const array = parentComponent[arrayName]
          if (arrayIndex >= 0 && arrayIndex < array.length) {
            // å…ƒã®é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã‚’æ›´æ–°
            array[arrayIndex][key] = value
            logger.debug(
              `âœ… z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ æ›´æ–°å®Œäº†: ${parentComponentId}.${arrayName}[${arrayIndex}].${key}`
            )
          }
        }
      }

      emit('update:page', editablePageData.value)
    }
  }

  // propsã®ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’ç›£è¦–ã—ã¦åæ˜ 
  watch(
    () => props.commonImages,
    newCommonImages => {
      if (Array.isArray(newCommonImages)) {
        commonImages.value = JSON.parse(JSON.stringify(newCommonImages))
      }
    },
    { immediate: true }
  )

  watch(
    () => props.individualImages,
    newIndividualImages => {
      if (Array.isArray(newIndividualImages)) {
        individualImages.value = JSON.parse(JSON.stringify(newIndividualImages))
      }
    },
    { immediate: true }
  )

  // ãƒšãƒ¼ã‚¸ãƒ‡ãƒ¼ã‚¿ãŒå¤‰æ›´ã•ã‚ŒãŸæ™‚ã«ç”»åƒç·¨é›†ãƒœã‚¿ãƒ³ã‚’å†ç”Ÿæˆ
  let addImageEditButtonsFunction: (() => void) | null = null
  
  watch(
    () => editablePageData.value,
    () => {
      if (addImageEditButtonsFunction) {
        nextTick(() => {
          addImageEditButtonsFunction!()
        })
      }
    },
    { deep: true }
  )

  // ç”»åƒé¸æŠžãƒ¢ãƒ¼ãƒ€ãƒ«é–¢æ•°
  const openGlobalImageSelector = (component: Component, fieldName: string, currentValue: string) => {
    currentGlobalImageComponent.value = component
    currentGlobalImageField.value = fieldName
    currentGlobalImageValue.value = currentValue
    showGlobalImageSelector.value = true
  }

  const closeGlobalImageSelector = () => {
    showGlobalImageSelector.value = false
    currentGlobalImageComponent.value = null
    currentGlobalImageField.value = ''
    currentGlobalImageValue.value = ''
  }

  const onGlobalImageSelected = (imageUrl: string, imageAlt: string) => {
    if (currentGlobalImageComponent.value && currentGlobalImageField.value) {
      // Base64ã®data URLã®å ´åˆã¯ã€å…ƒã®ç”»åƒURLã‚’å–å¾—
      let originalUrl = imageUrl
      let selectedImageData = null
      
      if (imageUrl.startsWith('data:')) {
        // Base64 URLã®å ´åˆã€å¯¾å¿œã™ã‚‹å…ƒã®URLã‚’è¦‹ã¤ã‘ã‚‹
        const allImagesData = [...commonImages.value, ...individualImages.value]
        selectedImageData = allImagesData.find(img => getImageSrc(img) === imageUrl)
        if (selectedImageData) {
          originalUrl = selectedImageData.url
        }
      } else {
        // é€šå¸¸ã®URLã®å ´åˆã€å¯¾å¿œã™ã‚‹ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’è¦‹ã¤ã‘ã‚‹
        const allImagesData = [...commonImages.value, ...individualImages.value]
        selectedImageData = allImagesData.find(img => img.url === imageUrl)
      }
      
      // é¸æŠžã•ã‚ŒãŸç”»åƒãŒå…ƒã®ã‚ªãƒªã‚¸ãƒŠãƒ«ç”»åƒã®å ´åˆã€Base64ãƒ‡ãƒ¼ã‚¿ã‚’ãƒªã‚»ãƒƒãƒˆ
      if (selectedImageData && !imageUrl.startsWith('data:')) {
        // å…ƒã®ç”»åƒã‚’é¸æŠžã—ãŸå ´åˆã€Base64ãƒ‡ãƒ¼ã‚¿ã‚’ã‚¯ãƒªã‚¢
        // ãŸã ã—ã€ãã®ç”»åƒãŒç¾åœ¨Base64ã§å·®ã—æ›¿ãˆã‚‰ã‚Œã¦ã„ã‚‹å ´åˆã®ã¿
        if (selectedImageData.fileData?.base64 && selectedImageData.fileData.base64.trim() !== '') {
          resetImageToOriginal(selectedImageData.id)
        }
      }
      
      // ã‚ªãƒªã‚¸ãƒŠãƒ«ã®URLã‚’è¨­å®šï¼ˆresolveImageUrlsInHTMLã§é©åˆ‡ã«è§£æ±ºã•ã‚Œã‚‹ï¼‰
      currentGlobalImageComponent.value[currentGlobalImageField.value] = originalUrl
      
      // altãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ãŒã‚ã‚‹å ´åˆã¯è‡ªå‹•è¨­å®š
      const altFieldKey = currentGlobalImageField.value.replace(/url$/i, 'alt').replace(/image$/i, 'alt')
      if (altFieldKey !== currentGlobalImageField.value && currentGlobalImageComponent.value[altFieldKey] !== undefined) {
        currentGlobalImageComponent.value[altFieldKey] = imageAlt
      }
      
      // ãƒšãƒ¼ã‚¸ãƒ‡ãƒ¼ã‚¿ã‚’æ›´æ–°
      emit('update:page', editablePageData.value)
      
      // æ˜Žç¤ºçš„ã«å†ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ã‚’ãƒˆãƒªã‚¬ãƒ¼
      nextTick(() => {
        triggerRef(editablePageData)
        // ãƒ‘ãƒ¼ãƒ„ãƒ‡ãƒ¼ã‚¿ã‚‚æ›´æ–°ã‚’ãƒˆãƒªã‚¬ãƒ¼
        if (localParts.value) {
          triggerRef(localParts)
        }
      })
      
      logger.debug('ðŸ–¼ï¸ ç”»åƒé¸æŠžå®Œäº†:', originalUrl)
    }
    closeGlobalImageSelector()
  }
  
  // ãƒ›ãƒãƒ¼æ™‚ã®ã¿è¡¨ç¤ºæ–¹å¼ã«å¤‰æ›´ã—ãŸãŸã‚ãƒ¢ãƒ¼ãƒ€ãƒ«ç›£è¦–ã¯ä¸è¦

  // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå›ºæœ‰ã‚¯ãƒªãƒƒã‚¯ãƒãƒ³ãƒ‰ãƒ©ãƒ¼ï¼ˆç·¨é›†å¤–ã‚¯ãƒªãƒƒã‚¯æ™‚ã®è‡ªå‹•ä¿å­˜ï¼‰
  const handleComponentClick = (e: Event) => {
    if (!currentEditingElement.value || !showRichTextToolbar.value) return

    const clickedElement = e.target as HTMLElement
    
    // è‡ªåˆ†ã®ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹å†…ã®ã‚¯ãƒªãƒƒã‚¯ã‹ã©ã†ã‹ã‚’ç¢ºèª
    const componentElement = document.querySelector(`.${componentInstanceId.value}`)
    if (!componentElement || !componentElement.contains(clickedElement)) {
      return // ä»–ã®ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã®ã‚¯ãƒªãƒƒã‚¯ã¯ç„¡è¦–
    }

    // ç·¨é›†ä¸­ã®è¦ç´ ã‚„ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ãŒã‚¯ãƒªãƒƒã‚¯ã•ã‚ŒãŸå ´åˆã¯ä½•ã‚‚ã—ãªã„
    if (
      clickedElement.closest('.zero-code-rich-text-toolbar') ||
      clickedElement === currentEditingElement.value ||
      currentEditingElement.value.contains(clickedElement)
    ) {
      return
    }

    logger.debug('ðŸ”§ ç·¨é›†å¤–ã‚¨ãƒªã‚¢ã‚¯ãƒªãƒƒã‚¯ã€è‡ªå‹•ä¿å­˜ã—ã¦ãƒ„ãƒ¼ãƒ«ãƒãƒ¼éžè¡¨ç¤º')
    stopRichTextEdit(true)
  }

  // ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—ã®ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—é–¢æ•°
  let cleanupNestedDragAndDrop: (() => void) | null = null
  
  // ç”»åƒã‚¯ãƒªãƒƒã‚¯ãƒãƒ³ãƒ‰ãƒ©ãƒ¼ï¼ˆã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ç”¨ï¼‰
  let imageClickHandler: ((e: Event) => void) | null = null

  // åˆæœŸåŒ–å‡¦ç†
  onMounted(async () => {
    logger.debug('ðŸ”§ ZeroCodeåˆæœŸåŒ–é–‹å§‹')


    // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå›ºæœ‰ã‚¯ãƒªãƒƒã‚¯ãƒªã‚¹ãƒŠãƒ¼ã‚’è¿½åŠ 
    document.addEventListener('click', handleComponentClick)
    logger.debug('ðŸ”§ ã‚°ãƒ­ãƒ¼ãƒãƒ«ã‚¯ãƒªãƒƒã‚¯ãƒªã‚¹ãƒŠãƒ¼è¿½åŠ å®Œäº†')

    // æµ®éŠç·¨é›†ãƒœã‚¿ãƒ³ã‚’ç”»åƒã«è¿½åŠ ã™ã‚‹å‡¦ç†
    const addImageEditButtons = () => {
      logger.debug('ðŸ–¼ï¸ æµ®éŠç·¨é›†ãƒœã‚¿ãƒ³è¿½åŠ å‡¦ç†é–‹å§‹')
      
      // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå†…ã®ç”»åƒè¦ç´ ã‚’å–å¾—
      const componentElement = document.querySelector(`.${componentInstanceId.value}`)
      if (!componentElement) return
      
      const allImages = componentElement.querySelectorAll('img')
      logger.debug('ðŸ–¼ï¸ æ¤œå‡ºã•ã‚ŒãŸç”»åƒæ•°:', allImages.length)
      
      allImages.forEach((imgElement) => {
        const img = imgElement as HTMLImageElement
        
        // æ—¢ã«ç·¨é›†ãƒœã‚¿ãƒ³ãŒè¿½åŠ ã•ã‚Œã¦ã„ã‚‹å ´åˆã¯ã‚¹ã‚­ãƒƒãƒ—
        if (img.hasAttribute('data-edit-button-id')) {
          return
        }
        
        // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼ã‚³ãƒ³ãƒ†ãƒ³ãƒ„å†…ã®ç”»åƒã®ã¿ã‚’å¯¾è±¡ï¼ˆã‚¯ãƒ©ã‚¹åˆ¤å®šï¼‰
        if (!img.classList.contains('zero-code-editable-image')) {
          return
        }
        
        // é©å½“ãªã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’ä½¿ç”¨ï¼ˆå¾Œã§æ”¹å–„äºˆå®šï¼‰
        const testComponent = editablePageData.value?.[0]
        
        // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼ã‚³ãƒ³ãƒ†ãƒ³ãƒ„å†…ã®ç”»åƒã®ã¿å‡¦ç†
        if (testComponent) {
          try {
            // ç”»åƒã®ä½ç½®ã‚’å–å¾—
            const imgRect = img.getBoundingClientRect()
            logger.debug('ðŸ” ç”»åƒä½ç½®æƒ…å ±:', {
              src: img.src.substring(img.src.lastIndexOf('/') + 1),
              rect: {
                top: imgRect.top,
                left: imgRect.left,
