              scrollX: window.scrollX
            })
            
            // å°ã•ãªæµ®éŠç·¨é›†ãƒœã‚¿ãƒ³ã‚’ä½œæˆ
            const editButton = document.createElement('button')
            editButton.innerHTML = 'âœï¸'
            editButton.className = 'zero-code-image-floating-button'
            // position: fixedã®å ´åˆã¯ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«é‡ã‚’åŠ ç®—ã—ãªã„
            const buttonTop = imgRect.top + 8
            const buttonLeft = imgRect.right - 32
            
            logger.debug('ðŸ” ãƒœã‚¿ãƒ³è¨ˆç®—ä½ç½®:', { buttonTop, buttonLeft })
            
            editButton.style.cssText = `
              position: fixed !important;
              top: ${buttonTop}px !important;
