              font-size: 12px !important;
              cursor: pointer !important;
              box-shadow: 0 3px 12px rgba(59, 130, 246, 0.4), 0 1px 4px rgba(0, 0, 0, 0.2) !important;
              z-index: 1000 !important;
              opacity: 0 !important;
              font-family: inherit !important;
              display: flex !important;
              align-items: center !important;
              justify-content: center !important;
              pointer-events: auto !important;
            `
            
            // ç”»åƒã«ãƒ‡ãƒ¼ã‚¿å±žæ€§ã‚’è¿½åŠ ã—ã¦ãƒœã‚¿ãƒ³ã¨ã®é–¢é€£ã‚’è¨˜éŒ²
            img.setAttribute('data-edit-button-id', `edit-btn-${Date.now()}`)
            editButton.setAttribute('data-target-image', img.getAttribute('data-edit-button-id')!)
            
            // ãƒ›ãƒãƒ¼ã‚¤ãƒ™ãƒ³ãƒˆ
            // ãƒ›ãƒãƒ¼æ™‚ã®ã¿è¡¨ç¤ºåˆ¶å¾¡ï¼ˆã‚·ãƒ³ãƒ—ãƒ«åŒ–ï¼‰
            const showButton = () => {
              editButton.style.opacity = '1'
            }
            
            const hideButton = () => {
              editButton.style.opacity = '0'
            }
            
            // ãƒ›ãƒãƒ¼å‡¦ç†ï¼ˆã‚·ãƒ³ãƒ—ãƒ«åŒ–ï¼‰
            img.addEventListener('mouseenter', showButton)
            img.addEventListener('mouseleave', hideButton)
            editButton.addEventListener('mouseenter', showButton)
            editButton.addEventListener('mouseleave', hideButton)
            
            // ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«ãƒ»ãƒªã‚µã‚¤ã‚ºæ™‚ã®ä½ç½®æ›´æ–°ï¼ˆã‚·ãƒ³ãƒ—ãƒ«åŒ–ï¼‰
            const updateButtonPosition = () => {
              if (document.body.contains(img)) {
                const newRect = img.getBoundingClientRect()
                const newTop = newRect.top + 8
                const newLeft = newRect.right - 32
                editButton.style.top = `${newTop}px`
                editButton.style.left = `${newLeft}px`
              } else {
                editButton.remove()
              }
            }
            
            window.addEventListener('scroll', updateButtonPosition)
            window.addEventListener('resize', updateButtonPosition)
            
            // ã‚¯ãƒªãƒƒã‚¯ã‚¤ãƒ™ãƒ³ãƒˆ
            editButton.addEventListener('click', (e) => {
              e.preventDefault()
              e.stopPropagation()
              logger.debug('ðŸ–¼ï¸ æµ®éŠç·¨é›†ãƒœã‚¿ãƒ³ã‚¯ãƒªãƒƒã‚¯')
              
              // ç”»åƒãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’æ¤œç´¢ã—ã¦ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‹ã
              if (testComponent) {
                const imageFields = Object.keys(testComponent).filter(key => 
                  key.toLowerCase().includes('url') || 
                  key.toLowerCase().includes('image') || 
                  key.toLowerCase().includes('icon')
                )
                
                logger.debug('ðŸ” ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãƒ‡ãƒ¼ã‚¿:', testComponent)
                logger.debug('ðŸ” ç”»åƒãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ä¸€è¦§:', imageFields)
                logger.debug('ðŸ” å„ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®å€¤:', imageFields.reduce((acc: Record<string, any>, field) => {
                  acc[field] = (testComponent as any)[field]
                  return acc
                }, {} as Record<string, any>))
                
                if (imageFields.length > 0) {
                  // ã‚¯ãƒªãƒƒã‚¯ã•ã‚ŒãŸç”»åƒã®srcã¨ä¸€è‡´ã™ã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’æ¤œç´¢
                  let targetField = imageFields[0] // ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã¯æœ€åˆã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰
                  const clickedSrc = img.src
                  
                  logger.debug('ðŸ” ã‚¯ãƒªãƒƒã‚¯ã•ã‚ŒãŸç”»åƒã®src:', clickedSrc)
                  
                  // å„ç”»åƒãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®å€¤ã¨æ¯”è¼ƒã—ã¦ä¸€è‡´ã™ã‚‹ã‚‚ã®ã‚’æŽ¢ã™
                  for (const field of imageFields) {
                    const fieldValue = (testComponent as any)[field]
                    logger.debug(`ðŸ” ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ ${field} ã®å€¤ã‚’ãƒã‚§ãƒƒã‚¯:`, fieldValue)
                    if (fieldValue && typeof fieldValue === 'string') {
                      // resolveImageUrlsInHTMLã¨åŒã˜ãƒ­ã‚¸ãƒƒã‚¯ã§ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’æ¤œç´¢
                      const currentImages = [...(commonImages.value || []), ...(individualImages.value || [])]
                      
                      // ç”»åƒãƒ‡ãƒ¼ã‚¿ãŒå­˜åœ¨ã™ã‚‹å ´åˆã®ã¿æ¤œç´¢
                      let imageData = null
                      if (currentImages.length > 0) {
                        imageData = currentImages.find(img => {
                          // å®Œå…¨ä¸€è‡´ãƒã‚§ãƒƒã‚¯
                          if (img.id === fieldValue || img.url === fieldValue || img.name === fieldValue) {
                            return true
                          }
                          
                          // URLãƒ‘ã‚¹ã®ä¸€è‡´ãƒã‚§ãƒƒã‚¯
                          if (fieldValue.endsWith('/' + img.name) || img.url.endsWith(fieldValue)) {
                            return true
                          }
                          
                          // ãƒ•ã‚¡ã‚¤ãƒ«åéƒ¨åˆ†ã®ãƒžãƒƒãƒãƒ³ã‚°
                          const originalFileName = fieldValue.split('/').pop()
                          if (originalFileName === img.name) {
                            return true
                          }
                          
                          return false
                        })
                      }
                      
                      if (imageData) {
                        try {
                          const resolvedSrc = getImageSrc(imageData)
                          logger.debug(`ðŸ” ${field}ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®è§£æ±ºã•ã‚ŒãŸsrc:`, resolvedSrc)
                          // è§£æ±ºã•ã‚ŒãŸsrcã¨ã‚¯ãƒªãƒƒã‚¯ã•ã‚ŒãŸç”»åƒã®srcãŒä¸€è‡´ã™ã‚‹ã‹ç¢ºèª
                          if (resolvedSrc === clickedSrc) {
                            logger.debug(`âœ… ä¸€è‡´ç™ºè¦‹! ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰: ${field}`)
                            targetField = field
                            break
                          }
                        } catch (error) {
                          logger.debug('ðŸ” ç”»åƒè§£æ±ºã‚¨ãƒ©ãƒ¼:', error)
                        }
                      }
                      
                      // ç›´æŽ¥ä¸€è‡´ã‚‚ç¢ºèªï¼ˆãƒ•ã‚©ãƒ¼ãƒ«ãƒãƒƒã‚¯ï¼‰
                      if (fieldValue === clickedSrc) {
                        logger.debug(`âœ… ç›´æŽ¥ä¸€è‡´ç™ºè¦‹! ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰: ${field}`)
                        targetField = field
                        break
                      }
                    }
                  }
                  
                  logger.debug('ðŸ–¼ï¸ ç‰¹å®šã•ã‚ŒãŸãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰:', targetField, 'ã‚¯ãƒªãƒƒã‚¯ã•ã‚ŒãŸç”»åƒ:', clickedSrc)
                  openGlobalImageSelector(testComponent, targetField, img.src)
                } else {
                  logger.debug('ðŸ” ç”»åƒãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“')
                }
              } else {
                logger.debug('ðŸ” ãƒ†ã‚¹ãƒˆç”¨ã‚¯ãƒªãƒƒã‚¯ - ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãªã—')
              }
            })
            
            // ãƒœã‚¿ãƒ³ã‚’ãƒšãƒ¼ã‚¸ã«è¿½åŠ 
            document.body.appendChild(editButton)
            
            logger.debug('ðŸ–¼ï¸ æµ®éŠç·¨é›†ãƒœã‚¿ãƒ³è¿½åŠ å®Œäº†')
          } catch (error) {
