    if (typeof window !== 'undefined') {
      ;(window as any).testAddImageButtons = () => {
        logger.debug('ðŸ” æ‰‹å‹•ãƒ†ã‚¹ãƒˆï¼šç”»åƒãƒœã‚¿ãƒ³è¿½åŠ å®Ÿè¡Œ')
        addImageEditButtons()
