    // z-forå‡¦ç†ç”¨ã®æœ€æ–°ãƒ‡ãƒ¼ã‚¿å–å¾—é–¢æ•°ã‚’è¿½åŠ ã§å…¬é–‹
    if (typeof window !== 'undefined') {
      ;(window as any).getLatestComponentData = (componentId: string) => {
        // é€šå¸¸ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆIDã®å ´åˆ
        const component = editablePageData.value.find(c => c.id === componentId)
        if (component) {
          // console.log(`[Z-FORæ¤œè¨¼] ${componentId}ã®æœ€æ–°ãƒ‡ãƒ¼ã‚¿ã‚’æä¾›:`, Object.keys(component).filter(k => Array.isArray(component[k])))
          return component
        }
        
        // å­z-forã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆIDã®å ´åˆï¼ˆä¾‹: div_nested_zfor_001_outer_items_0ï¼‰
        // ãƒ‘ã‚¿ãƒ¼ãƒ³: åŸºåº•ID_é…åˆ—å_ã‚¤ãƒ³ãƒ‡ãƒƒã‚¯ã‚¹
        const zForPattern = /^(.+?)_([^_]+_[^_]+)_(\d+)$/
        const match = componentId.match(zForPattern)
        if (match) {
          const [, baseId, arrayName, arrayIndexStr] = match
          const arrayIndex = parseInt(arrayIndexStr, 10)
          
          const parentComponent = editablePageData.value.find(c => c.id === baseId)
          if (parentComponent && Array.isArray(parentComponent[arrayName]) && parentComponent[arrayName][arrayIndex]) {
            const arrayElement = parentComponent[arrayName][arrayIndex]
            const childData = {
              ...parentComponent,  // è¦ªã®å…¨ãƒ‡ãƒ¼ã‚¿ã‚’ç¶™æ‰¿
              ...arrayElement,     // é…åˆ—è¦ç´ ãƒ‡ãƒ¼ã‚¿ã§ä¸Šæ›¸ã
              type: parentComponent.type,
              module_name: parentComponent.module_name,
              id: componentId,
            }
            // console.log(`[Z-FORæ¤œè¨¼] å­z-for: ${componentId}ã®æœ€æ–°ãƒ‡ãƒ¼ã‚¿ã‚’æä¾›`)
            // console.log(`[Z-FORæ¤œè¨¼] - åŸºåº•ID: ${baseId}`)
            // console.log(`[Z-FORæ¤œè¨¼] - é…åˆ—å: ${arrayName}`)
            // console.log(`[Z-FORæ¤œè¨¼] - ã‚¤ãƒ³ãƒ‡ãƒƒã‚¯ã‚¹: ${arrayIndex}`)
            // console.log(`[Z-FORæ¤œè¨¼] - é…åˆ—è¦ç´ :`, JSON.stringify(arrayElement))
            // console.log(`[Z-FORæ¤œè¨¼] - å­ãƒ‡ãƒ¼ã‚¿é…åˆ—ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰:`, Object.keys(childData).filter(k => Array.isArray(childData[k])))
            return childData
          }
        }
        
        return null
      }
    }

    // ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã®æŠ˜ã‚ŠãŸãŸã¿æ©Ÿèƒ½ã‚’ã‚°ãƒ­ãƒ¼ãƒãƒ«é–¢æ•°ã¨ã—ã¦å…¬é–‹
    ;(window as any).toggleNestedCollapse = (pathString: string) => {
