    }

    // z-forä¸¦ã³æ›¿ãˆé–¢æ•°ã‚’ã‚°ãƒ­ãƒ¼ãƒãƒ«ã«å…¬é–‹
    ;(window as any).reorderZForArrayItem = (
      componentId: string,
      arrayName: string,
