      toIndex: number
    ) => {
      logger.debug('ðŸ”„ reorderZForArrayItem ã‚°ãƒ­ãƒ¼ãƒãƒ«é–¢æ•°å‘¼ã³å‡ºã—:', { componentId, arrayName, fromIndex, toIndex })
      editorEventBus.emit('reorderZForArrayItem', { componentId, arrayName, fromIndex, toIndex })
    }

    // ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã®ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—æ©Ÿèƒ½ã‚’è¨­å®š
    cleanupNestedDragAndDrop = setupNestedDragAndDrop() || null

    // startRichTextEdité–¢æ•°ã‚‚è¿½åŠ ã§å…¬é–‹ï¼ˆé‡è¤‡å®Ÿè¡Œé˜²æ­¢ç‰ˆï¼‰
    ;(window as any).startRichTextEdit = async (
      componentId: string,
      paramName: string,
      currentValue: string,
      element?: HTMLElement
    ) => {
      logger.debug('ðŸ”§ ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆç·¨é›†é–‹å§‹:', { componentId, paramName, currentValue })

      // æ—¢ã«åŒã˜è¦ç´ ã‚’ç·¨é›†ä¸­ã®å ´åˆã¯ã‚¹ã‚­ãƒƒãƒ—
      const targetElement = element || (window.event?.target as HTMLElement)
      if (currentEditingElement.value === targetElement) {
        logger.debug('ðŸ”§ æ—¢ã«ç·¨é›†ä¸­ã®è¦ç´ ã€ã‚¹ã‚­ãƒƒãƒ—')
        return
      }

      if (targetElement) {
        // å‰å›žã®ç·¨é›†ã‚’çµ‚äº†ï¼ˆãƒ­ã‚°å‡ºåŠ›ã‚’è¿½åŠ ï¼‰
        if (currentEditingElement.value) {
          logger.debug('ðŸ”§ å‰å›žã®ç·¨é›†ã‚»ãƒƒã‚·ãƒ§ãƒ³ã‚’çµ‚äº†')
          stopRichTextEdit(false)
        }

        // äº‹å‰ã«ç·¨é›†çŠ¶æ…‹ã‚’è¨­å®šï¼ˆãƒ†ã‚­ã‚¹ãƒˆé¸æŠžã‚’å¦¨å®³ã—ãªã„ã‚ˆã†ã«ï¼‰
        logger.debug('ðŸ”§ äº‹å‰è¨­å®šé–‹å§‹')

        currentEditingElement.value = targetElement
        originalContent.value = targetElement.innerHTML
        currentComponentId.value = componentId
        currentParamName.value = paramName

        // nextTickã§éžåŒæœŸã«ç·¨é›†è¨­å®šã‚’è¡Œã„ã€ãƒ†ã‚­ã‚¹ãƒˆé¸æŠžã‚’å¦¨å®³ã—ãªã„ã‚ˆã†ã«ã™ã‚‹
        await nextTick()

        // è¦ç´ ã‚’ç·¨é›†å¯èƒ½ã«ã™ã‚‹
        targetElement.contentEditable = 'true'
        logger.debug('ðŸ”§ contentEditableè¨­å®šå®Œäº†:', targetElement.contentEditable)

        // user-selectã‚’æ˜Žç¤ºçš„ã«è¨­å®š
        targetElement.style.userSelect = 'text'
        targetElement.style.webkitUserSelect = 'text'
        logger.debug('ðŸ”§ user-selectè¨­å®šå®Œäº†:', targetElement.style.userSelect)

        // CSSã‚¯ãƒ©ã‚¹ã‚’è¿½åŠ 
        targetElement.classList.add('editing')
        logger.debug('ðŸ”§ editingã‚¯ãƒ©ã‚¹è¿½åŠ å®Œäº†')

        logger.debug('ðŸ”§ ç·¨é›†ãƒ¢ãƒ¼ãƒ‰è¨­å®šå®Œäº†')

        // ãƒ•ã‚©ãƒ¼ã‚«ã‚¹ã‚¢ã‚¦ãƒˆæ™‚ã®è‡ªå‹•ä¿å­˜å‡¦ç†
        const handleBlur = (e: Event) => {
          const relatedTarget = (e as FocusEvent).relatedTarget as HTMLElement
          if (relatedTarget && relatedTarget.closest('.zero-code-rich-text-toolbar')) {
            logger.debug('ðŸ”§ ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ã¸ã®ãƒ•ã‚©ãƒ¼ã‚«ã‚¹ç§»å‹•ã€ç·¨é›†ç¶™ç¶š')
            return
          }

          logger.debug('ðŸ”§ ãƒ•ã‚©ãƒ¼ã‚«ã‚¹å–ªå¤±ã€è‡ªå‹•ä¿å­˜ã¨ãƒ„ãƒ¼ãƒ«ãƒãƒ¼éžè¡¨ç¤º')
          // å°‘ã—é…å»¶ã•ã›ã¦ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ã‚¯ãƒªãƒƒã‚¯ã¨ã®ç«¶åˆã‚’é¿ã‘ã‚‹
          setTimeout(() => {
            if (currentEditingElement.value === targetElement) {
              stopRichTextEdit(true) // è‡ªå‹•ä¿å­˜
            }
          }, 150)
        }

        targetElement.addEventListener('blur', handleBlur)
        logger.debug('ðŸ”§ ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼è¿½åŠ å®Œäº†')

        // ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—é–¢æ•°
        ;(targetElement as any)._cleanup = () => {
          targetElement.removeEventListener('blur', handleBlur)
          logger.debug('ðŸ”§ ã‚¤ãƒ™ãƒ³ãƒˆãƒªã‚¹ãƒŠãƒ¼ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—å®Œäº†')
        }

        // ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ä½ç½®è¨ˆç®—
        logger.debug('ðŸ”§ ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ä½ç½®è¨ˆç®—é–‹å§‹')
        const rect = targetElement.getBoundingClientRect()
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop
        const scrollLeft = window.pageXOffset || document.documentElement.scrollLeft

        richTextPosition.value = {
          top: rect.top + scrollTop - 80,
          left: rect.left + scrollLeft,
        }
        logger.debug('ðŸ”§ ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ä½ç½®è¨­å®šå®Œäº†:', richTextPosition.value)

        // ãƒ„ãƒ¼ãƒ«ãƒãƒ¼è¡¨ç¤º
        logger.debug('ðŸ”§ ãƒ„ãƒ¼ãƒ«ãƒãƒ¼è¡¨ç¤ºé–‹å§‹')
        showRichTextToolbar.value = true
        logger.debug('ðŸ”§ ãƒªãƒƒãƒãƒ†ã‚­ã‚¹ãƒˆç·¨é›†é–‹å§‹å®Œäº†ï¼ˆãƒ„ãƒ¼ãƒ«ãƒãƒ¼ä»˜ãï¼‰')

        // æ¬¡ã®æç”»ã‚µã‚¤ã‚¯ãƒ«ã§è¦ç´ ã«ãƒ•ã‚©ãƒ¼ã‚«ã‚¹ã‚’å½“ã¦ã€ãƒ†ã‚­ã‚¹ãƒˆé¸æŠžã‚’æœ‰åŠ¹åŒ–
        await nextTick()

        // ä¸€å›žç›®ã®ã‚¯ãƒªãƒƒã‚¯ã§ãƒ†ã‚­ã‚¹ãƒˆé¸æŠžã‚’ç¢ºå®Ÿã«å‹•ä½œã•ã›ã‚‹ãŸã‚ã€å°‘ã—é…å»¶ã—ã¦ã‹ã‚‰ãƒ•ã‚©ãƒ¼ã‚«ã‚¹
        setTimeout(() => {
          if (targetElement && currentEditingElement.value === targetElement) {
            targetElement.focus()

            // ã‚«ãƒ¼ã‚½ãƒ«ã‚’æœ«å°¾ã«ç§»å‹•
            const range = document.createRange()
            const selection = window.getSelection()

            if (selection && targetElement.childNodes.length > 0) {
              // ãƒ†ã‚­ã‚¹ãƒˆãƒŽãƒ¼ãƒ‰ã®æœ€å¾Œã«ã‚«ãƒ¼ã‚½ãƒ«ã‚’ç§»å‹•
              const lastNode = targetElement.childNodes[targetElement.childNodes.length - 1]
              if (lastNode.nodeType === Node.TEXT_NODE) {
                range.setStart(lastNode, lastNode.textContent?.length || 0)
              } else {
                range.setStartAfter(lastNode)
              }
              range.collapse(true)

              selection.removeAllRanges()
              selection.addRange(range)

              logger.debug('ðŸ”§ ã‚«ãƒ¼ã‚½ãƒ«ã‚’æœ«å°¾ã«ç§»å‹•å®Œäº†')
            } else if (selection) {
              // ãƒ†ã‚­ã‚¹ãƒˆãŒç©ºã®å ´åˆã¯è¦ç´ ã®æœ«å°¾ã«ã‚«ãƒ¼ã‚½ãƒ«ã‚’è¨­å®š
              range.selectNodeContents(targetElement)
              range.collapse(false)
              selection.removeAllRanges()
              selection.addRange(range)

              logger.debug('ðŸ”§ ç©ºè¦ç´ ã®æœ«å°¾ã«ã‚«ãƒ¼ã‚½ãƒ«è¨­å®šå®Œäº†')
            }

            logger.debug('ðŸ”§ é…å»¶ãƒ•ã‚©ãƒ¼ã‚«ã‚¹å®Œäº†ã€ãƒ†ã‚­ã‚¹ãƒˆé¸æŠžæº–å‚™å®Œäº†ï¼ˆæœ«å°¾ã‹ã‚‰é–‹å§‹ï¼‰')
          }
        }, 10)
      }

      // Tiptapå‡¦ç†ã¯ä¸€æ™‚çš„ã«ç„¡åŠ¹åŒ–ï¼ˆæ®µéšŽçš„å¾©æ—§ï¼‰
      // editor.actions.startTiptapEdit(componentId, paramName, [], 0)
    }

    // ãƒ‡ãƒãƒƒã‚°ç”¨: localPartsã®å†…å®¹ã‚’ç¢ºèª
    logger.debug('localParts.value:', localParts.value)
    logger.debug('props.parts:', props.parts)

    // ã‚°ãƒ­ãƒ¼ãƒãƒ«é–¢æ•°ã®ç¢ºèª
    logger.debug('ðŸ”§ ã‚°ãƒ­ãƒ¼ãƒãƒ«é–¢æ•°ç¢ºèª:')
    logger.debug('  openNestedPartSelector:', typeof window.openNestedPartSelector)
    logger.debug('  openNestedEditor:', typeof window.openNestedEditor)

    logger.debug('âœ… ZeroCodeåˆæœŸåŒ–å®Œäº†')
  })

  // Webã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã¨ã—ã¦ä½¿ç”¨ã™ã‚‹å ´åˆã®ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã‚’å…¬é–‹
  defineExpose({
    parts: computed(() => localParts.value && localParts.value.length > 0 ? localParts.value : props.parts || []),
    page: computed(() => editablePageData.value || [])
  })

  // ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—å‡¦ç†
  onUnmounted(() => {
    logger.debug('ðŸ”§ ZeroCodeç ´æ£„å‡¦ç†é–‹å§‹')
    document.removeEventListener('click', handleComponentClick)
    if (imageClickHandler) {
      document.removeEventListener('click', imageClickHandler)
    }
    
    // æµ®éŠç”»åƒç·¨é›†ãƒœã‚¿ãƒ³ã®ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—
    const componentElement = document.querySelector(`.${componentInstanceId.value}`)
    const floatingButtons = componentElement ? componentElement.querySelectorAll('.zero-code-image-floating-button') : []
    floatingButtons.forEach(button => button.remove())
    
    if (cleanupNestedDragAndDrop) {
      cleanupNestedDragAndDrop()
    }
    
    // EventBusãƒªã‚¹ãƒŠãƒ¼ã®ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—
    editorEventBus.off('reorderZForArrayItem')
    
    logger.debug('ðŸ”§ ZeroCodeç ´æ£„å‡¦ç†å®Œäº†')
  })
</script>

