<template>
  <div class="zero-code-data-display">
    <div class="zero-code-component-header">
      <h4>ãƒ‘ãƒ¼ãƒ„ç®¡ç†</h4>
    </div>

    <!-- æ¤œç´¢ã¨ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼ -->
    <div class="zero-code-data-tabs">
      <div class="zero-code-form-group">
        <input
          v-model="searchQuery"
          placeholder="ãƒ‘ãƒ¼ãƒ„åã§æ¤œç´¢..."
          class="zero-code-form-input"
          @input="handleSearch"
        >
      </div>
      <div class="zero-code-form-group">
        <select v-model="selectedType" class="zero-code-form-select" @change="handleFilter">
          <option value="">ã™ã¹ã¦ã®Type</option>
          <option v-for="type in existingTypes" :key="type" :value="type">
            {{ type }}
          </option>
        </select>
      </div>
    </div>

    <!-- ãƒ‘ãƒ¼ãƒ„ä¸€è¦§ -->
    <div class="zero-code-data-content">
      <div v-if="Array.isArray(filteredParts) && filteredParts.length > 0" class="zero-code-rendered-content">
        <div
          v-for="(section, sectionIndex) in filteredParts"
          :key="sectionIndex"
          class="zero-code-data-section"
        >
          <div class="zero-code-component-header">
            <div class="zero-code-component-info-header">
              <span class="zero-code-component-type">{{ section.type }}</span>
            </div>
            <span class="zero-code-component-id">{{ section.module.length }}å€‹ã®ãƒ‘ãƒ¼ãƒ„</span>
          </div>
          <div class="zero-code-rendered-component">
            <div
              v-for="(module, moduleIndex) in section.module"
              :key="moduleIndex"
              class="zero-code-draggable-item"
            >
              <div class="zero-code-component-header">
                <div class="zero-code-component-info-header">
                  <span class="zero-code-component-module">{{ module.title }}</span>
                </div>
                <div class="zero-code-header-actions">
                  <button
                    class="zero-code-edit-btn"
                    @click="$emit('edit-part', { type: section.type, module })"
                  >
                    ç·¨é›†
                  </button>
                  <button
                    class="zero-code-delete-btn"
                    @click="$emit('delete-part', { type: section.type, title: module.title })"
                  >
                    å‰Šé™¤
                  </button>
                </div>
              </div>
              <!-- eslint-disable-next-line vue/no-v-html -->
              <div class="zero-code-module-preview" v-html="props.renderPartPreview(module.body)" />
            </div>
          </div>
        </div>
      </div>
      <div v-else class="zero-code-no-parts">
        <p>ç™»éŒ²ã•ã‚ŒãŸãƒ‘ãƒ¼ãƒ„ãŒã‚ã‚Šã¾ã›ã‚“ã€‚</p>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'
  import type { PartSection } from '@types'

  interface Props {
    parts: PartSection[]
    renderPartPreview: (body: string) => string
  }

  const props = defineProps<Props>()

  const emit = defineEmits<{
    (e: 'edit-part', part: { type: string; module: { title: string; body: string } }): void
    (e: 'delete-part', part: { type: string; title: string }): void
    (e: 'search', query: string): void
    (e: 'filter', type: string): void
  }>()

  // æ¤œç´¢ã¨ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼
  const searchQuery = ref('')
  const selectedType = ref('')

  // æ—¢å­˜ã®ã‚¿ã‚¤ãƒ—ä¸€è¦§ã‚’å–å¾—
  const existingTypes = computed(() => {
    return Array.from(new Set(props.parts.map(section => section.type)))
  })

  // ãƒ•ã‚£ãƒ«ã‚¿ãƒªãƒ³ã‚°ã•ã‚ŒãŸãƒ‘ãƒ¼ãƒ„ä¸€è¦§
  const filteredParts = computed(() => {
    let filtered = [...props.parts]

    // ã‚¿ã‚¤ãƒ—ã§ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼
    if (selectedType.value) {
      filtered = filtered.filter(section => section.type === selectedType.value)
    }

    // æ¤œç´¢ã‚¯ã‚¨ãƒªã§ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼
    if (searchQuery.value) {
      const query = searchQuery.value.toLowerCase()
      filtered = filtered
        .map(section => ({
          ...section,
          module: section.module.filter(
            m => m.title.toLowerCase().includes(query) || m.body.toLowerCase().includes(query)
          ),
        }))
        .filter(section => section.module.length > 0)
    }

    return filtered
  })

  // æ¤œç´¢ãƒãƒ³ãƒ‰ãƒ©ãƒ¼
  const handleSearch = (): void => {
    emit('search', searchQuery.value)
  }

  // ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼ãƒãƒ³ãƒ‰ãƒ©ãƒ¼
  const handleFilter = (): void => {
    emit('filter', selectedType.value)
  }
</script>
