/**
 * ç”»åƒè¡¨ç¤ºç”¨ã®ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
 * Base64ãƒ‡ãƒ¼ã‚¿ãŒã‚ã‚Œã°å„ªå…ˆçš„ã«è¡¨ç¤ºã—ã€ãªã‘ã‚Œã°URLã‚’ä½¿ç”¨
 */

import { computed } from 'vue'
import type { ImageData } from '@types'

export function useImageDisplay() {
  /**
   * ç”»åƒã®ã‚½ãƒ¼ã‚¹URLã‚’å–å¾—
   * Base64ãƒ‡ãƒ¼ã‚¿ãŒã‚ã‚Œã°å„ªå…ˆçš„ã«ä½¿ç”¨ã€ãªã‘ã‚Œã°URLã‚’ä½¿ç”¨
   */
  const getImageSrc = (image: ImageData | any): string => {
    // Base64ãƒ‡ãƒ¼ã‚¿ãŒã‚ã‚‹å ´åˆã¯å„ªå…ˆçš„ã«ä½¿ç”¨
    if (image.fileData?.base64 && image.fileData.base64.trim() !== '') {
      const mimeType = image.fileData.type || image.type || 'image/jpeg'
      return `data:${mimeType};base64,${image.fileData.base64}`
    }
    
    // Base64ãŒãªã„å ´åˆã¯URLã‚’ä½¿ç”¨
    return image.url || ''
  }

  /**
   * ç”»åƒãŒæ›´æ–°ã•ã‚Œã¦ã„ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
   */
  const isImageUpdated = (image: ImageData | any): boolean => {
    return !!(image.fileData?.base64 && image.fileData.base64.trim() !== '')
  }

  /**
   * ç”»åƒã®altå±žæ€§ã‚’å–å¾—
   */
  const getImageAlt = (image: ImageData | any): string => {
    return image.alt || image.name || 'ç”»åƒ'
  }

  /**
   * ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’æ›´æ–°ï¼ˆå·®ã—æ›¿ãˆç”¨ï¼‰
   */
  const updateImageData = (
    originalImage: ImageData | any,
    newFile: File,
    newBase64: string
  ): ImageData | any => {
    return {
      ...originalImage,
      fileData: {
        ...originalImage.fileData,
        base64: newBase64,
        type: newFile.type,
        size: newFile.size,
      },
      // å¿…è¦ã«å¿œã˜ã¦ä»–ã®ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã‚‚æ›´æ–°
      type: newFile.type,
      size: newFile.size,
      uploadedAt: new Date().toISOString(),
    }
  }

  return {
    getImageSrc,
    isImageUpdated,
    getImageAlt,
    updateImageData,
  }
}

/**
 * ãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãªç”»åƒã‚½ãƒ¼ã‚¹è¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£
 */
export function useReactiveImageSrc(image: any) {
  const { getImageSrc, getImageAlt } = useImageDisplay()
  
  const imageSrc = computed(() => getImageSrc(image.value || image))
  const imageAlt = computed(() => getImageAlt(image.value || image))
  
  return {
    imageSrc,
    imageAlt,
  }
}