<template>
  <div class="zero-code-data-display">
    <!-- ãƒ‡ãƒ¼ã‚¿è¡¨ç¤ºã‚¿ãƒ– -->
    <div class="zero-code-data-tabs">
      <div class="zero-code-tab-buttons">
        <button
          v-for="tab in dataTabs"
          :key="tab"
          :class="['zero-code-data-tab-button', { active: modelValue === tab }]"
          @click="$emit('update:modelValue', tab)"
        >
          {{ getTabLabel(tab) }}
        </button>
      </div>
      
      <!-- ä¸€æ‹¬æ“ä½œãƒœã‚¿ãƒ³ï¼ˆãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°çµæžœã‚¿ãƒ–ã®ã¿è¡¨ç¤ºï¼‰ -->
      <div v-if="modelValue === 'rendered'" class="zero-code-collapse-controls">
        <span class="zero-code-collapse-stats">
          {{ collapsedCount }}/{{ pageData.length }} æŠ˜ã‚ŠãŸãŸã¿ä¸­
        </span>
        <button 
          class="zero-code-collapse-all-btn"
          :disabled="pageData.length === 0"
          @click="handleCollapseAll"
        >
          ã™ã¹ã¦æŠ˜ã‚ŠãŸãŸã¿
        </button>
        <button 
          class="zero-code-expand-all-btn"
          :disabled="collapsedCount === 0"
          @click="expandAll"
        >
          ã™ã¹ã¦å±•é–‹
        </button>
      </div>
    </div>

    <div class="zero-code-data-content">
      <!-- ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°çµæžœã‚¿ãƒ– -->
      <div v-if="modelValue === 'rendered'" class="zero-code-data-section">
        <div v-if="Array.isArray(pageData) && pageData.length > 0" class="zero-code-rendered-content">
          <!-- æœ€åˆã®ãƒ‘ãƒ¼ãƒ„ã®å‰ã«è¿½åŠ ãƒœã‚¿ãƒ³ -->
          <div class="zero-code-add-part-button-container">
            <button class="zero-code-add-part-button" @click="$emit('add-part', 0)">
              <span class="zero-code-plus-icon">ï¼‹</span>
              <span class="zero-code-button-text">ãƒ‘ãƒ¼ãƒ„ã‚’è¿½åŠ </span>
            </button>
          </div>

          <!-- ãƒ‰ãƒ©ãƒƒã‚°å¯èƒ½ãªã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãƒªã‚¹ãƒˆ -->
          <VueDraggable
            v-model="draggablePageData"
            :animation="200"
            :disabled="isTextEditing"
            ghost-class="ghost"
            chosen-class="chosen"
            drag-class="drag"
            @start="onDragStart"
            @end="onDragEnd"
          >
            <div
              v-for="(component, index) in pageData"
              :key="`${component.id || index}`"
              class="zero-code-rendered-component zero-code-draggable-item"
            >
              <div class="zero-code-component-header">
                <div class="zero-code-component-meta">
                  <div class="zero-code-drag-handle">
                    <span class="zero-code-drag-icon">â ¿</span>
                  </div>
                  <span class="zero-code-component-type">{{ component.type || 'unknown' }}</span>
                  <span class="zero-code-component-module">{{ component.module_name || '' }}</span>
                </div>
                <div class="zero-code-header-actions">
                  <button 
                    class="zero-code-collapse-btn"
                    :title="isCollapsed(component.id || `component-${index}`) ? 'å±•é–‹' : 'æŠ˜ã‚ŠãŸãŸã¿'"
                    @click="toggleCollapse(component.id || `component-${index}`)"
                  >
                    {{ isCollapsed(component.id || `component-${index}`) ? 'ï¼‹' : 'ï¼' }}
                  </button>
                  <button class="zero-code-edit-btn" @click="$emit('edit-part', index)">ç·¨é›†</button>
                  <button class="zero-code-delete-btn" @click="$emit('delete-part', index)">å‰Šé™¤</button>
                </div>
              </div>
              <!-- ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼ã‚¨ãƒªã‚¢ï¼ˆæŠ˜ã‚ŠãŸãŸã¿ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ä»˜ãï¼‰ -->
              <Transition name="collapse">
                <div 
                  v-show="!isCollapsed(component.id || `component-${index}`)"
                  :key="`preview-${component.id}`"
                  class="zero-code-component-preview" 
                  v-html="renderComponentWithNested(component, index)" 
                />
              </Transition>

              <!-- å„ãƒ‘ãƒ¼ãƒ„ã®å¾Œã«è¿½åŠ ãƒœã‚¿ãƒ³ -->
              <div class="zero-code-add-part-button-container">
                <button class="zero-code-add-part-button" @click="$emit('add-part', index + 1)">
                  <span class="zero-code-plus-icon">ï¼‹</span>
                  <span class="zero-code-button-text">ãƒ‘ãƒ¼ãƒ„ã‚’è¿½åŠ </span>
                </button>
              </div>
            </div>
          </VueDraggable>
        </div>
        <div v-else class="zero-code-no-data">
          <div class="zero-code-empty-state">
            <p class="zero-code-empty-message">ãƒ‘ãƒ¼ãƒ„ãŒã‚ã‚Šã¾ã›ã‚“</p>
            <div class="zero-code-add-part-button-container">
              <button class="zero-code-add-part-button" @click="$emit('add-part', 0)">
                <span class="zero-code-plus-icon">ï¼‹</span>
                <span class="zero-code-button-text">æœ€åˆã®ãƒ‘ãƒ¼ãƒ„ã‚’è¿½åŠ </span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- ãƒ‡ãƒ¼ã‚¿è¡¨ç¤ºã‚¿ãƒ– -->
      <div v-if="modelValue === 'page'" class="zero-code-data-section">
        <div class="zero-code-data-display-section">
          <div class="zero-code-parts-sub-content">
            <div class="zero-code-parts-sub-section">
              <h4 style="margin: 0 0 16px 0; color: #374151; font-size: 1.1rem;">Pageãƒ‡ãƒ¼ã‚¿ï¼ˆãƒªã‚¢ãƒ«ã‚¿ã‚¤ãƒ æ›´æ–°ï¼‰</h4>
              <pre class="zero-code-data-display-pre">{{ formattedPageData }}</pre>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { computed } from 'vue'
  import { logger } from '@shared/utils/logger'
  import { VueDraggable } from 'vue-draggable-plus'
  import { useCollapsibleItems } from '@/composables/useCollapsibleItems'
  import type { Component, PartSection, DataTabType } from '@types'

  // Propså®šç¾©
  interface Props {
    modelValue: DataTabType
    pageData: Component[]
    parts: PartSection[]
    commonParts?: PartSection[]
    individualParts?: PartSection[]
    commonImages?: any[]
    individualImages?: any[]
    renderComponentWithNested: (component: Component, index?: number) => string
    isTextEditing?: boolean
  }

  const props = defineProps<Props>()

  // Emitså®šç¾©
  const _emit = defineEmits<{
    'update:modelValue': [value: DataTabType]
    'add-part': [index: number]
    'edit-part': [index: number]
    'delete-part': [index: number]
    'reorder-parts': [newOrder: Component[]]
  }>()

  // ãƒ‰ãƒ©ãƒƒã‚°å¯èƒ½ãªãƒ‡ãƒ¼ã‚¿
  const draggablePageData = computed({
    get: () => props.pageData,
    set: (newOrder: Component[]) => {
      _emit('reorder-parts', newOrder)
    },
  })

  // ãƒ‰ãƒ©ãƒƒã‚°ã‚¤ãƒ™ãƒ³ãƒˆãƒãƒ³ãƒ‰ãƒ©
  const onDragStart = () => {
    logger.debug('ãƒ‰ãƒ©ãƒƒã‚°é–‹å§‹')
  }

  const onDragEnd = () => {
    logger.debug('ãƒ‰ãƒ©ãƒƒã‚°çµ‚äº†')
  }

  // æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã®ç®¡ç†ï¼ˆã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«ä½¿ç”¨ï¼‰
  const { isCollapsed, toggleCollapse, collapsedCount, expandAll, collapseAll } = useCollapsibleItems()

  // ã™ã¹ã¦ã®ãƒ‘ãƒ¼ãƒ„ã‚’æŠ˜ã‚ŠãŸãŸã‚€
  const handleCollapseAll = (): void => {
    const componentIds = props.pageData.map((component, index) => 
      component.id || `component-${index}`
    )
    collapseAll(componentIds)
  }

  // ãƒ‡ãƒ¼ã‚¿ã‚¿ãƒ–ã®ä¸€è¦§
  const dataTabs = ['rendered', 'page'] as const

  // ã‚¿ãƒ–ã®ãƒ©ãƒ™ãƒ«ã‚’å–å¾—
  const getTabLabel = (tab: DataTabType): string => {
    const labels: Record<DataTabType, string> = {
      rendered: 'ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°çµæžœ',
      source: 'HTMLã‚½ãƒ¼ã‚¹',
      page: 'ãƒ‡ãƒ¼ã‚¿è¡¨ç¤º',
    }
    return labels[tab]
  }

  // ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆæ¸ˆã¿ãƒ‡ãƒ¼ã‚¿
  const formattedPageData = computed(() => {
    return formatToReadableJson(props.pageData)
  })

  // èª­ã¿ã‚„ã™ã„JSONå½¢å¼ã«å¤‰æ›
  const formatToReadableJson = (data: unknown): string => {
    const formatValue = (obj: unknown, depth: number = 0): string => {
      const indent = '  '.repeat(depth)
      const nextIndent = '  '.repeat(depth + 1)

      if (Array.isArray(obj)) {
        if (obj.length === 0) return '[]'
        const items = obj.map(item => nextIndent + formatValue(item, depth + 1)).join(',\n')
        return `[\n${items}\n${indent}]`
      }

      if (typeof obj === 'object' && obj !== null) {
        const entries = Object.entries(obj as Record<string, unknown>)
        if (entries.length === 0) return '{}'

        const items = entries.map(([key, value]) => {
          let formattedValue: string

          if (typeof value === 'string') {
            // æ–‡å­—åˆ—ã®å ´åˆã¯ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—æ–‡å­—ã‚’å®Ÿéš›ã®æ–‡å­—ã«å¤‰æ›ã—ã¦ã‹ã‚‰è¡¨ç¤º
            const cleanString = value
              .replace(/\\n/g, '\n')
              .replace(/\\t/g, '\t')
              .replace(/\\"/g, '"')
              .replace(/\\'/g, "'")
              .replace(/\\\\/g, '\\')

            // æ”¹è¡ŒãŒå«ã¾ã‚Œã‚‹å ´åˆã¯ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆãƒªãƒ†ãƒ©ãƒ«é¢¨ã«è¡¨ç¤º
            if (cleanString.includes('\n')) {
              const lines = cleanString.split('\n')
              const formattedLines = lines.map((line, index) => {
                if (index === 0) return `\`${line}`
                if (index === lines.length - 1) return `${nextIndent}${line}\``
                return `${nextIndent}${line}`
              }).join('\n')
              formattedValue = formattedLines
            } else {
              formattedValue = `"${cleanString}"`
            }
          } else {
            formattedValue = formatValue(value, depth + 1)
          }

          return `${nextIndent}"${key}": ${formattedValue}`
        }).join(',\n')

        return `{\n${items}\n${indent}}`
      }

      if (typeof obj === 'string') {
        return `"${obj.replace(/"/g, '\\"')}"`
      }

      return JSON.stringify(obj)
    }

    return formatValue(data)
  }
</script>
