<template>
  <!-- Teleportã‚’ä½¿ã‚ãšã«ç›´æŽ¥ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚° -->
  <div
    v-if="show"
    class="zero-code-rich-text-toolbar"
    :style="{
      top: position.top + 'px',
      left: position.left + 'px',
    }"
  >
    <div class="zero-code-toolbar-content">
      <!-- æ›¸å¼è¨­å®šãƒœã‚¿ãƒ³ -->
      <div class="zero-code-toolbar-section">
        <template v-for="(action, index) in actions" :key="index">
          <div
            v-if="action && action.name === 'separator'"
            class="zero-code-toolbar-separator"
          />
          <button
            v-else-if="action"
            :class="['zero-code-toolbar-btn', { active: action.active, disabled: !isEditing }]"
            :title="action.title || ''"
            :disabled="!isEditing"
            tabindex="-1"
            @click="executeCommand(action.command || '')"
            @mousedown.prevent.stop
          >
            {{ action.icon || '' }}
          </button>
        </template>
      </div>

      <!-- åŒºåˆ‡ã‚Šç·š -->
      <div class="zero-code-toolbar-separator" />

      <!-- ä¿å­˜ãƒ»ã‚­ãƒ£ãƒ³ã‚»ãƒ«ãƒœã‚¿ãƒ³ -->
      <div class="zero-code-toolbar-section">
        <button
          class="zero-code-toolbar-btn zero-code-save-btn"
          title="ä¿å­˜ (Ctrl+S)"
          tabindex="-1"
          @click="handleSave"
          @mousedown.prevent.stop
        >
          âœ“
        </button>
        <button
          class="zero-code-toolbar-btn zero-code-cancel-btn"
          title="ã‚­ãƒ£ãƒ³ã‚»ãƒ« (Esc)"
          tabindex="-1"
          @click="handleCancel"
          @mousedown.prevent.stop
        >
          âœ•
        </button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { watch, onMounted } from 'vue'
  import { logger } from '@shared/utils/logger'

  // Props
  interface Props {
    show: boolean
    position: { top: number; left: number }
    actions: any[]
    editingState: any
    isEditing: boolean
  }

  const props = defineProps<Props>()

  // ãƒ‡ãƒãƒƒã‚°ç”¨
  onMounted(() => {
    logger.debug('ðŸ”§ RichTextToolbar mounted')
  })

  // propsã®å¤‰åŒ–ã‚’ç›£è¦–
  watch(
    () => props.show,
    newShow => {
      logger.debug('ðŸ”§ RichTextToolbar show changed:', newShow)
    },
    { immediate: true }
  )

  watch(
    () => props.position,
    newPosition => {
      logger.debug('ðŸ”§ RichTextToolbar position changed:', newPosition)
    },
    { deep: true, immediate: true }
  )

  watch(
    () => props.actions,
    newActions => {
      logger.debug('ðŸ”§ RichTextToolbar actions changed:', newActions?.length || 0)
    },
    { immediate: true }
  )

  // Emits
  const emit = defineEmits<{
    executeCommand: [command: string]
    save: []
    cancel: []
  }>()

  // ã‚³ãƒžãƒ³ãƒ‰å®Ÿè¡Œ
  const executeCommand = (command: string) => {
    logger.debug('ðŸ”§ ãƒ„ãƒ¼ãƒ«ãƒãƒ¼ã‚³ãƒžãƒ³ãƒ‰å®Ÿè¡Œ:', command)
    emit('executeCommand', command)
  }

  // ä¿å­˜
  const handleSave = () => {
    emit('save')
  }

  // ã‚­ãƒ£ãƒ³ã‚»ãƒ«
  const handleCancel = () => {
    emit('cancel')
  }
</script>

