<template>
  <div class="zero-code-tab-content">
    <!-- ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼ã‚¿ãƒ– -->
    <div class="zero-code-data-tabs">
      <div class="zero-code-tab-buttons">
        <button
          v-for="tab in previewTabs"
          :key="tab"
          :class="['zero-code-data-tab-button', { active: activePreviewTab === tab }]"
          @click="activePreviewTab = tab"
        >
          {{ getPreviewTabLabel(tab) }}
        </button>
      </div>
    </div>

    <div class="zero-code-preview-content">
      <!-- ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼è¡¨ç¤ºã‚¿ãƒ– -->
      <div v-if="activePreviewTab === 'preview'" class="zero-code-preview-section">
        <PagePreview
          :key="previewKey"
          :page-data="editablePageData"
          :parts="localParts && localParts.length > 0 ? localParts : parts || []"
          :common-images="commonImages"
          :individual-images="individualImages"
        />
      </div>

      <!-- HTMLã‚½ãƒ¼ã‚¹ã‚¿ãƒ– -->
      <div v-if="activePreviewTab === 'source'" class="zero-code-preview-section">
        <div class="zero-code-html-source">
          <div class="zero-code-source-header">
            <p class="zero-code-source-description">ãƒ‘ãƒ¼ãƒ„ã”ã¨ã«æ”¹è¡Œãƒ»ã‚¤ãƒ³ãƒ‡ãƒ³ãƒˆã•ã‚ŒãŸHTMLã‚½ãƒ¼ã‚¹</p>
          </div>
          <pre class="zero-code-html-source-pre"><code>{{ formatHtmlSource(renderedPageHtml) }}</code></pre>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import PagePreview from '@/components/preview/PagePreview.vue'
  import type { Component, PartSection, Part } from '@types'
  import { watch, ref, computed } from 'vue'
  import { useTemplateProcessor } from '../../composables/useTemplateProcessor'

  type PreviewTab = 'preview' | 'source'

  // Propså®šç¾©
  interface Props {
    editablePageData: Component[]
    localParts: PartSection[]
    parts: PartSection[]
    commonImages: any[]
    individualImages: any[]
  }
  
  const props = defineProps<Props>()
  
  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå‡¦ç†ç”¨ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
  const templateProcessor = useTemplateProcessor()
  
  // ãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãƒ‡ãƒ¼ã‚¿
  const activePreviewTab = ref<PreviewTab>('preview')
  const previewKey = ref(0)

  // ã‚¿ãƒ–ã®ä¸€è¦§
  const previewTabs: PreviewTab[] = ['preview', 'source']

  // ã‚¿ãƒ–ã®ãƒ©ãƒ™ãƒ«ã‚’å–å¾—
  const getPreviewTabLabel = (tab: PreviewTab): string => {
    const labels: Record<PreviewTab, string> = {
      preview: 'ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼è¡¨ç¤º',
      source: 'HTMLã‚½ãƒ¼ã‚¹',
    }
    return labels[tab]
  }

  // èª­ã¿ã‚„ã™ã„JSONå½¢å¼ã«å¤‰æ›ï¼ˆPartsManagementPanelã¨åŒã˜é–¢æ•°ï¼‰
  const formatToReadableJson = (data: unknown): string => {
    const formatValue = (obj: unknown, depth: number = 0): string => {
      const indent = '  '.repeat(depth)
      const nextIndent = '  '.repeat(depth + 1)

      if (Array.isArray(obj)) {
        if (obj.length === 0) return '[]'
        const items = obj.map(item => nextIndent + formatValue(item, depth + 1)).join(',\n')
        return `[\n${items}\n${indent}]`
      }

      if (typeof obj === 'object' && obj !== null) {
        const entries = Object.entries(obj as Record<string, unknown>)
        if (entries.length === 0) return '{}'
        
        const items = entries.map(([key, value]) => {
          let formattedValue: string
          
          if (typeof value === 'string') {
            // æ–‡å­—åˆ—ã®å ´åˆã¯ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—æ–‡å­—ã‚’å®Ÿéš›ã®æ–‡å­—ã«å¤‰æ›ã—ã¦ã‹ã‚‰è¡¨ç¤º
            const cleanString = value
              .replace(/\\n/g, '\n')
              .replace(/\\t/g, '\t')
              .replace(/\\"/g, '"')
              .replace(/\\'/g, "'")
              .replace(/\\\\/g, '\\')
            
            // æ”¹è¡ŒãŒå«ã¾ã‚Œã‚‹å ´åˆã¯ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆãƒªãƒ†ãƒ©ãƒ«é¢¨ã«è¡¨ç¤º
            if (cleanString.includes('\n')) {
              const lines = cleanString.split('\n')
              const formattedLines = lines.map((line, index) => {
                if (index === 0) return `\`${line}`
                if (index === lines.length - 1) return `${nextIndent}${line}\``
                return `${nextIndent}${line}`
              }).join('\n')
              formattedValue = formattedLines
            } else {
              formattedValue = `"${cleanString}"`
            }
          } else {
            formattedValue = formatValue(value, depth + 1)
          }
          
          return `${nextIndent}"${key}": ${formattedValue}`
        }).join(',\n')
        
        return `{\n${items}\n${indent}}`
      }

      if (typeof obj === 'string') {
        return `"${obj.replace(/"/g, '\\"')}"`
      }

      return JSON.stringify(obj)
    }

    return formatValue(data)
  }

  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’å–å¾—ã™ã‚‹é–¢æ•°
  const getTemplate = (type: string, moduleName: string): string => {
    const parts = props.localParts && props.localParts.length > 0 ? props.localParts : props.parts || []
    
    if (!Array.isArray(parts)) return ''

    const section = parts.find((s: PartSection) => s.type === type)
    if (!section) return ''

    const module = section.module.find((m: Part) => m.title === moduleName)
    return module ? module.body : ''
  }

  // ã‚¯ãƒªãƒ¼ãƒ³ãªã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ï¼ˆç·¨é›†UIè¦ç´ ãªã—ï¼‰
  const renderCleanComponent = (component: Component): string => {
    // ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼ãƒ¢ãƒ¼ãƒ‰ã‚’ä½¿ç”¨ï¼ˆç·¨é›†UIè¦ç´ ãªã—ã®ã‚¯ãƒªãƒ¼ãƒ³ãªãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ï¼‰
    return templateProcessor.renderComponentOptimized(component, getTemplate, { isPreviewMode: true })
  }

  // ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼å°‚ç”¨ï¼šç·¨é›†UIã‚’å«ã¾ãªã„ã‚¯ãƒªãƒ¼ãƒ³ãªHTMLã‚’ç”Ÿæˆï¼ˆè¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ï¼‰
  const renderedPageHtml = computed((): string => {
    if (!Array.isArray(props.editablePageData) || props.editablePageData.length === 0) {
      return '<div class="zero-code-no-content">ãƒšãƒ¼ã‚¸ã«ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ãŒã‚ã‚Šã¾ã›ã‚“</div>'
    }

    let pageHtml = '<div class="zero-code-page-container">'

    props.editablePageData.forEach((pageComponent) => {
      const componentHtml = renderCleanComponent(pageComponent)

      if (!componentHtml || componentHtml.trim() === '') {
        pageHtml += `<div class="zero-code-error-component">ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“: ${pageComponent.type}/${pageComponent.module_name}</div>`
      } else {
        pageHtml += componentHtml
      }
    })

    pageHtml += '</div>'
    
    return pageHtml
  })

  // HTMLã‚’ç¾Žã—ããƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆã™ã‚‹é–¢æ•°
  const formatHtmlSource = (html: string): string => {
    if (!html) return ''

    try {
      // DOMParserã§HTMLã‚’æ­£ç¢ºã«è§£æž
      const parser = new (globalThis as any).DOMParser()
      const doc = parser.parseFromString(`<root>${html}</root>`, 'text/html')
      const rootElement = doc.body.firstChild as Element

      if (!rootElement) return html

      // è‡ªå·±é–‰ã˜ã‚¿ã‚°ä¸€è¦§
      const voidElements = new Set([
        'img', 'br', 'hr', 'input', 'meta', 'link', 'area', 'base', 'col', 'embed', 'source', 'track', 'wbr',
      ])

      // ã‚¤ãƒ³ãƒ©ã‚¤ãƒ³è¦ç´ ä¸€è¦§ï¼ˆæ”¹è¡Œã‚’å…¥ã‚Œãªã„è¦ç´ ï¼‰
      const inlineElements = new Set([
        'span', 'a', 'strong', 'em', 'u', 'mark', 'code', 'small', 'sub', 'sup', 'i', 'b',
      ])

      // å†å¸°çš„ã«HTMLã‚’ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
      const formatElement = (element: Element, depth: number = 0, parentIsInline: boolean = false): string => {
        const tagName = element.tagName.toLowerCase()
        const indent = parentIsInline ? '' : '  '.repeat(depth)
        const isVoidElement = voidElements.has(tagName)
        const isInlineElement = inlineElements.has(tagName)

        // é–‹å§‹ã‚¿ã‚°ã‚’ä½œæˆ
        let startTag = `<${tagName}`

        // å±žæ€§ã‚’è¿½åŠ 
        for (let i = 0; i < element.attributes.length; i++) {
          const attr = element.attributes[i]
          startTag += ` ${attr.name}="${attr.value}"`
        }
        startTag += '>'

        let result = indent + startTag

        // è‡ªå·±é–‰ã˜ã‚¿ã‚°ã®å ´åˆã¯çµ‚äº†
        if (isVoidElement) {
          return result
        }

        // å­è¦ç´ ã‚’å‡¦ç†
        let hasElementChildren = false
        let hasTextContent = false
        const childrenResults: string[] = []
        let textContent = ''

        for (let i = 0; i < element.childNodes.length; i++) {
          const child = element.childNodes[i]

          if (child.nodeType === 1) {
            // Element node
            hasElementChildren = true
            const childElement = child as Element
            const childResult = formatElement(childElement, depth + 1, isInlineElement || parentIsInline)
            childrenResults.push(childResult)
          } else if (child.nodeType === 3) {
            // Text node
            const text = child.textContent?.replace(/\s+/g, ' ').trim()
            if (text) {
              hasTextContent = true
              textContent += text
            }
          }
        }

        // å†…å®¹ã®å‡¦ç†
        if (hasElementChildren && hasTextContent) {
          // ãƒ†ã‚­ã‚¹ãƒˆã¨è¦ç´ ã®ä¸¡æ–¹ãŒã‚ã‚‹å ´åˆï¼ˆã‚¤ãƒ³ãƒ©ã‚¤ãƒ³ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ï¼‰
          if (isInlineElement || parentIsInline) {
            result += textContent + childrenResults.join('')
          } else {
            result += textContent + '\n' + childrenResults.join('\n')
            if (childrenResults.length > 0) result += '\n' + indent
          }
        } else if (hasElementChildren) {
          // è¦ç´ ã®ã¿ã®å ´åˆ
          if (isInlineElement || parentIsInline) {
            result += childrenResults.join('')
          } else {
            result += '\n' + childrenResults.join('\n')
            if (childrenResults.length > 0) result += '\n' + indent
          }
        } else if (hasTextContent) {
          // ãƒ†ã‚­ã‚¹ãƒˆã®ã¿ã®å ´åˆ
          result += textContent
        }

        // çµ‚äº†ã‚¿ã‚°ã‚’è¿½åŠ 
        result += `</${tagName}>`

        return result
      }

      // rootElementã®å­è¦ç´ ã‚’ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
      let formattedHtml = ''
      for (let i = 0; i < rootElement.childNodes.length; i++) {
        const child = rootElement.childNodes[i]
        if (child.nodeType === 1) {
          // Element node
          formattedHtml += formatElement(child as Element) + '\n'
        }
      }

      return formattedHtml
        .replace(/\n{3,}/g, '\n\n') // 3å›žä»¥ä¸Šã®é€£ç¶šæ”¹è¡Œã‚’2å›žã«åˆ¶é™
        .replace(/\n\s*\n\s*</g, '\n<') // ç©ºè¡Œã¨è¦ç´ é–“ã®ç„¡é§„ãªæ”¹è¡Œã‚’é™¤åŽ»
        .trim()
    } catch (error) {
      // ãƒ•ã‚©ãƒ¼ãƒ«ãƒãƒƒã‚¯: ã‚ˆã‚Šç°¡æ½”ãªãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
      return html
        .replace(/>\s*</g, '>\n<')
        .split('\n')
        .filter(line => line.trim()) // ç©ºè¡Œã‚’é™¤åŽ»
        .map((line, index, array) => {
          const trimmed = line.trim()
          if (!trimmed) return ''

          let depth = 0
          for (let i = 0; i < index; i++) {
            const prevLine = array[i].trim()
            if (prevLine.match(/<[^\/!][^>]*[^\/]>$/)) depth++
            if (prevLine.match(/<\/[^>]+>$/)) depth--
          }
          if (trimmed.startsWith('</')) depth--

          return '  '.repeat(Math.max(0, depth)) + trimmed
        })
        .join('\n')
    }
  }
  
  // PreviewPanelã®ãƒ‡ãƒ¼ã‚¿å¤‰æ›´ã‚’ç›£è¦–ã—ã€PagePreviewã‚’å¼·åˆ¶å†ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°
  watch(
    () => props.editablePageData,
    () => {
      // PagePreviewã‚’å¼·åˆ¶å†ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°
      previewKey.value++
    },
    { deep: true, immediate: true }
  )
</script>

