import type {
  Component,
  PartSection,
  ArrayItem,
  ComponentProps,
  FieldInfo,
  ExtendedFieldInfo,
} from '@types'
import { logger } from '@shared/utils/logger'
import { errorHandler, safeExecute, ErrorType } from '@shared/utils/errorHandler'
import { SYSTEM_FIELDS, Z_FOR_SYSTEM_FIELDS } from '@/config/editorConfig'

interface Module {
  title: string
  body: string
}

interface ComponentUtils {
  findComponentByPath: (data: Component[], path: number[]) => Component | null
  getParentContainer: (data: Component[], path: number[]) => Component[]
  parseHierarchyPath: (pathString: string) => number[]
  stringifyHierarchyPath: (path: number[]) => string
  findComponentRecursively: (
    data: Component[],
    componentId: string,
    currentPath?: number[]
  ) => Component | null
  getTemplate: (type: string, moduleName: string) => string
  generateUniqueId: () => string
  getFieldInfo: (component: Component) => FieldInfo[]
  isTemplateField: (component: Component, key: string) => boolean
  shouldExcludeField: (key: string, value: unknown) => boolean
  shouldShowBasicField: (key: string, value: unknown, component: Component) => boolean
  shouldShowArrayField: (key: string, value: unknown, component: Component) => boolean
  shouldShowArrayItemField: (
    itemKey: string,
    itemValue: unknown,
    component: Component,
    arrayKey?: string,
    _item?: unknown
  ) => boolean
  getInputType: (key: string) => string
  updateCheckboxValue: (
    component: Component,
    propKey: string,
    option: string,
    checked: boolean
  ) => void
  isCheckboxChecked: (component: Component, propKey: string, option: string) => boolean
  addArrayItem: (component: Component, arrayKey: string) => void
  removeArrayItem: (component: Component, arrayKey: string, index: number) => void
  createNewComponent: (partType: string, module: Module) => Component
  createArrayItem: (forContent: string) => ArrayItem
  getArrayItemFieldInfo: (component: Component, arrayKey: string) => ExtendedFieldInfo[]
}

export function useComponentUtils(parts: PartSection[]): ComponentUtils {
  // éšŽå±¤ãƒ‘ã‚¹ã§ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’æ¤œç´¢
  const findComponentByPath = (data: Component[], path: number[]): Component | null => {
    if (path.length === 0) return null
    if (path[0] >= data.length) return null

    let current = data[path[0]]
    for (let i = 1; i < path.length; i++) {
      if (!current.nested_parts || !Array.isArray(current.nested_parts)) {
        return null
      }
      if (path[i] >= current.nested_parts.length) {
        return null
      }
      current = current.nested_parts[path[i]]
    }
    return current
  }

  // éšŽå±¤ãƒ‘ã‚¹ã§è¦ªã‚³ãƒ³ãƒ†ãƒŠã‚’å–å¾—
  const getParentContainer = (data: Component[], path: number[]): Component[] => {
    if (path.length === 1) return data
    let current = data[path[0]]
    for (let i = 1; i < path.length - 1; i++) {
      if (!current.nested_parts || !Array.isArray(current.nested_parts)) return []
      current = current.nested_parts[path[i]]
    }
    return current.nested_parts || []
  }

  // éšŽå±¤ãƒ‘ã‚¹æ–‡å­—åˆ—ã‚’é…åˆ—ã«å¤‰æ›
  const parseHierarchyPath = (pathString: string): number[] => {
    return pathString.split('-').map(Number)
  }

  // éšŽå±¤ãƒ‘ã‚¹é…åˆ—ã‚’æ–‡å­—åˆ—ã«å¤‰æ›
  const stringifyHierarchyPath = (path: number[]): string => {
    return path.join('-')
  }

  // å†å¸°çš„ã«ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’æ¤œç´¢ï¼ˆç„¡é™éšŽå±¤å¯¾å¿œã€z-foré…åˆ—å†…ã‚‚æ¤œç´¢ï¼‰
  const findComponentRecursively = (
    data: Component[],
    componentId: string,
    currentPath: number[] = []
  ): Component | null => {
    logger.debug(
      `findComponentRecursively: æ¤œç´¢é–‹å§‹ - componentId=${componentId}, depth=${currentPath.length}`
    )

    // dataãŒæœªå®šç¾©ã¾ãŸã¯é…åˆ—ã§ãªã„å ´åˆã¯nullã‚’è¿”ã™
    if (!data || !Array.isArray(data)) {
      logger.debug(
        `findComponentRecursively: ç„¡åŠ¹ãªãƒ‡ãƒ¼ã‚¿ - data=${data}, isArray=${Array.isArray(data)}`
      )
      return null
    }

    logger.debug(`findComponentRecursively: ${data.length}å€‹ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’æ¤œç´¢ä¸­`)

    for (let i = 0; i < data.length; i++) {
      const item = data[i]
      const itemPath = [...currentPath, i]

      logger.debug(`findComponentRecursively: [${i}] ãƒã‚§ãƒƒã‚¯ä¸­ - id=${item.id}, type=${item.type}`)

      if (item.id === componentId) {
        logger.success(
          `findComponentRecursively: ç™ºè¦‹ï¼ - ${componentId} at path=${itemPath.join('-')}`
        )
        return item
      }

      // nested_partså†…ã‚’æ¤œç´¢
      if (item.nested_parts && Array.isArray(item.nested_parts)) {
        logger.debug(
          `findComponentRecursively: nested_partsæ¤œç´¢ - ${item.id} (${item.nested_parts.length}å€‹)`
        )
        const result = findComponentRecursively(item.nested_parts, componentId, itemPath)
        if (result) return result
      }

      // z-foré…åˆ—å†…ã®ã‚¢ã‚¤ãƒ†ãƒ ã‚‚æ¤œç´¢ï¼ˆå†å¸°çš„ã«ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã‚’æŽ¢ç´¢ï¼‰
      const keys = Object.keys(item)
      for (const key of keys) {
        const value = item[key]
        if (Array.isArray(value) && key !== 'nested_parts') {
          logger.debug(
            `findComponentRecursively: z-foré…åˆ—æ¤œç´¢ - ${item.id}.${key} (${value.length}å€‹)`
          )
          // é…åˆ—ã®å„ã‚¢ã‚¤ãƒ†ãƒ ã«å¯¾ã—ã¦å†å¸°æ¤œç´¢
          for (let arrayIndex = 0; arrayIndex < value.length; arrayIndex++) {
            const arrayItem = value[arrayIndex]
            if (arrayItem && typeof arrayItem === 'object' && arrayItem.id) {
              logger.debug(
                `findComponentRecursively: z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ  - ${arrayItem.id} in ${item.id}.${key}[${arrayIndex}]`
              )

              // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ è‡ªä½“ãŒç›®çš„ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‹ãƒã‚§ãƒƒã‚¯
              if (arrayItem.id === componentId) {
                logger.success(
                  `findComponentRecursively: z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã§ç™ºè¦‹ï¼ - ${componentId}`
                )
                // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ è‡ªä½“ã‚’è¿”ã™ï¼ˆç·¨é›†å¯¾è±¡ã¨ã—ã¦ï¼‰
                return arrayItem
              }

              // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„å†…ã‚’æ¤œç´¢
              if (arrayItem.nested_parts && Array.isArray(arrayItem.nested_parts)) {
                logger.debug(
                  `findComponentRecursively: z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„æ¤œç´¢ - ${arrayItem.id}.nested_parts (${arrayItem.nested_parts.length}å€‹)`
                )
                const nestedResult = findComponentRecursively(arrayItem.nested_parts, componentId, [
                  ...itemPath,
                  arrayIndex,
                ])
                if (nestedResult) {
                  logger.success(
                    `findComponentRecursively: z-foré…åˆ—å†…ã®ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã§ç™ºè¦‹ï¼ - ${componentId}`
                  )
                  return nestedResult
                }
              }
            }
          }
        }
      }
    }

    logger.debug(`findComponentRecursively: æœªç™ºè¦‹ - ${componentId}`)
    return null
  }

  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’å–å¾—ã™ã‚‹é–¢æ•°
  const getTemplate = (type: string, moduleName: string): string => {
    return (
      safeExecute(
        () => {
          // z-for-itemã®å ´åˆã¯ç©ºã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’è¿”ã™ï¼ˆ_isZForItemãƒ•ãƒ©ã‚°ã§ç‰¹åˆ¥å‡¦ç†ã•ã‚Œã‚‹ï¼‰
          if (type === 'z-for-item') {
            return ''
          }

          if (!Array.isArray(parts)) {
            throw new Error('Partsé…åˆ—ãŒç„¡åŠ¹ã§ã™')
          }

          if (!type || !moduleName) {
            throw new Error('Type ã¾ãŸã¯ ModuleName ãŒæŒ‡å®šã•ã‚Œã¦ã„ã¾ã›ã‚“')
          }

          const section = parts.find(s => s.type === type)
          if (!section) {
            throw new Error(`ãƒ‘ãƒ¼ãƒ„ã‚¿ã‚¤ãƒ— "${type}" ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“`)
          }

          const module = section.module.find(m => m.title === moduleName)
          if (!module) {
            throw new Error(`ãƒ¢ã‚¸ãƒ¥ãƒ¼ãƒ« "${moduleName}" ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“ (type: ${type})`)
          }

          return module.body
        },
        errorHandler,
        `ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå–å¾—ã‚¨ãƒ©ãƒ¼: ${type}/${moduleName}`,
        ErrorType.TEMPLATE
      ) || ''
    )
  }

  // å‡¦ç†ä¸­ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’è¿½è·¡ã—ã¦ç„¡é™ãƒ«ãƒ¼ãƒ—ã‚’é˜²ã
  const processingStack = new Set<string>()

  // z-forå†…ã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’æŠ½å‡ºï¼ˆãƒã‚¹ãƒˆã—ãŸdivæ§‹é€ ã«å¯¾å¿œï¼‰
  const extractZForTemplate = (parentTemplate: string, arrayName: string): string => {
    logger.debug(`extractZForTemplateé–‹å§‹: arrayName=${arrayName}`)

    // z-forè¦ç´ ã®é–‹å§‹ä½ç½®ã‚’ç‰¹å®š
    const startPattern = new RegExp(`<(\\w+)[^>]*z-for="${arrayName}"[^>]*>`)
    const startMatch = parentTemplate.match(startPattern)
    if (!startMatch) {
      logger.warn(`z-for="${arrayName}" é–‹å§‹ã‚¿ã‚°ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“`)
      logger.debug('è¦ªãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå†…å®¹:', parentTemplate.substring(0, 500))
      return ''
    }

    const startIndex = parentTemplate.indexOf(startMatch[0])

    // å¯¾å¿œã™ã‚‹çµ‚äº†ã‚¿ã‚°ã‚’è¦‹ã¤ã‘ã‚‹ãŸã‚ã€æ·±åº¦ã‚’è¿½è·¡ï¼ˆä»»æ„è¦ç´ å¯¾å¿œï¼‰
    const tagName = startMatch[1] // ã‚­ãƒ£ãƒ—ãƒãƒ£ã‚°ãƒ«ãƒ¼ãƒ—ã‹ã‚‰ã‚¿ã‚°åã‚’å–å¾—
    let depth = 0
    let endIndex = startIndex
    let i = startIndex

    while (i < parentTemplate.length) {
      // è©²å½“ã‚¿ã‚°ã®é–‹å§‹ã‚’ãƒã‚§ãƒƒã‚¯
      if (parentTemplate.substring(i, i + tagName.length + 1) === `<${tagName}`) {
        // å®Œå…¨ãªã‚¿ã‚°ã¾ã§ç§»å‹•
        const tagEnd = parentTemplate.indexOf('>', i)
        if (tagEnd !== -1) {
          depth++
          i = tagEnd + 1
        } else {
          i++
        }
      }
      // è©²å½“ã‚¿ã‚°ã®çµ‚äº†ã‚’ãƒã‚§ãƒƒã‚¯
      else if (parentTemplate.substring(i, i + tagName.length + 3) === `</${tagName}>`) {
        depth--
        if (depth === 0) {
          endIndex = i + tagName.length + 3
          break
        }
        i += tagName.length + 3
      } else {
        i++
      }
    }

    if (depth !== 0) {
      logger.warn(`z-for="${arrayName}" ã®å¯¾å¿œã™ã‚‹çµ‚äº†ã‚¿ã‚°ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“ï¼ˆæ·±åº¦: ${depth}ï¼‰`)
      return ''
    }

    // z-forè¦ç´ å…¨ä½“ã‚’æŠ½å‡º
    const extractedContent = parentTemplate.substring(startIndex, endIndex).trim()

    logger.debug(`z-forå†…ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆæŠ½å‡ºæˆåŠŸ: ${arrayName}`, {
      extractedContent: extractedContent.substring(0, 200),
      fullContent: extractedContent,
      startIndex,
      endIndex,
      depth: 0,
    })

    return extractedContent
  }

  // ãƒ¦ãƒ‹ãƒ¼ã‚¯IDã‚’ç”Ÿæˆ
  const generateUniqueId = (): string => {
    return 'comp_' + Math.random().toString(36).substring(2, 11) + Date.now().toString(36)
  }

  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã‹ã‚‰å…¥åŠ›ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰æƒ…å ±ã‚’æŠ½å‡º
  const getFieldInfo = (component: Component): ExtendedFieldInfo[] => {
    const componentKey = `${component.id}_${component.type}_${component.module_name}`

    // ãƒ‡ãƒãƒƒã‚°ãƒ­ã‚°ã‚’è¿½åŠ 
    logger.debug(`getFieldInfo: _isZForItem=${(component as any)._isZForItem}`)
    logger.debug(`getFieldInfo: _arrayName=${(component as any)._arrayName}`)

    // ç„¡é™ãƒ«ãƒ¼ãƒ—é˜²æ­¢
    if (processingStack.has(componentKey)) {
      logger.warn(`getFieldInfo: ç„¡é™ãƒ«ãƒ¼ãƒ—ã‚’æ¤œå‡ºã€å‡¦ç†ã‚’ã‚¹ã‚­ãƒƒãƒ—: ${componentKey}`)
      return []
    }

    processingStack.add(componentKey)

    try {
      let template = ''

      // z-for-itemã®å ´åˆã¯è¦ªã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’å–å¾—
      if ((component as any)._isZForItem) {
        const parentType = (component as any)._parentType
        const parentModuleName = (component as any)._parentModuleName

        logger.debug(
          `getFieldInfo: z-for-itemã®parentæƒ…å ± - type: ${parentType}, module: ${parentModuleName}`
        )

        if (parentType && parentModuleName) {
          template = getTemplate(parentType, parentModuleName)
        } else {
          // è¦ªæƒ…å ±ãŒãªã„å ´åˆã¯ã€ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆè‡ªä½“ã®type/moduleã‚’ä½¿ç”¨
          logger.debug(`getFieldInfo: è¦ªæƒ…å ±ãªã—ã€ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆè‡ªä½“ã‚’ä½¿ç”¨`)
          template = getTemplate(component.type, component.module_name)
        }
      } else {
        template = getTemplate(component.type, component.module_name)
      }

      if (!template) {
        return []
      }

      logger.debug(
        `getFieldInfo: component.id=${component.id}, type=${component.type}, module=${component.module_name}`
      )
      logger.debug(`getFieldInfo: _isZForItem=${(component as any)._isZForItem}`)

      // z-foré…åˆ—è¦ç´ ã®å ´åˆã¯ã€z-forå†…ã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã®ã¿ã‚’å¯¾è±¡ã¨ã™ã‚‹
      if ((component as any)._isZForItem) {
        const arrayName = (component as any)._arrayName
        logger.debug(`getFieldInfo: z-foré…åˆ—è¦ç´ ã‚’æ¤œå‡º, arrayName=${arrayName}`)
        logger.debug(`getFieldInfo: componentãƒ—ãƒ­ãƒ‘ãƒ†ã‚£:`, Object.keys(component))
        if (arrayName) {
          logger.debug('getFieldInfo: å…ƒã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆ:', template.substring(0, 300))
          const zForTemplate = extractZForTemplate(template, arrayName)
          if (zForTemplate) {
            template = zForTemplate // z-forå†…ã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã®ã¿ã‚’ä½¿ç”¨
            logger.debug('getFieldInfo: z-forå†…ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆæŠ½å‡ºå®Œäº†:', template)
          } else {
            logger.warn(`getFieldInfo: z-forå†…ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã®æŠ½å‡ºã«å¤±æ•—: arrayName=${arrayName}`)
          }
        }
      } else {
        logger.debug('getFieldInfo: é€šå¸¸ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå‡¦ç†')
      }

      const fields: ExtendedFieldInfo[] = []

      // æ–°ä»•æ§˜ï¼šçµ±ä¸€è¨˜æ³•ã®è§£æž ($param:...)
      const allMatches = template.match(/\(\$([^:]+):(.*?)\)/g) || []

      // z-foré…åˆ—å†…ã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–
      const arrayFieldsToExclude: string[] = []

      // z-forè¨˜æ³•ã®å‡¦ç†ï¼ˆã‚ˆã‚ŠåŽ³å¯†ãªè§£æžï¼‰
      if (template.includes('z-for=')) {
        // z-forå±žæ€§ã‚’æŒã¤è¦ç´ ã‚’è¦‹ã¤ã‘ã€å¯¾å¿œã™ã‚‹çµ‚äº†ã‚¿ã‚°ã¾ã§ã®å†…å®¹ã‚’è§£æž
        const zForStartPattern = /<(\w+)[^>]*z-for="([^"]+)"[^>]*>/g
        let zForMatch

        while ((zForMatch = zForStartPattern.exec(template)) !== null) {
          const childArrayName = zForMatch[2] // 2ç•ªç›®ã®ã‚­ãƒ£ãƒ—ãƒãƒ£ã‚°ãƒ«ãƒ¼ãƒ—ãŒé…åˆ—å
          const startIndex = zForMatch.index

          logger.debug(`z-foræ¤œå‡º: ${childArrayName}, ç¾åœ¨ã®é…åˆ—: ${(component as any)._arrayName}`)

          // å­z-forã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–å¯¾è±¡ã¨ã™ã‚‹
          // z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†æ™‚ã€ç¾åœ¨ã®è¦ªz-foré…åˆ—ä»¥å¤–ã®z-forãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–
          if ((component as any)._isZForItem && childArrayName !== (component as any)._arrayName) {
            // å­z-forã®ç¯„å›²ã‚’ç‰¹å®š
            let depth = 0
            let endIndex = startIndex
            let i = startIndex

            while (i < template.length) {
              if (template.substring(i, i + 4) === '<div') {
                depth++
              } else if (template.substring(i, i + 6) === '</div>') {
                depth--
                if (depth === 0) {
                  endIndex = i + 6
                  break
                }
              }
              i++
            }

            if (depth === 0) {
              // å­z-forè¦ç´ ã®å®Œå…¨ãªå†…å®¹ã‚’å–å¾—
              const childZForContent = template.substring(startIndex, endIndex)

              // å­z-forå†…ã®çµ±ä¸€è¨˜æ³•ãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿ã‚’æŠ½å‡ºã—ã¦é™¤å¤–ãƒªã‚¹ãƒˆã«è¿½åŠ 
              const innerMatches = childZForContent.match(/\(\$([^:]+):(.*?)\)/g)
              if (innerMatches) {
                innerMatches.forEach(innerMatch => {
                  const match = innerMatch.match(/\(\$([^:]+):(.*?)\)/)
                  if (match) {
                    const paramName = match[1].replace('?', '')
                    arrayFieldsToExclude.push(paramName)
                  }
                })
              }

              logger.debug(`å­z-foré™¤å¤–: ${childArrayName} ã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–ãƒªã‚¹ãƒˆã«è¿½åŠ `)
            }
          }
        }
      }

      // ãƒ‡ãƒãƒƒã‚°ï¼šé™¤å¤–ã™ã¹ããƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’ãƒ­ã‚°å‡ºåŠ›ï¼ˆé–‹ç™ºç’°å¢ƒã®ã¿ï¼‰
      if (arrayFieldsToExclude.length > 0) {
        logger.debug('z-foré…åˆ—å†…ã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–:', arrayFieldsToExclude)
      }

      // z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†æ™‚ã¯ã€z-forå†…ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ã¿ã‚’å‡¦ç†
      // é€šå¸¸ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆç·¨é›†æ™‚ã¯ã€z-forå†…ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–
      let mainFieldMatches: string[]

      if (component._isZForItem) {
        // z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†æ™‚ï¼šè¦ªz-forã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ã¿è¡¨ç¤ºã€å­z-forãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
        mainFieldMatches = allMatches.filter(match => {
          const paramMatch = match.match(/\(\$([^:]+):(.*?)\)/)
          if (paramMatch) {
            const paramName = paramMatch[1].replace('?', '')
            const shouldInclude = !arrayFieldsToExclude.includes(paramName)
            if (shouldInclude) {
              logger.debug(`z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†: ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ ${paramName} ã‚’è¡¨ç¤º`)
            } else {
              logger.debug(
                `z-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†: ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ ${paramName} ã‚’é™¤å¤–ï¼ˆå­z-forãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ï¼‰`
              )
            }
            return shouldInclude
          }
          return false
        })
      } else {
        // é€šå¸¸ã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆç·¨é›†æ™‚ï¼šz-forå†…ã§ã®ã¿ä½¿ç”¨ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’é™¤å¤–
        mainFieldMatches = allMatches.filter(match => {
          const paramMatch = match.match(/\(\$([^:]+):(.*?)\)/)
          if (paramMatch) {
            const paramName = paramMatch[1].replace('?', '')

            // z-forå†…ã§ã®ã¿ä½¿ç”¨ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‹ãƒã‚§ãƒƒã‚¯
            let usedInZForOnly = false
            let usedOutsideZFor = false

            // z-forãƒ–ãƒ­ãƒƒã‚¯å†…ã§ã®ä½¿ç”¨ã‚’ãƒã‚§ãƒƒã‚¯
            const fieldPattern = new RegExp(
              `\\(\\$${paramName.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}[?:]`,
              'g'
            )

            // z-forãƒ–ãƒ­ãƒƒã‚¯æŠ½å‡º
            const zForBlockPattern = /<[^>]*z-for="[^"]*"[^>]*>[\s\S]*?<\/[^>]*>/g
            let zForMatch

            while ((zForMatch = zForBlockPattern.exec(template)) !== null) {
              const zForBlock = zForMatch[0]
              if (fieldPattern.test(zForBlock)) {
                usedInZForOnly = true
                break
              }
            }

            // z-forãƒ–ãƒ­ãƒƒã‚¯å¤–ã§ã®ä½¿ç”¨ã‚’ãƒã‚§ãƒƒã‚¯
            if (usedInZForOnly) {
              const templateWithoutZFor = template.replace(zForBlockPattern, '')
              fieldPattern.lastIndex = 0
              if (fieldPattern.test(templateWithoutZFor)) {
                usedOutsideZFor = true
              }
            }

            // z-forãƒ–ãƒ­ãƒƒã‚¯å†…ã§ã®ã¿ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹å ´åˆã¯é™¤å¤–
            if (usedInZForOnly && !usedOutsideZFor) {
              logger.debug(`getFieldInfo: ${paramName} ã‚’z-forå†…å°‚ç”¨ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¨ã—ã¦é™¤å¤–`)
              return false
            } else {
              logger.debug(`getFieldInfo: ${paramName} ã‚’è¡¨ç¤ºå¯¾è±¡ã«å«ã‚ã‚‹`)
              return true
            }
          }
          return true
        })
      }

      // æ–°ä»•æ§˜ï¼šUIåˆ¤å®šãƒ«ãƒ¼ãƒ«ã«åŸºã¥ãå‡¦ç†
      mainFieldMatches.forEach(match => {
        const paramMatch = match.match(/\(\$([^:]+):(.*?)\)/)
        if (paramMatch) {
          const [, fullParamName, optionsStr] = paramMatch
          const isMultipleSelect = fullParamName.includes('?')
          const paramName = fullParamName.replace('?', '')

          // UIåˆ¤å®šãƒ«ãƒ¼ãƒ«
          if (optionsStr.includes('|')) {
            // ãƒ‘ã‚¤ãƒ—åŒºåˆ‡ã‚Š â†’ ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ ã¾ãŸã¯ ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹
            let options = optionsStr.split('|')
            let hasEmptyOption = false

            // ç©ºã®è¨­å®šï¼ˆ:|ã‚„æœ«å°¾ã®|ï¼‰ã‚’ãƒã‚§ãƒƒã‚¯
            if (
              optionsStr.includes('||') ||
              optionsStr.startsWith('|') ||
              optionsStr.endsWith('|')
            ) {
              hasEmptyOption = true
            }

            // ç©ºã®ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’é™¤åŽ»
            options = options.filter(opt => opt.trim() !== '')

            // ç©ºè¨­å®šãŒã‚ã‚‹å ´åˆã¯ã€ŒæŒ‡å®šãªã—ã€ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’è¿½åŠ 
            if (hasEmptyOption) {
              options.unshift('æŒ‡å®šãªã—')
            }

            if (isMultipleSelect) {
              // ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ï¼ˆè¤‡æ•°é¸æŠžï¼‰
              const defaultValue = options.length > 0 ? [options[0]] : []
              const fieldInfo = {
                type: 'checkbox' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹)`,
              }
              fields.push(fieldInfo)
            } else {
              // ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ï¼ˆå˜ä¸€é¸æŠžï¼‰
              const defaultValue = options[0] || ''
              const fieldInfo = {
                type: 'radio' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³)`,
              }
              fields.push(fieldInfo)
            }
          } else if (optionsStr.includes(',')) {
            // ã‚«ãƒ³ãƒžåŒºåˆ‡ã‚Š â†’ ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹
            const options = optionsStr
              .split(',')
              .map(opt => opt.trim())
              .filter(opt => opt !== '')

            if (isMultipleSelect) {
              // ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹ï¼ˆè¤‡æ•°é¸æŠžï¼‰
              const defaultValue = options.length > 0 ? [options[0]] : []
              const fieldInfo = {
                type: 'select-multiple' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹ãƒ»è¤‡æ•°é¸æŠž)`,
              }
              fields.push(fieldInfo)
            } else {
              // ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹ï¼ˆå˜ä¸€é¸æŠžï¼‰
              const defaultValue = options[0] || ''
              const fieldInfo = {
                type: 'select' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹)`,
              }
              fields.push(fieldInfo)
            }
          } else {
            // å˜ä¸€å€¤ â†’ ãƒ†ã‚­ã‚¹ãƒˆãƒœãƒƒã‚¯ã‚¹
            const fieldInfo = {
              type: 'text' as const,
              paramName,
              propKey: paramName,
              options: [],
              defaultValue: optionsStr,
              label: `${paramName} (ãƒ†ã‚­ã‚¹ãƒˆãƒœãƒƒã‚¯ã‚¹)`,
            }
            fields.push(fieldInfo)
          }
        }
      })

      logger.debug(
        `getFieldInfo: æŠ½å‡ºã•ã‚ŒãŸãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰æ•°=${fields.length}`,
        fields.map(f => ({ paramName: f.paramName, type: f.type }))
      )
      return fields
    } catch (error) {
      logger.error(`getFieldInfo: ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ: ${componentKey}`, error)
      return []
    } finally {
      processingStack.delete(componentKey)
    }
  }

  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã§å‡¦ç†ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‹ã©ã†ã‹ã‚’åˆ¤å®š
  const isTemplateField = (component: Component, key: string): boolean => {
    try {
      const template = getTemplate(component.type, component.module_name)
      if (!template) return false

      // z-foré…åˆ—è¦ç´ ã®å ´åˆã¯z-forå†…ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’ãƒã‚§ãƒƒã‚¯
      let targetTemplate = template
      if ((component as any)._isZForItem) {
        const arrayName = (component as any)._arrayName
        const zForTemplate = extractZForTemplate(template, arrayName)
        if (zForTemplate) {
          targetTemplate = zForTemplate
        }
      }

      // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã®ãƒ‘ã‚¿ãƒ¼ãƒ³ãƒžãƒƒãƒãƒ³ã‚°
      const textPattern = new RegExp(`\\{\\$${key}:[^}]*\\}`)
      const selectPattern = new RegExp(`\\(\\$${key}:[^)]*\\)`)
      const conditionalPattern = new RegExp(`\\(\\$${key}\\?:[^)]*\\)`)

      const isTemplateFieldFound =
        textPattern.test(targetTemplate) ||
        selectPattern.test(targetTemplate) ||
        conditionalPattern.test(targetTemplate)

      // z-forå†…ã§ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚‚ãƒã‚§ãƒƒã‚¯ï¼ˆãƒ¡ã‚¤ãƒ³ç·¨é›†ãƒ‘ãƒãƒ«ã‹ã‚‰é™¤å¤–ã™ã‚‹ãŸã‚ï¼‰
      if (!isTemplateFieldFound && !(component as any)._isZForItem) {
        // z-forãƒ–ãƒ­ãƒƒã‚¯å†…ã§ã“ã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ãŒä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
        const zForBlockPattern = /<[^>]*z-for="[^"]*"[^>]*>[\s\S]*?<\/[^>]*>/g
        let zForMatch
        while ((zForMatch = zForBlockPattern.exec(template)) !== null) {
          const zForBlock = zForMatch[0]
          logger.debug(`isTemplateField: z-forãƒ–ãƒ­ãƒƒã‚¯æ¤œæŸ»ä¸­ - ${key}`, zForBlock.substring(0, 100))
          if (
            textPattern.test(zForBlock) ||
            selectPattern.test(zForBlock) ||
            conditionalPattern.test(zForBlock)
          ) {
            logger.success(
              `isTemplateField: ${key} ã¯z-forå†…ã§ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ãŸã‚ãƒ¡ã‚¤ãƒ³ç·¨é›†ãƒ‘ãƒãƒ«ã‹ã‚‰é™¤å¤–`
            )
            return true
          }
        }

        // ã‚ˆã‚Šè©³ç´°ãªãƒ‡ãƒãƒƒã‚°ãƒ­ã‚°
        logger.debug(`isTemplateField: ${key} - z-forå†…ã§ã®ä½¿ç”¨æ¤œå‡ºçµæžœ: false`)
        logger.debug(`isTemplateField: ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå†…å®¹ (æœ€åˆ200æ–‡å­—):`, template.substring(0, 200))
      }

      return isTemplateFieldFound
    } catch (error) {
      logger.warn(`isTemplateField error for ${key}:`, error)
      return false
    }
  }

  // é™¤å¤–ã™ã¹ããƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‹ã©ã†ã‹ã‚’åˆ¤å®š
  const shouldExcludeField = (key: string, value: unknown): boolean => {
    // ã‚·ã‚¹ãƒ†ãƒ å†…éƒ¨ã§ä½¿ç”¨ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ï¼ˆå›ºå®šï¼‰
    const fixedSystemFields = ['content', 'nested_parts']
    if (fixedSystemFields.includes(key)) return true

    // é…åˆ—ãƒ‡ãƒ¼ã‚¿
    if (Array.isArray(value)) return true

    // ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆãƒ‡ãƒ¼ã‚¿
    if (typeof value === 'object' && value !== null) return true

    return false
  }

  // åŸºæœ¬ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’è¡¨ç¤ºã™ã‚‹ã‹ã©ã†ã‹ã‚’åˆ¤å®š
  const shouldShowBasicField = (key: string, value: unknown, component: Component): boolean => {
    // IDãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
    if (key === 'id') return false

    // typeã€module_nameãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
    if (key === 'type' || key === 'module_name') return false

    // z-forå†…ã§ã®ã¿ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯è¦ªã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã§ã¯é™¤å¤–ï¼ˆæ±Žç”¨çš„ãªæ¤œå‡ºï¼‰
    const template = getTemplate(component.type, component.module_name)
    if (template && template.includes('z-for=')) {
      // z-forãƒ–ãƒ­ãƒƒã‚¯å†…ã§ã®ã¿ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‹ãƒã‚§ãƒƒã‚¯
      const zForBlockPattern = /<[^>]*z-for="[^"]*"[^>]*>[\s\S]*?<\/[^>]*>/g
      let zForMatch
      let usedInZForOnly = false
      let usedOutsideZFor = false

      // z-forãƒ–ãƒ­ãƒƒã‚¯å†…ã§ã®ä½¿ç”¨ã‚’ãƒã‚§ãƒƒã‚¯ï¼ˆãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•å…¨ã¦ã«å¯¾å¿œï¼‰
      const fieldPattern = new RegExp(
        `\\(\\$${key.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}[?:]`,
        'g'
      )

      while ((zForMatch = zForBlockPattern.exec(template)) !== null) {
        const zForBlock = zForMatch[0]
        if (fieldPattern.test(zForBlock)) {
          usedInZForOnly = true
          break
        }
      }

      // z-forãƒ–ãƒ­ãƒƒã‚¯å¤–ã§ã®ä½¿ç”¨ã‚’ãƒã‚§ãƒƒã‚¯
      if (usedInZForOnly) {
        const templateWithoutZFor = template.replace(zForBlockPattern, '')
        fieldPattern.lastIndex = 0
        if (fieldPattern.test(templateWithoutZFor)) {
          usedOutsideZFor = true
        }
      }

      // z-forãƒ–ãƒ­ãƒƒã‚¯å†…ã§ã®ã¿ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹å ´åˆã¯é™¤å¤–
      if (usedInZForOnly && !usedOutsideZFor) {
        return false
      } else if (usedInZForOnly && usedOutsideZFor) {
      } else {
      }
    }

    // z-foré…åˆ—è¦ç´ ã®å ´åˆï¼šåŸºæœ¬ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’ä¸€åˆ‡è¡¨ç¤ºã—ãªã„ï¼ˆãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ã¿è¡¨ç¤ºï¼‰
    if ((component as any)._isZForItem) {
      return false
    }

    // z-foré…åˆ—è¦ç´ å›ºæœ‰ã®ãƒ¡ã‚¿ãƒ‡ãƒ¼ã‚¿ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
    if (Z_FOR_SYSTEM_FIELDS.includes(key as any)) return false

    // ã‚·ã‚¹ãƒ†ãƒ ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚„è¤‡é›‘ãªãƒ‡ãƒ¼ã‚¿ã¯é™¤å¤–
    if (shouldExcludeField(key, value)) return false

    // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã§å‡¦ç†ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–ï¼ˆé‡è¤‡ã‚’é˜²ããŸã‚ï¼‰
    if (isTemplateField(component, key)) {
      return false
    }

    // ç©ºã®å€¤ã‚„æœªå®šç¾©ã®å€¤ã¯é™¤å¤–ï¼ˆã‚ˆã‚ŠåŽ³å¯†ã«ãƒã‚§ãƒƒã‚¯ï¼‰
    if (value === '' || value === null || value === undefined) return false
    if (typeof value === 'string' && value.trim() === '') return false
    if (typeof value === 'number' && value === 0) return false

    // ãƒ—ãƒªãƒŸãƒ†ã‚£ãƒ–åž‹ã§å€¤ãŒå­˜åœ¨ã™ã‚‹å ´åˆã®ã¿è¡¨ç¤º
    return (
      (typeof value === 'string' && value.trim() !== '') ||
      (typeof value === 'number' && value !== 0) ||
      typeof value === 'boolean'
    )
  }

  // é…åˆ—ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’è¡¨ç¤ºã™ã‚‹ã‹ã©ã†ã‹ã‚’åˆ¤å®š
  const shouldShowArrayField = (key: string, value: unknown, component: Component): boolean => {
    const componentKey = `${component.id}_${component.type}_${component.module_name}_array_${key}`

    // ç„¡é™ãƒ«ãƒ¼ãƒ—é˜²æ­¢
    if (processingStack.has(componentKey)) {
      logger.warn(`shouldShowArrayField: ç„¡é™ãƒ«ãƒ¼ãƒ—ã‚’æ¤œå‡ºã€å‡¦ç†ã‚’ã‚¹ã‚­ãƒƒãƒ—: ${componentKey}`)
      return false
    }

    processingStack.add(componentKey)

    try {
      logger.debug(
        `shouldShowArrayFieldé–‹å§‹: key=${key}, component: ${component.id} (${component.type}/${component.module_name})`,
        { key, value, componentId: component.id }
      )

      // ã‚·ã‚¹ãƒ†ãƒ å†…éƒ¨ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
      if (SYSTEM_FIELDS.includes(String(key) as any)) {
        logger.debug(`shouldShowArrayField: ${key} ã¯ã‚·ã‚¹ãƒ†ãƒ å†…éƒ¨ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ãŸã‚é™¤å¤–`)
        return false
      }

      // z-forã‚·ã‚¹ãƒ†ãƒ ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–ï¼ˆz-foré…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç·¨é›†æ™‚ï¼‰
      if ((component as any)._isZForItem) {
        if (Z_FOR_SYSTEM_FIELDS.includes(String(key) as any)) {
          logger.debug(`shouldShowArrayField: ${key} ã¯z-forã‚·ã‚¹ãƒ†ãƒ ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ãŸã‚é™¤å¤–`)
          return false
        }
      }

      // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã§å‡¦ç†ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
      if (isTemplateField(component, key)) {
        logger.debug(`shouldShowArrayField: ${key} ã¯ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ãŸã‚é™¤å¤–`)
        return false
      }

      // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã§z-forè¨˜æ³•ã¾ãŸã¯forè¨˜æ³•ãŒä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
      const template = getTemplate(component.type, component.module_name)
      if (template) {
        logger.debug(`shouldShowArrayField: ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå–å¾—æˆåŠŸ:`, template.substring(0, 200))
        const zForPattern = new RegExp(`z-for="${key}"`)

        if (zForPattern.test(template)) {
          logger.success(
            `shouldShowArrayField: ${key}ã¯z-forè¨˜æ³•ã§ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ãŸã‚ç·¨é›†ãƒ‘ãƒãƒ«ã§ã¯éžè¡¨ç¤ºï¼ˆãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼å´ã§ç·¨é›†ï¼‰`
          )

          // z-forè¨˜æ³•ã§ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹å ´åˆã¯ã€é…åˆ—ã§ãªãã¦ã‚‚ç©ºé…åˆ—ã§åˆæœŸåŒ–
          if (!Array.isArray(value)) {
            logger.debug(
              `shouldShowArrayField: ${key}ã¯é…åˆ—ã§ãªã„ãŒã€è¨˜æ³•ã§ä½¿ç”¨ã•ã‚Œã¦ã„ã‚‹ãŸã‚ç©ºé…åˆ—ã§åˆæœŸåŒ–`
            )
            component[key] = []
          }

          // z-forè¨˜æ³•ã®é…åˆ—ã¯ç·¨é›†ãƒ‘ãƒãƒ«ã§ã¯éžè¡¨ç¤ºï¼ˆãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼å´ã§ã‚¤ãƒ³ãƒ©ã‚¤ãƒ³ç·¨é›†ï¼‰
          return false
        } else {
          logger.debug(`shouldShowArrayField: ${key}ã¯é…åˆ—è¨˜æ³•ã§ä½¿ç”¨ã•ã‚Œã¦ã„ãªã„`)
        }
      } else {
        logger.debug(`shouldShowArrayField: ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå–å¾—å¤±æ•—`)
      }

      // é…åˆ—ã§ãªã„å ´åˆã¯é™¤å¤–ï¼ˆãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ãƒã‚§ãƒƒã‚¯å¾Œï¼‰
      if (!Array.isArray(value)) {
        logger.debug(`shouldShowArrayField: ${key}ã¯é…åˆ—ã§ãªãã€è¨˜æ³•ã§ã‚‚ä½¿ç”¨ã•ã‚Œã¦ã„ãªã„`)
        return false
      }

      return false
    } catch (error) {
      logger.error(`shouldShowArrayField: ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ: ${componentKey}`, error)
      return false
    } finally {
      processingStack.delete(componentKey)
    }
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å†…ã§ã¯é€šå¸¸ã®ãƒ†ã‚­ã‚¹ãƒˆãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‚’éžè¡¨ç¤ºã«ã—ã€ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ã¿ã‚’è¡¨ç¤ºã™ã‚‹ã‚ˆã†ã«ä¿®æ­£
  const shouldShowArrayItemField = (
    itemKey: string,
    itemValue: unknown,
    component: Component,
    arrayKey?: string,
    _item?: unknown
  ): boolean => {
    // IDãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
    if (itemKey === 'id') return false

    // é…åˆ—ãƒ‡ãƒ¼ã‚¿ã¯é™¤å¤–
    if (Array.isArray(itemValue)) return false

    // ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆãƒ‡ãƒ¼ã‚¿ã¯é™¤å¤–
    if (typeof itemValue === 'object' && itemValue !== null) return false

    // ç©ºã®å€¤ã‚„æœªå®šç¾©ã®å€¤ã¯é™¤å¤–
    if (itemValue === '' || itemValue === null || itemValue === undefined) return false
    if (typeof itemValue === 'string' && itemValue.trim() === '') return false
    if (typeof itemValue === 'number' && itemValue === 0) return false

    // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã§å‡¦ç†ã•ã‚Œã‚‹ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã‹ã©ã†ã‹ã‚’ãƒã‚§ãƒƒã‚¯
    const template = getTemplate(component.type, component.module_name)

    // ãƒ—ãƒªãƒŸãƒ†ã‚£ãƒ–åž‹ã§å€¤ãŒå­˜åœ¨ã™ã‚‹å ´åˆã®ã¿è¡¨ç¤º
    return (
      (typeof itemValue === 'string' && itemValue.trim() !== '') ||
      (typeof itemValue === 'number' && itemValue !== 0) ||
      typeof itemValue === 'boolean'
    )
  }

  // å…¥åŠ›ã‚¿ã‚¤ãƒ—ã‚’æ±ºå®š
  const getInputType = (key: string): string => {
    if (key.includes('url') || key.includes('src')) return 'url'
    if (key.includes('email')) return 'email'
    if (key.includes('number') || key.includes('count')) return 'number'
    return 'text'
  }

  // ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ã®å€¤ã‚’æ›´æ–°
  const updateCheckboxValue = (
    component: Component,
    propKey: string,
    option: string,
    checked: boolean
  ): void => {
    if (!component[propKey]) {
      component[propKey] = []
    }

    const propValue = component[propKey] as unknown as string[]
    if (checked) {
      if (!propValue.includes(option)) {
        propValue.push(option)
      }
    } else {
      component[propKey] = propValue.filter((item: string) => item !== option) as unknown as
        | string
        | Component[]
        | ComponentProps
        | undefined
        | ArrayItem[]
    }
  }

  // ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ãŒãƒã‚§ãƒƒã‚¯ã•ã‚Œã¦ã„ã‚‹ã‹ã©ã†ã‹
  const isCheckboxChecked = (component: Component, propKey: string, option: string): boolean => {
    const propValue = component[propKey] as unknown as string[]
    return Array.isArray(propValue) && propValue.includes(option)
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã‚’è¿½åŠ 
  const addArrayItem = (component: Component, arrayKey: string): void => {
    
    if (!component[arrayKey]) {
      component[arrayKey] = []
    }

    const newItem: Record<string, string | number | boolean | undefined> = {
      id: `${arrayKey}_${Date.now()}`,
    }

    // æ—¢å­˜ã®ã‚¢ã‚¤ãƒ†ãƒ ã‹ã‚‰æ§‹é€ ã‚’æŽ¨æ¸¬
    const arrayValue = component[arrayKey] as unknown as ArrayItem[]
    
    if (arrayValue.length > 0) {
      const template = arrayValue[0]
      
      Object.keys(template).forEach(key => {
        if (key !== 'id') {
          const originalValue = template[key]
          const newValue = typeof originalValue === 'string' ? '' : originalValue
          newItem[key] = newValue
        }
      })
    } else {
    }
    // z-forè¨˜æ³•ã®ã¿ä½¿ç”¨ï¼ˆ@forè¨˜æ³•ã‹ã‚‰ã®æ§‹é€ ç”Ÿæˆã¯å‰Šé™¤æ¸ˆã¿ï¼‰

    arrayValue.push(newItem as ArrayItem)
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã‚’å‰Šé™¤
  const removeArrayItem = (component: Component, arrayKey: string, index: number): void => {
    const arrayValue = component[arrayKey] as unknown as ArrayItem[]
    if (arrayValue && arrayValue.length > index) {
      arrayValue.splice(index, 1)
    }
  }

  // æ–°ã—ã„ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’ä½œæˆ
  const createNewComponent = (partType: string, module: Module): Component => {
    return (
      safeExecute(
        () => {
          if (!partType || !module) {
            throw new Error('partType ã¾ãŸã¯ module ãŒæŒ‡å®šã•ã‚Œã¦ã„ã¾ã›ã‚“')
          }

          if (!module.title || !module.body) {
            throw new Error('module.title ã¾ãŸã¯ module.body ãŒç„¡åŠ¹ã§ã™')
          }

          const id = generateUniqueId()
          logger.debug(`createNewComponenté–‹å§‹: partType=${partType}, module=${module.title}`)

          const newComponent: Component = {
            id,
            type: partType,
            module_name: module.title,
          }

          // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‹ã‚‰åˆæœŸå€¤ã‚’è¨­å®š
          const template = module.body
          logger.debug(`createNewComponent ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆ:`, template.substring(0, 300))

          // ãƒ†ã‚­ã‚¹ãƒˆå…¥åŠ›ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®åˆæœŸå€¤ã‚’è¨­å®šï¼ˆãƒ‡ã‚£ãƒ¼ãƒ—ã‚³ãƒ”ãƒ¼ï¼‰
          const textMatches = template.match(/\{\$([^:}]+):([^}]+)\}/g)
          if (textMatches) {
            textMatches.forEach((match: string) => {
              const textMatch = match.match(/\{\$([^:}]+):([^}]+)\}/)
              if (textMatch) {
                const [, paramName, defaultValue] = textMatch
                if (paramName !== 'input') {
                  // input ã¯ç‰¹åˆ¥ãªå€¤ãªã®ã§ã‚¹ã‚­ãƒƒãƒ—
                  // æ–‡å­—åˆ—ã‚’ãƒ‡ã‚£ãƒ¼ãƒ—ã‚³ãƒ”ãƒ¼ï¼ˆæ–°ã—ã„æ–‡å­—åˆ—ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’ä½œæˆï¼‰
                  newComponent[paramName] = String(defaultValue)
                }
              }
            })
          }

          // ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ã®åˆæœŸå€¤ã‚’è¨­å®šï¼ˆãƒ‡ã‚£ãƒ¼ãƒ—ã‚³ãƒ”ãƒ¼ï¼‰
          const radioMatches = template.match(/\(\$([^:?]+):(.*?)\)/g)
          if (radioMatches) {
            radioMatches.forEach((match: string) => {
              const radioMatch = match.match(/\(\$([^:?]+):(.*?)\)/)
              if (radioMatch) {
                const [, paramName, optionsStr] = radioMatch
                const options = optionsStr.split('|').filter((opt: string) => opt.trim() !== '')
                if (options.length > 0) {
                  // æœ€åˆã®ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’åˆæœŸå€¤ã¨ã—ã¦è¨­å®šï¼ˆæ–°ã—ã„æ–‡å­—åˆ—ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ï¼‰
                  newComponent[paramName] = String(options[0])
                }
              }
            })
          }

          // ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ã®åˆæœŸå€¤ã‚’è¨­å®šï¼ˆæ–°ã—ã„é…åˆ—ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ï¼‰
          const checkboxMatches = template.match(/\(\$([^:?]+)\?:(.*?)\)/g)
          if (checkboxMatches) {
            checkboxMatches.forEach((match: string) => {
              const checkboxMatch = match.match(/\(\$([^:?]+)\?:(.*?)\)/)
              if (checkboxMatch) {
                const [, paramName, optionsStr] = checkboxMatch
                const options = optionsStr.split('|').filter((opt: string) => opt.trim() !== '')
                // æœ€åˆã®ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’åˆæœŸå€¤ã¨ã—ã¦é…åˆ—ã§è¨­å®š
                newComponent[paramName] = options.length > 0 ? [options[0]] : []
              }
            })
          }

          // z-forè¨˜æ³•ã®ã¿ä½¿ç”¨ï¼ˆ@forè¨˜æ³•ã®åˆæœŸåŒ–ã¯å‰Šé™¤æ¸ˆã¿ï¼‰

          // z-foré…åˆ—ã®åˆæœŸå€¤ã‚’è¨­å®šï¼ˆç©ºé…åˆ—ã¨ã—ã¦åˆæœŸåŒ–ï¼‰
          const zForMatches = template.match(/z-for="([^"]+)"/g)
          if (zForMatches) {
            logger.debug(`createNewComponent: z-forè¨˜æ³•ç™ºè¦‹:`, zForMatches)
            zForMatches.forEach((match: string) => {
              const zForMatch = match.match(/z-for="([^"]+)"/)
              if (zForMatch) {
                const [, arrayName] = zForMatch
                // ã¾ã åˆæœŸåŒ–ã•ã‚Œã¦ã„ãªã„å ´åˆã®ã¿ç©ºé…åˆ—ã§åˆæœŸåŒ–
                if (!newComponent[arrayName]) {
                  logger.debug(`createNewComponent: z-foré…åˆ— ${arrayName} ã‚’ç©ºé…åˆ—ã§åˆæœŸåŒ–`)
                  newComponent[arrayName] = []
                } else {
                  logger.debug(`createNewComponent: z-foré…åˆ— ${arrayName} ã¯æ—¢ã«åˆæœŸåŒ–æ¸ˆã¿`)
                }
              }
            })
          } else {
            logger.debug(`createNewComponent: z-forè¨˜æ³•ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“ã§ã—ãŸ`)
          }

          logger.debug(`createNewComponentå®Œäº†: newComponent=`, newComponent)
          return newComponent
        },
        errorHandler,
        `ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆä½œæˆã‚¨ãƒ©ãƒ¼: ${partType}/${module?.title}`,
        ErrorType.COMPONENT
      ) || {
        id: generateUniqueId(),
        type: partType || 'unknown',
        module_name: module?.title || 'unknown',
      }
    )
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®åˆæœŸå€¤ã‚’ä½œæˆï¼ˆãƒ‡ã‚£ãƒ¼ãƒ—ã‚³ãƒ”ãƒ¼ç‰ˆï¼‰
  const createArrayItem = (forContent: string): ArrayItem => {
    const item: ArrayItem = {
      id: generateUniqueId(),
    }

    // z-forå†…ã®ãƒ†ã‚­ã‚¹ãƒˆãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®åˆæœŸå€¤ã‚’è¨­å®š
    const textMatches = forContent.match(/\{\$([^:}]+):([^}]+)\}/g)
    if (textMatches) {
      textMatches.forEach((match: string) => {
        const textMatch = match.match(/\{\$([^:}]+):([^}]+)\}/)
        if (textMatch) {
          const [, paramName, defaultValue] = textMatch
          // æ–°ã—ã„æ–‡å­—åˆ—ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’ä½œæˆ
          item[paramName] = String(defaultValue)
        }
      })
    }

    // z-forå†…ã®ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ã®åˆæœŸå€¤ã‚’è¨­å®š
    const radioMatches = forContent.match(/\(\$([^:?]+):(.*?)\)/g)
    if (radioMatches) {
      radioMatches.forEach((match: string) => {
        const radioMatch = match.match(/\(\$([^:?]+):(.*?)\)/)
        if (radioMatch) {
          const [, paramName, optionsStr] = radioMatch
          const options = optionsStr.split('|').filter((opt: string) => opt.trim() !== '')
          if (options.length > 0) {
            // æ–°ã—ã„æ–‡å­—åˆ—ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ã‚’ä½œæˆ
            item[paramName] = String(options[0])
          }
        }
      })
    }

    return item
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç”¨ã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰æƒ…å ±ã‚’å–å¾—
  const getArrayItemFieldInfo = (component: Component, arrayKey: string): ExtendedFieldInfo[] => {
    const template = getTemplate(component.type, component.module_name)
    if (!template) return []

    const fields: ExtendedFieldInfo[] = []

    // z-forå±žæ€§ã‚’æŒã¤è¦ç´ å†…ã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆè¨˜æ³•ã‚’æŠ½å‡º
    let forContent = ''

    // z-forè¨˜æ³•ã®ãƒã‚§ãƒƒã‚¯ - ãƒã‚¹ãƒˆã—ãŸdivæ§‹é€ ã«å¯¾å¿œ
    const zForExtractedContent = extractZForTemplate(template, arrayKey)
    if (zForExtractedContent) {
      forContent = zForExtractedContent
    }

    if (forContent) {
      // æ–°ä»•æ§˜ï¼šçµ±ä¸€è¨˜æ³•ã®è§£æž ($param:...)
      const arrayFieldMatches = forContent.match(/\(\$([^:]+):(.*?)\)/g) || []

      arrayFieldMatches.forEach(match => {
        const paramMatch = match.match(/\(\$([^:]+):(.*?)\)/)
        if (paramMatch) {
          const [, fullParamName, optionsStr] = paramMatch
          const isMultipleSelect = fullParamName.includes('?')
          const paramName = fullParamName.replace('?', '')

          // UIåˆ¤å®šãƒ«ãƒ¼ãƒ«
          if (optionsStr.includes('|')) {
            // ãƒ‘ã‚¤ãƒ—åŒºåˆ‡ã‚Š â†’ ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ ã¾ãŸã¯ ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹
            let options = optionsStr.split('|')
            let hasEmptyOption = false

            // ç©ºã®è¨­å®šï¼ˆ:|ã‚„æœ«å°¾ã®|ï¼‰ã‚’ãƒã‚§ãƒƒã‚¯
            if (
              optionsStr.includes('||') ||
              optionsStr.startsWith('|') ||
              optionsStr.endsWith('|')
            ) {
              hasEmptyOption = true
            }

            // ç©ºã®ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’é™¤åŽ»
            options = options.filter(opt => opt.trim() !== '')

            // ç©ºè¨­å®šãŒã‚ã‚‹å ´åˆã¯ã€ŒæŒ‡å®šãªã—ã€ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã‚’è¿½åŠ 
            if (hasEmptyOption) {
              options.unshift('æŒ‡å®šãªã—')
            }

            if (isMultipleSelect) {
              // ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ï¼ˆè¤‡æ•°é¸æŠžï¼‰
              const defaultValue = options.length > 0 ? [options[0]] : []
              const fieldInfo = {
                type: 'checkbox' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹)`,
              }
              fields.push(fieldInfo)
            } else {
              // ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ï¼ˆå˜ä¸€é¸æŠžï¼‰
              const defaultValue = options[0] || ''
              const fieldInfo = {
                type: 'radio' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³)`,
              }
              fields.push(fieldInfo)
            }
          } else if (optionsStr.includes(',')) {
            // ã‚«ãƒ³ãƒžåŒºåˆ‡ã‚Š â†’ ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹
            const options = optionsStr
              .split(',')
              .map(opt => opt.trim())
              .filter(opt => opt !== '')

            if (isMultipleSelect) {
              // ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹ï¼ˆè¤‡æ•°é¸æŠžï¼‰
              const defaultValue = options.length > 0 ? [options[0]] : []
              const fieldInfo = {
                type: 'select-multiple' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹ãƒ»è¤‡æ•°é¸æŠž)`,
              }
              fields.push(fieldInfo)
            } else {
              // ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹ï¼ˆå˜ä¸€é¸æŠžï¼‰
              const defaultValue = options[0] || ''
              const fieldInfo = {
                type: 'select' as const,
                paramName,
                propKey: paramName,
                options,
                defaultValue,
                label: `${paramName} (ã‚»ãƒ¬ã‚¯ãƒˆãƒœãƒƒã‚¯ã‚¹)`,
              }
              fields.push(fieldInfo)
            }
          } else {
            // å˜ä¸€å€¤ â†’ ãƒ†ã‚­ã‚¹ãƒˆãƒœãƒƒã‚¯ã‚¹
            const fieldInfo = {
              type: 'text' as const,
              paramName,
              propKey: paramName,
              options: [],
              defaultValue: optionsStr,
              label: `${paramName} (ãƒ†ã‚­ã‚¹ãƒˆãƒœãƒƒã‚¯ã‚¹)`,
            }
            fields.push(fieldInfo)
          }
        }
      })
    }

    return fields
  }

  return {
    findComponentByPath,
    getParentContainer,
    parseHierarchyPath,
    stringifyHierarchyPath,
    findComponentRecursively,
    getTemplate,
    generateUniqueId,
    getFieldInfo,
    isTemplateField,
    shouldExcludeField,
    shouldShowBasicField,
    shouldShowArrayField,
    shouldShowArrayItemField,
    getInputType,
    updateCheckboxValue,
    isCheckboxChecked,
    addArrayItem,
    removeArrayItem,
    createNewComponent,
    createArrayItem,
    getArrayItemFieldInfo,
  }
}
