import { ref, computed, watch } from 'vue'
import { logger } from '@shared/utils/logger'

/**
 * æŠ˜ã‚ŠãŸãŸã¿å¯èƒ½ãªã‚¢ã‚¤ãƒ†ãƒ ã®çŠ¶æ…‹ç®¡ç†ã‚’è¡Œã†ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
 */
export function useCollapsibleItems() {
  // æŠ˜ã‚ŠãŸãŸã¾ã‚Œã¦ã„ã‚‹ã‚¢ã‚¤ãƒ†ãƒ ã®IDã‚»ãƒƒãƒˆ
  const collapsedItems = ref<Set<string>>(new Set())

  // LocalStorageã‚­ãƒ¼
  const STORAGE_KEY = 'zero-code-collapsed-items'

  /**
   * LocalStorageã‹ã‚‰æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã‚’å¾©å…ƒï¼ˆåˆæœŸè¡¨ç¤ºã¯ã™ã¹ã¦å±•é–‹ï¼‰
   */
  const loadCollapsedState = (): void => {
    try {
      // åˆæœŸè¡¨ç¤ºã¯ã™ã¹ã¦å±•é–‹ã«ã™ã‚‹ãŸã‚ã€LocalStorageã‹ã‚‰ã®å¾©å…ƒã‚’ç„¡åŠ¹åŒ–
      // const stored = localStorage.getItem(STORAGE_KEY)
      // if (stored) {
      //   const parsedIds = JSON.parse(stored) as string[]
      //   collapsedItems.value = new Set(parsedIds)
      //   logger.debug('æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã‚’å¾©å…ƒ:', parsedIds.length, 'ã‚¢ã‚¤ãƒ†ãƒ ')
      // }
      
      // åˆæœŸçŠ¶æ…‹ï¼šã™ã¹ã¦å±•é–‹
      collapsedItems.value = new Set()
      logger.debug('åˆæœŸè¡¨ç¤ºï¼šã™ã¹ã¦å±•é–‹çŠ¶æ…‹ã§é–‹å§‹')
    } catch (error) {
      logger.error('åˆæœŸåŒ–ã«å¤±æ•—:', error)
      collapsedItems.value = new Set()
    }
  }

  /**
   * LocalStorageã«æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã‚’ä¿å­˜
   */
  const saveCollapsedState = (): void => {
    try {
      const idsArray = Array.from(collapsedItems.value)
      localStorage.setItem(STORAGE_KEY, JSON.stringify(idsArray))
      logger.debug('æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã‚’ä¿å­˜:', idsArray.length, 'ã‚¢ã‚¤ãƒ†ãƒ ')
    } catch (error) {
      logger.error('æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã®ä¿å­˜ã«å¤±æ•—:', error)
    }
  }

  /**
   * æŒ‡å®šã•ã‚ŒãŸã‚¢ã‚¤ãƒ†ãƒ ãŒæŠ˜ã‚ŠãŸãŸã¾ã‚Œã¦ã„ã‚‹ã‹ãƒã‚§ãƒƒã‚¯
   */
  const isCollapsed = (itemId: string): boolean => {
    return collapsedItems.value.has(itemId)
  }

  /**
   * æŒ‡å®šã•ã‚ŒãŸã‚¢ã‚¤ãƒ†ãƒ ã®æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã‚’åˆ‡ã‚Šæ›¿ãˆ
   */
  const toggleCollapse = (itemId: string): void => {
    const newCollapsedItems = new Set(collapsedItems.value)
    
    if (newCollapsedItems.has(itemId)) {
      newCollapsedItems.delete(itemId)
      logger.debug(`ã‚¢ã‚¤ãƒ†ãƒ å±•é–‹: ${itemId}`)
    } else {
      newCollapsedItems.add(itemId)
      logger.debug(`ã‚¢ã‚¤ãƒ†ãƒ æŠ˜ã‚ŠãŸãŸã¿: ${itemId}`)
    }
    
    collapsedItems.value = newCollapsedItems
  }

  /**
   * æŒ‡å®šã•ã‚ŒãŸã‚¢ã‚¤ãƒ†ãƒ ã‚’æŠ˜ã‚ŠãŸãŸã¿
   */
  const collapseItem = (itemId: string): void => {
    if (!collapsedItems.value.has(itemId)) {
      const newCollapsedItems = new Set(collapsedItems.value)
      newCollapsedItems.add(itemId)
      collapsedItems.value = newCollapsedItems
      logger.debug(`ã‚¢ã‚¤ãƒ†ãƒ æŠ˜ã‚ŠãŸãŸã¿: ${itemId}`)
    }
  }

  /**
   * æŒ‡å®šã•ã‚ŒãŸã‚¢ã‚¤ãƒ†ãƒ ã‚’å±•é–‹
   */
  const expandItem = (itemId: string): void => {
    if (collapsedItems.value.has(itemId)) {
      const newCollapsedItems = new Set(collapsedItems.value)
      newCollapsedItems.delete(itemId)
      collapsedItems.value = newCollapsedItems
      logger.debug(`ã‚¢ã‚¤ãƒ†ãƒ å±•é–‹: ${itemId}`)
    }
  }

  /**
   * ã™ã¹ã¦ã®ã‚¢ã‚¤ãƒ†ãƒ ã‚’å±•é–‹
   */
  const expandAll = (): void => {
    const count = collapsedItems.value.size
    collapsedItems.value = new Set()
    logger.debug(`ã™ã¹ã¦ã®ã‚¢ã‚¤ãƒ†ãƒ ã‚’å±•é–‹: ${count}ã‚¢ã‚¤ãƒ†ãƒ `)
  }

  /**
   * ã™ã¹ã¦ã®ã‚¢ã‚¤ãƒ†ãƒ ã‚’æŠ˜ã‚ŠãŸãŸã¿
   */
  const collapseAll = (itemIds: string[]): void => {
    collapsedItems.value = new Set(itemIds)
    logger.debug(`ã™ã¹ã¦ã®ã‚¢ã‚¤ãƒ†ãƒ ã‚’æŠ˜ã‚ŠãŸãŸã¿: ${itemIds.length}ã‚¢ã‚¤ãƒ†ãƒ `)
  }

  /**
   * æŠ˜ã‚ŠãŸãŸã¾ã‚Œã¦ã„ã‚‹ã‚¢ã‚¤ãƒ†ãƒ æ•°ã‚’å–å¾—
   */
  const collapsedCount = computed(() => collapsedItems.value.size)

  /**
   * å±•é–‹ã•ã‚Œã¦ã„ã‚‹ã‚¢ã‚¤ãƒ†ãƒ æ•°ã‚’å–å¾—
   */
  const getExpandedCount = (totalItems: number): number => {
    return totalItems - collapsedItems.value.size
  }

  // æŠ˜ã‚ŠãŸãŸã¿çŠ¶æ…‹ã®å¤‰æ›´ã‚’ç›£è¦–ã—ã¦LocalStorageã«è‡ªå‹•ä¿å­˜
  watch(
    () => collapsedItems.value,
    () => {
      saveCollapsedState()
    },
    { deep: true }
  )

  // åˆæœŸåŒ–æ™‚ã«LocalStorageã‹ã‚‰çŠ¶æ…‹ã‚’å¾©å…ƒ
  loadCollapsedState()

  return {
    // çŠ¶æ…‹
    collapsedItems: computed(() => collapsedItems.value),
    collapsedCount,

    // ãƒ¡ã‚½ãƒƒãƒ‰
    isCollapsed,
    toggleCollapse,
    collapseItem,
    expandItem,
    expandAll,
    collapseAll,
    getExpandedCount,

    // ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£ãƒ¡ã‚½ãƒƒãƒ‰
    loadCollapsedState,
    saveCollapsedState,
  }
}