import type { Component, PartSection, ArrayItem, Part } from '@types'
import { logger } from '@shared/utils/logger'

// å…±é€šã®ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£é–¢æ•°ã‚’çµ±åˆ
export function useEditorUtils() {
  // å…¥åŠ›ã‚¿ã‚¤ãƒ—åˆ¤å®šï¼ˆçµ±åˆç‰ˆï¼‰
  const getInputType = (key: string): string => {
    if (key.includes('url') || key.includes('src') || key.includes('link')) return 'url'
    if (key.includes('email')) return 'email'
    if (key.includes('number') || key.includes('count')) return 'number'
    if (key.includes('date')) return 'date'
    if (key.includes('time')) return 'time'
    if (key.includes('password')) return 'password'
    if (key.includes('tel') || key.includes('phone')) return 'tel'
    return 'text'
  }

  // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆæ“ä½œ
  const componentUtils = {
    // éšŽå±¤ãƒ‘ã‚¹ã§ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’æ¤œç´¢
    findComponentByPath: (components: Component[], path: number[]): Component | null => {
      let current = components
      let target: Component | null = null

      for (let i = 0; i < path.length; i++) {
        const index = path[i]
        if (index >= 0 && index < current.length) {
          target = current[index]
          if (i < path.length - 1) {
            current = target.nested_parts || []
          }
        } else {
          return null
        }
      }

      return target
    },

    // æ–°è¦ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆä½œæˆ
    createNewComponent: (type: string, moduleName: string): Component => {
      const newId = `comp_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`

      return {
        id: newId,
        type,
        module_name: moduleName,
        nested_parts: [],
      }
    },

    // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå‰Šé™¤
    deleteComponent: (components: Component[], index: number): Component[] => {
      return components.filter((_, i) => i !== index)
    },

    // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆè¤‡è£½
    duplicateComponent: (component: Component): Component => {
      const newId = `comp_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`

      return {
        ...JSON.parse(JSON.stringify(component)),
        id: newId,
      }
    },

    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ è¿½åŠ 
    addArrayItem: (component: Component, arrayKey: string): void => {
      if (!component[arrayKey]) {
        component[arrayKey] = []
      }
      const arrayData = component[arrayKey] as any[]
      const newItem = { id: `item_${Date.now()}_${Math.random().toString(36).substring(2, 11)}` }
      arrayData.push(newItem)
    },

    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å‰Šé™¤
    removeArrayItem: (component: Component, arrayKey: string, index: number): void => {
      const arrayData = component[arrayKey] as any[]
      if (Array.isArray(arrayData) && index >= 0 && index < arrayData.length) {
        arrayData.splice(index, 1)
      }
    },
  }

  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆé–¢é€£
  const templateUtils = {
    // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå–å¾—
    getTemplate: (type: string, moduleName: string, parts: PartSection[]): string => {
      const section = parts.find(section => section.type === type)
      if (!section) return ''

      const part = section.module.find((part: Part) => part.title === moduleName)
      return part?.body || ''
    },

    // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå†…ã®ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰æŠ½å‡º
    extractFields: (template: string): string[] => {
      const fields: string[] = []
      const patterns = [
        /\{\$([^:}]+):/g, // {$field:default}
        /\(\$([^:}]+):/g, // ($field:option1|option2)
        /\(\$([^?}]+)\?:/g, // ($field?:option1|option2)
      ]

      patterns.forEach(pattern => {
        let match
        while ((match = pattern.exec(template)) !== null) {
          const fieldName = match[1]
          if (!fields.includes(fieldName)) {
            fields.push(fieldName)
          }
        }
      })

      return fields
    },

    // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå†…ã®ã‚¹ãƒ­ãƒƒãƒˆæŠ½å‡º
    extractSlots: (template: string): string[] => {
      // @slotè¨˜æ³•ã¯å»ƒæ­¢ã•ã‚ŒãŸãŸã‚ã€ç©ºé…åˆ—ã‚’è¿”ã™
      return []
    },
  }

  // é…åˆ—æ“ä½œ
  const arrayUtils = {
    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ è¿½åŠ 
    addArrayItem: (array: ArrayItem[], newItem: ArrayItem): ArrayItem[] => {
      return [...array, newItem]
    },

    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å‰Šé™¤
    removeArrayItem: (array: ArrayItem[], index: number): ArrayItem[] => {
      return array.filter((_, i) => i !== index)
    },

    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ æ›´æ–°
    updateArrayItem: (array: ArrayItem[], index: number, updatedItem: ArrayItem): ArrayItem[] => {
      return array.map((item, i) => (i === index ? updatedItem : item))
    },

    // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ç§»å‹•
    moveArrayItem: (array: ArrayItem[], fromIndex: number, toIndex: number): ArrayItem[] => {
      const result = [...array]
      const [removed] = result.splice(fromIndex, 1)
      result.splice(toIndex, 0, removed)
      return result
    },
  }

  // ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰æ“ä½œ
  const fieldUtils = {
    // ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰å€¤å–å¾—
    getFieldValue: (component: Component, fieldName: string): any => {
      return component[fieldName] || ''
    },

    // ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰å€¤è¨­å®š
    setFieldValue: (component: Component, fieldName: string, value: any): Component => {
      return {
        ...component,
        [fieldName]: value,
      }
    },

    // ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰å€¤æ¤œè¨¼
    validateField: (fieldName: string, value: any): boolean => {
      const inputType = getInputType(fieldName)

      switch (inputType) {
        case 'email':
          return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)
        case 'url':
          try {
            new URL(value)
            return true
          } catch {
            return false
          }
        case 'number':
          return !isNaN(Number(value))
        default:
          return true
      }
    },
  }

  // æ–‡å­—åˆ—æ“ä½œ
  const stringUtils = {
    // éšŽå±¤ãƒ‘ã‚¹ã‚’æ–‡å­—åˆ—ã«å¤‰æ›
    stringifyHierarchyPath: (path: number[]): string => {
      return path.join('.')
    },

    // æ–‡å­—åˆ—ã‚’éšŽå±¤ãƒ‘ã‚¹ã«å¤‰æ›ï¼ˆã‚¨ãƒ©ãƒ¼ãƒãƒ³ãƒ‰ãƒªãƒ³ã‚°å¼·åŒ–ï¼‰
    parseHierarchyPath: (pathString: string): number[] => {
      if (!pathString || typeof pathString !== 'string') {
        logger.warn('Invalid pathString provided:', pathString)
        return []
      }

      try {
        const parts = pathString.split('.')
        const result: number[] = []

        for (const part of parts) {
          const trimmedPart = part.trim()
          if (trimmedPart === '') continue // ç©ºæ–‡å­—ã‚’ã‚¹ã‚­ãƒƒãƒ—

          const num = parseInt(trimmedPart, 10)
          if (!isNaN(num) && num >= 0) {
            result.push(num)
          } else {
            logger.error(`Invalid path component: "${part}" in pathString: "${pathString}"`)
            return [] // ä¸æ­£ãªå½¢å¼ã®å ´åˆã¯ç©ºé…åˆ—ã‚’è¿”ã™
          }
        }

        return result
      } catch (error) {
        logger.error('Error parsing hierarchy path:', pathString, error)
        return []
      }
    },

    // ã‚­ãƒ£ãƒ¡ãƒ«ã‚±ãƒ¼ã‚¹ã‚’ã‚±ãƒãƒ–ã‚±ãƒ¼ã‚¹ã«å¤‰æ›
    camelToKebab: (str: string): string => {
      return str.replace(/([a-z0-9])([A-Z])/g, '$1-$2').toLowerCase()
    },

    // ã‚±ãƒãƒ–ã‚±ãƒ¼ã‚¹ã‚’ã‚­ãƒ£ãƒ¡ãƒ«ã‚±ãƒ¼ã‚¹ã«å¤‰æ›
    kebabToCamel: (str: string): string => {
      return str.replace(/-([a-z])/g, (_match, letter) => letter.toUpperCase())
    },
  }

  // ãƒ•ã‚¡ã‚¤ãƒ«ã‚µã‚¤ã‚ºãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes'

    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))

    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  return {
    // åŸºæœ¬ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£
    getInputType,
    formatFileSize,

    // å„ç¨®æ“ä½œ
    component: componentUtils,
    template: templateUtils,
    array: arrayUtils,
    field: fieldUtils,
    string: stringUtils,
  }
}
