<template>
  <div class="zero-code-array-field">
    <div class="zero-code-array-header">
      <h5>{{ label }}</h5>
      <button class="zero-code-btn zero-code-add-array-item" @click="$emit('add')">+ è¿½åŠ </button>
    </div>

    <!-- ç©ºé…åˆ—ã®å ´åˆã®ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã¨è¿½åŠ ãƒœã‚¿ãƒ³ -->
    <div v-if="items.length === 0" class="zero-code-empty-array-message">
      <div class="zero-code-empty-message-content">
        <p class="zero-code-empty-text">{{ label }}ã¯ã¾ã è¿½åŠ ã•ã‚Œã¦ã„ã¾ã›ã‚“</p>
        <button class="zero-code-btn zero-code-add-array-item-empty" @click="$emit('add')">
          <span class="zero-code-plus-icon">ï¼‹</span>
          <span class="zero-code-button-text">æœ€åˆã®{{ label }}ã‚’è¿½åŠ </span>
        </button>
      </div>
    </div>

    <div
      v-for="(item, index) in items"
      :key="(item as any)?.id || index"
      class="zero-code-array-item"
      draggable="true"
      @dragstart="handleDragStart($event, index)"
      @dragover="handleDragOver"
      @drop="handleDrop($event, index)"
      @dragend="handleDragEnd"
    >
      <div class="zero-code-array-item-header">
        <div class="zero-code-component-meta">
          <span class="zero-code-drag-handle">â ¿</span>
          <h6>{{ itemLabel }} {{ index + 1 }}</h6>
        </div>
        <button class="zero-code-btn zero-code-remove-array-item" @click="$emit('remove', index)">å‰Šé™¤</button>
      </div>

      <div class="zero-code-array-item-fields">
        <template v-for="(field, fieldKey) in fields" :key="fieldKey">
          <div class="zero-code-field-group">
            <label :for="`${componentId}_${arrayKey}_${index}_${fieldKey}`">
              {{ field.label }}:
            </label>

            <!-- ãƒ©ã‚¸ã‚ªãƒœã‚¿ãƒ³ï¼ˆé¸æŠžè¨˜æ³•ï¼‰ -->
            <div
              v-if="field.type === 'radio' && field.options && field.options.length > 0"
              class="zero-code-radio-group"
            >
              <div v-for="option in field.options" :key="option" class="zero-code-radio-item">
                <input
                  :id="`${componentId}_${arrayKey}_${index}_${fieldKey}_${option}`"
                  :value="option"
                  type="radio"
                  :name="`${componentId}_${arrayKey}_${index}_${fieldKey}`"
                  class="zero-code-radio-input"
                  :checked="
                    (item as any)[fieldKey] === option ||
                      (option === 'æŒ‡å®šãªã—' && !(item as any)[fieldKey])
                  "
                  @change="updateValue(index, fieldKey, option === 'æŒ‡å®šãªã—' ? '' : option)"
                >
                <label
                  :for="`${componentId}_${arrayKey}_${index}_${fieldKey}_${option}`"
                  class="zero-code-radio-label"
                >
                  {{ option }}
                </label>
              </div>
            </div>

            <!-- ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ï¼ˆæ¡ä»¶ä»˜ãè¨˜æ³•ï¼‰ -->
            <div
              v-if="field.type === 'checkbox' && field.options && field.options.length > 0"
              class="zero-code-checkbox-group"
            >
              <div v-for="option in field.options" :key="option" class="zero-code-checkbox-item">
                <input
                  :id="`${componentId}_${arrayKey}_${index}_${fieldKey}_${option}`"
                  :checked="isArrayItemCheckboxChecked(item, fieldKey, option)"
                  type="checkbox"
                  class="zero-code-checkbox-input"
                  @change="
                    (e: Event) => {
                      const target = e.target as HTMLInputElement
                      updateArrayItemCheckboxValue(index, fieldKey, option, target.checked)
                    }
                  "
                >
                <label
                  :for="`${componentId}_${arrayKey}_${index}_${fieldKey}_${option}`"
                  class="zero-code-checkbox-label"
                >
                  {{ option }}
                </label>
              </div>
            </div>

            <!-- ã‚»ãƒ¬ã‚¯ãƒˆãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ï¼ˆé€šå¸¸ã®selectç”¨ï¼‰ -->
            <select
              v-else-if="field.type === 'select'"
              :id="`${componentId}_${arrayKey}_${index}_${fieldKey}`"
              :value="(item as any)[fieldKey] as string"
              class="zero-code-field-input zero-code-select-input"
              @input="
                (e: Event) => {
                  const target = e.target as HTMLSelectElement
                  updateValue(index, fieldKey, target.value)
                }
              "
            >
              <option v-for="option in field.options" :key="option" :value="option">
                {{ option }}
              </option>
            </select>

            <!-- ã‚»ãƒ¬ã‚¯ãƒˆãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ï¼ˆè¤‡æ•°é¸æŠžç”¨ï¼‰ -->
            <select
              v-else-if="field.type === 'select-multiple'"
              :id="`${componentId}_${arrayKey}_${index}_${fieldKey}`"
              :value="(item as any)[fieldKey] || field.defaultValue"
              multiple
              class="zero-code-field-input zero-code-select-multiple"
              @change="
                (e: Event) => {
                  const target = e.target as HTMLSelectElement
                  const selectedValues = Array.from(target.selectedOptions).map(
                    option => option.value
                  )
                  updateValue(index, fieldKey, selectedValues)
                }
              "
            >
              <option
                v-for="option in field.options"
                :key="option"
                :value="option"
                :selected="
                  Array.isArray((item as any)[fieldKey]) && (item as any)[fieldKey].includes(option)
                "
              >
                {{ option }}
              </option>
            </select>

            <!-- ãƒ†ã‚­ã‚¹ãƒˆã‚¨ãƒªã‚¢ -->
            <textarea
              v-else-if="field.type === 'textarea'"
              :id="`${componentId}_${arrayKey}_${index}_${fieldKey}`"
              :value="(item as any)[fieldKey] as string"
              class="zero-code-field-input zero-code-field-textarea"
              @input="
                (e: Event) => {
                  const target = e.target as HTMLTextAreaElement
                  updateValue(index, fieldKey, target.value)
                }
              "
            />

            <!-- ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã®å…¥åŠ›ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ -->
            <input
              v-else-if="field.type === 'text' || field.type === 'number'"
              :id="`${componentId}_${arrayKey}_${index}_${fieldKey}`"
              :value="(item as any)[fieldKey] as string"
              :type="field.type === 'text' ? 'text' : field.type"
              class="zero-code-field-input"
              @input="
                (e: Event) => {
                  const target = e.target as HTMLInputElement
                  updateValue(index, fieldKey, target.value)
                }
              "
            >
          </div>
        </template>

        <!-- ãƒã‚¹ãƒˆã—ãŸé…åˆ—ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®è¡¨ç¤º -->
        <template v-for="(value, key) in item" :key="`nested_${key}`">
          <div v-if="Array.isArray(value) && key !== 'id' && shouldShowNestedArray(key, value)" class="zero-code-nested-array-field">
            <h6 class="zero-code-nested-array-label">{{ key }} (ãƒã‚¹ãƒˆé…åˆ—)</h6>
            <div class="zero-code-nested-array-info">
              <span class="zero-code-array-count">{{ value.length }}å€‹ã®è¦ç´ </span>
              <button 
                class="zero-code-btn zero-code-edit-nested-array-btn" 
                title="ãƒã‚¹ãƒˆé…åˆ—ã‚’ç·¨é›†"
                @click="$emit('edit-nested', index, key)"
              >
                ç·¨é›†
              </button>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  interface Field {
    label: string
    type: 'text' | 'select' | 'textarea' | 'number' | 'radio' | 'checkbox' | 'select-multiple'
    options?: string[]
    defaultValue?: any
  }

  interface Props {
    componentId: string
    arrayKey: string
    items: Record<string, unknown>[]
    fields: Record<string, Field>
    label: string
    itemLabel?: string
  }

  const _props = defineProps<Props>()

  const emit = defineEmits<{
    (e: 'add'): void
    (e: 'remove', index: number): void
    (e: 'update', index: number, key: string, value: string | number | string[]): void
    (e: 'reorder', fromIndex: number, toIndex: number): void
    (e: 'edit-nested', index: number, key: string): void
  }>()

  const updateValue = (index: number, key: string, value: string | number | string[]): void => {
    emit('update', index, key, value)
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹ãŒãƒã‚§ãƒƒã‚¯ã•ã‚Œã¦ã„ã‚‹ã‹ã©ã†ã‹
  const isArrayItemCheckboxChecked = (item: any, fieldKey: string, option: string): boolean => {
    const fieldValue = item[fieldKey]
    return Array.isArray(fieldValue) && fieldValue.includes(option)
  }

  // é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ ã®ãƒã‚§ãƒƒã‚¯ãƒœãƒƒã‚¯ã‚¹å€¤ã‚’æ›´æ–°
  const updateArrayItemCheckboxValue = (
    index: number,
    fieldKey: string,
    option: string,
    checked: boolean
  ): void => {
    const currentItem = _props.items[index] as any
    let currentValue = currentItem[fieldKey] || []

    if (!Array.isArray(currentValue)) {
      currentValue = []
    }

    if (checked) {
      if (!currentValue.includes(option)) {
        currentValue = [...currentValue, option]
      }
    } else {
      currentValue = currentValue.filter((item: string) => item !== option)
    }

    emit('update', index, fieldKey, currentValue as string[])
  }

  // ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—é–¢é€£ã®çŠ¶æ…‹
  let draggedIndex: number | null = null

  // ãƒ‰ãƒ©ãƒƒã‚°é–‹å§‹
  const handleDragStart = (e: DragEvent, index: number): void => {
    draggedIndex = index
    if (e.dataTransfer) {
      e.dataTransfer.effectAllowed = 'move'
      e.dataTransfer.setData('text/plain', index.toString())
    }

    // è¦–è¦šçš„ãƒ•ã‚£ãƒ¼ãƒ‰ãƒãƒƒã‚¯
    const target = e.target as HTMLElement
    target.style.opacity = '0.5'
  }

  // ãƒ‰ãƒ©ãƒƒã‚°ã‚ªãƒ¼ãƒãƒ¼
  const handleDragOver = (e: DragEvent): void => {
    e.preventDefault()
    if (e.dataTransfer) {
      e.dataTransfer.dropEffect = 'move'
    }
  }

  // ãƒ‰ãƒ­ãƒƒãƒ—
  const handleDrop = (e: DragEvent, targetIndex: number): void => {
    e.preventDefault()

    if (draggedIndex !== null && draggedIndex !== targetIndex) {
      emit('reorder', draggedIndex, targetIndex)
    }

    draggedIndex = null
  }

  // ãƒ‰ãƒ©ãƒƒã‚°çµ‚äº†
  const handleDragEnd = (e: DragEvent): void => {
    // è¦–è¦šçš„ãƒ•ã‚£ãƒ¼ãƒ‰ãƒãƒƒã‚¯ã‚’ãƒªã‚»ãƒƒãƒˆ
    const target = e.target as HTMLElement
    target.style.opacity = '1'
    draggedIndex = null
  }

  // ãƒã‚¹ãƒˆã—ãŸé…åˆ—ã‚’è¡¨ç¤ºã™ã‚‹ã‹ã©ã†ã‹ã‚’åˆ¤å®š
  const shouldShowNestedArray = (key: string, value: unknown[]): boolean => {
    // åŸºæœ¬çš„ãªæ¡ä»¶ãƒã‚§ãƒƒã‚¯
    if (!Array.isArray(value) || key === 'id') return false
    
    // ã‚·ã‚¹ãƒ†ãƒ å†…éƒ¨ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã¯é™¤å¤–
    const systemFields = ['nested_parts', 'content']
    if (systemFields.includes(key)) return false
    
    // å°‘ãªãã¨ã‚‚1ã¤ã®è¦ç´ ãŒã‚ã‚Šã€ãã®è¦ç´ ãŒã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆã®å ´åˆã«ãƒã‚¹ãƒˆé…åˆ—ã¨ã—ã¦èªè­˜
    return value.length > 0 && typeof value[0] === 'object' && value[0] !== null
  }
</script>


