<template>
  <div class="zero-code-modal-overlay" @click="$emit('close')">
    <div class="zero-code-modal-content" @click.stop>
      <div class="zero-code-modal-header">
        <h4>ç”»åƒã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰ï¼ˆ{{ category === 'common' ? 'å…±é€š' : 'å€‹åˆ¥' }}ï¼‰</h4>
        <button class="zero-code-img-close-btn" @click="$emit('close')">&times;</button>
      </div>

      <div class="zero-code-modal-body">
        <div class="zero-code-img-file-input-section">
          <label for="file-input" class="zero-code-img-file-input-label">
            <div class="zero-code-img-file-input-area">
              <span class="zero-code-img-file-input-text">ã‚¯ãƒªãƒƒã‚¯ã—ã¦ãƒ•ã‚¡ã‚¤ãƒ«ã‚’é¸æŠž</span>
              <span class="zero-code-img-file-input-hint">ã¾ãŸã¯ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—</span>
            </div>
          </label>
          <input
            id="file-input"
            type="file"
            accept="image/*"
            style="display: none"
            @change="onFileChange"
            @drop="onFileDrop"
            @dragover.prevent
            @dragenter.prevent
          >
        </div>

        <div v-if="selectedFile" class="zero-code-img-file-info">
          <div class="zero-code-img-file-preview">
            <img :src="previewUrl" alt="ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼">
          </div>
          <div class="zero-code-img-file-details">
            <p><strong>ãƒ•ã‚¡ã‚¤ãƒ«å:</strong> {{ selectedFile.name }}</p>
            <p><strong>ã‚µã‚¤ã‚º:</strong> {{ formatFileSize(selectedFile.size) }}</p>
            <p><strong>ã‚¿ã‚¤ãƒ—:</strong> {{ selectedFile.type }}</p>
          </div>
        </div>

        <div class="zero-code-form-section">
          <div class="zero-code-form-group">
            <label for="image-name">ä¿å­˜å:</label>
            <input
              id="image-name"
              v-model="imageName"
              type="text"
              placeholder="ç”»åƒã®ä¿å­˜åã‚’å…¥åŠ›"
              :disabled="!selectedFile"
            >
          </div>

          <div class="zero-code-form-group">
            <label for="image-alt">altå±žæ€§:</label>
            <input
              id="image-alt"
              v-model="imageAlt"
              type="text"
              placeholder="ç”»åƒã®èª¬æ˜Žã‚’å…¥åŠ›"
              :disabled="!selectedFile"
            >
          </div>

          <div v-if="category === 'individual'" class="zero-code-form-group">
            <label for="component-id">ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆID:</label>
            <input
              id="component-id"
              v-model="componentId"
              type="text"
              placeholder="ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆIDã‚’å…¥åŠ›"
              :disabled="!selectedFile"
            >
          </div>
        </div>
      </div>

      <div class="zero-code-modal-footer">
        <button class="zero-code-img-btn zero-code-img-btn-secondary" @click="$emit('close')">ã‚­ãƒ£ãƒ³ã‚»ãƒ«</button>
        <button class="zero-code-img-btn zero-code-img-btn-primary" :disabled="!canUpload" @click="upload">
          ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰
        </button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'
  import type { ImageData } from '@types'

  // Propså®šç¾©
  interface Props {
    category: 'common' | 'individual'
    componentId?: string
  }

  const props = defineProps<Props>()

  // Emitså®šç¾©
  const emit = defineEmits<{
    close: []
    upload: [imageData: ImageData]
  }>()

  // çŠ¶æ…‹ç®¡ç†
  const selectedFile = ref<File | null>(null)
  const previewUrl = ref<string>('')
  const imageName = ref<string>('')
  const imageAlt = ref<string>('')
  const componentId = ref<string>(props.componentId || '')

  // ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰å¯èƒ½ã‹ã©ã†ã‹
  const canUpload = computed(() => {
    return selectedFile.value && imageName.value.trim() && imageAlt.value.trim()
  })

  // ãƒ•ã‚¡ã‚¤ãƒ«é¸æŠžå‡¦ç†
  const onFileChange = (event: Event) => {
    const target = event.target as HTMLInputElement
    if (target.files && target.files[0]) {
      handleFileSelect(target.files[0])
    }
  }

  // ãƒ‰ãƒ©ãƒƒã‚°&ãƒ‰ãƒ­ãƒƒãƒ—å‡¦ç†
  const onFileDrop = (event: DragEvent) => {
    event.preventDefault()
    if (event.dataTransfer && event.dataTransfer.files[0]) {
      handleFileSelect(event.dataTransfer.files[0])
    }
  }

  // ãƒ•ã‚¡ã‚¤ãƒ«é¸æŠžã®å…±é€šå‡¦ç†
  const handleFileSelect = (file: File) => {
    selectedFile.value = file
    imageName.value = file.name.replace(/\.[^/.]+$/, '') // æ‹¡å¼µå­ã‚’é™¤ã„ãŸãƒ•ã‚¡ã‚¤ãƒ«å
    imageAlt.value = file.name

    // ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼URLã‚’ä½œæˆ
    if (previewUrl.value) {
      URL.revokeObjectURL(previewUrl.value)
    }
    previewUrl.value = URL.createObjectURL(file)
  }

  // ãƒ•ã‚¡ã‚¤ãƒ«ã‚’Base64ã«å¤‰æ›
  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => resolve(reader.result as string)
      reader.onerror = error => reject(error)
    })
  }

  // ãƒ¦ãƒ‹ãƒ¼ã‚¯IDç”Ÿæˆ
  const generateUniqueId = (): string => {
    return Date.now().toString(36) + Math.random().toString(36).substr(2)
  }

  // ãƒ•ã‚¡ã‚¤ãƒ«ã‚µã‚¤ã‚ºã®ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  // ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰å‡¦ç†
  const upload = async () => {
    if (!selectedFile.value || !canUpload.value) return

    try {
      // Base64ã«å¤‰æ›
      const base64 = await fileToBase64(selectedFile.value)

      // Base64ãƒ‡ãƒ¼ã‚¿ã‹ã‚‰data:ãƒ—ãƒ¬ãƒ•ã‚£ãƒƒã‚¯ã‚¹ã‚’é™¤åŽ»ã—ã¦ãƒ™ãƒ¼ã‚¹ãƒ‡ãƒ¼ã‚¿ã®ã¿å–å¾—
      const base64WithoutPrefix = base64.split(',')[1] || base64

      // ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’æ§‹ç¯‰
      const imageData: ImageData = {
        id: generateUniqueId(),
        name: imageName.value,
        originalName: selectedFile.value.name,
        url: `/img/${imageName.value}`, // ä¸€èˆ¬çš„ãªURLå½¢å¼ã‚’ä½¿ç”¨
        alt: imageAlt.value,
        size: selectedFile.value.size,
        type: selectedFile.value.type,
        uploadedAt: new Date().toISOString(),
        category: props.category,
        componentId: props.category === 'individual' ? componentId.value : undefined,
        fileData: {
          base64: base64WithoutPrefix, // data:ãƒ—ãƒ¬ãƒ•ã‚£ãƒƒã‚¯ã‚¹ãªã—ã®Base64ãƒ‡ãƒ¼ã‚¿ã®ã¿
        },
      }

      emit('upload', imageData)
    } catch (error) {
      alert('ç”»åƒã®ã‚¢ãƒƒãƒ—ãƒ­ãƒ¼ãƒ‰ã«å¤±æ•—ã—ã¾ã—ãŸã€‚')
    }
  }

  // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã®ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—
  const cleanup = () => {
    if (previewUrl.value) {
      URL.revokeObjectURL(previewUrl.value)
    }
  }

  // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã®ã‚¢ãƒ³ãƒžã‚¦ãƒ³ãƒˆæ™‚ã«ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—
  import { onUnmounted } from 'vue'
  onUnmounted(cleanup)
</script>
