<template>
  <div class="zero-code-page-preview">
    <div class="zero-code-page-preview-content">
      <!-- eslint-disable-next-line vue/no-v-html -->
      <div v-html="renderedPageHtml" />
    </div>
  </div>
</template>

<script setup lang="ts">
  import { computed } from 'vue'
  import type { Component, PartSection, Part } from '@types'
  import { useTemplateProcessor } from '../../composables/useTemplateProcessor'
  import { useImageDisplay } from '../../composables/useImageDisplay'

  // Propså®šç¾©
  interface Props {
    pageData: Component[]
    parts: PartSection[]
    commonImages: any[]
    individualImages: any[]
  }

  const props = defineProps<Props>()
  
  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆå‡¦ç†ç”¨ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
  const templateProcessor = useTemplateProcessor()
  
  // ç”»åƒè¡¨ç¤ºç”¨ã‚³ãƒ³ãƒãƒ¼ã‚¶ãƒ–ãƒ«
  const { getImageSrc } = useImageDisplay()

  // å…¨ç”»åƒãƒ‡ãƒ¼ã‚¿ã®çµ±åˆ
  const allImages = computed(() => [...props.commonImages, ...props.individualImages])

  // ç”»åƒURLã‚’è§£æ±ºã™ã‚‹é–¢æ•°
  const resolveImageUrls = (html: string): string => {
    if (!html || allImages.value.length === 0) return html

    let processedHtml = html
    
    // imgè¦ç´ ã®srcå±žæ€§ã‚’ç½®æ›
    processedHtml = processedHtml.replace(
      /<img([^>]*?)src=["']([^"']+)["']([^>]*?)>/gi,
      (match, beforeSrc, originalSrc, afterSrc) => {
        // ç”»åƒIDã¾ãŸã¯URLã‚’ä½¿ã£ã¦ç”»åƒãƒ‡ãƒ¼ã‚¿ã‚’æ¤œç´¢
        const imageData = allImages.value.find(img => {
          // å®Œå…¨ä¸€è‡´ãƒã‚§ãƒƒã‚¯
          if (img.id === originalSrc || img.url === originalSrc || img.name === originalSrc) {
            return true
          }
          
          // URLãƒ‘ã‚¹ã®ä¸€è‡´ãƒã‚§ãƒƒã‚¯ï¼ˆä¾‹: /img/hero_image.jpg ã¨ hero_image.jpgï¼‰
          if (originalSrc.endsWith('/' + img.name) || img.url.endsWith(originalSrc)) {
            return true
          }
          
          // ãƒ•ã‚¡ã‚¤ãƒ«åéƒ¨åˆ†ã®ãƒžãƒƒãƒãƒ³ã‚°
          const originalFileName = originalSrc.split('/').pop()
          if (originalFileName === img.name) {
            return true
          }
          
          return false
        })
        
        if (imageData) {
          const resolvedSrc = getImageSrc(imageData)
          return `<img${beforeSrc}src="${resolvedSrc}"${afterSrc}>`
        }
        
        return match
      }
    )

    return processedHtml
  }

  // ç”»åƒãƒ‡ãƒ¼ã‚¿ã®å¤‰æ›´ã‚’ç›£è¦–ï¼ˆè¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã§è‡ªå‹•çš„ã«å†ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ã•ã‚Œã‚‹ãŸã‚ã€ç›£è¦–ã¯å‰Šé™¤ï¼‰

  // ãƒ­ãƒ¼ã‚«ãƒ«çŠ¶æ…‹ï¼ˆå‰Šé™¤æ¸ˆã¿ï¼‰

  // HTMLã‚’ç¾Žã—ããƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆã™ã‚‹é–¢æ•°
  const formatHtmlSource = (html: string): string => {
    if (!html) return ''

    try {
      // DOMParserã§HTMLã‚’æ­£ç¢ºã«è§£æž
      const parser = new (globalThis as any).DOMParser()
      const doc = parser.parseFromString(`<root>${html}</root>`, 'text/html')
      const rootElement = doc.body.firstChild as Element

      if (!rootElement) return html

      // è‡ªå·±é–‰ã˜ã‚¿ã‚°ä¸€è¦§
      const voidElements = new Set([
        'img',
        'br',
        'hr',
        'input',
        'meta',
        'link',
        'area',
        'base',
        'col',
        'embed',
        'source',
        'track',
        'wbr',
      ])

      // ã‚¤ãƒ³ãƒ©ã‚¤ãƒ³è¦ç´ ä¸€è¦§ï¼ˆæ”¹è¡Œã‚’å…¥ã‚Œãªã„è¦ç´ ï¼‰
      const inlineElements = new Set([
        'span',
        'a',
        'strong',
        'em',
        'u',
        'mark',
        'code',
        'small',
        'sub',
        'sup',
        'i',
        'b',
      ])

      // å†å¸°çš„ã«HTMLã‚’ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
      const formatElement = (
        element: Element,
        depth: number = 0,
        parentIsInline: boolean = false
      ): string => {
        const tagName = element.tagName.toLowerCase()
        const indent = parentIsInline ? '' : '  '.repeat(depth)
        const isVoidElement = voidElements.has(tagName)
        const isInlineElement = inlineElements.has(tagName)

        // é–‹å§‹ã‚¿ã‚°ã‚’ä½œæˆ
        let startTag = `<${tagName}`

        // å±žæ€§ã‚’è¿½åŠ 
        for (let i = 0; i < element.attributes.length; i++) {
          const attr = element.attributes[i]
          startTag += ` ${attr.name}="${attr.value}"`
        }
        startTag += '>'

        let result = indent + startTag

        // è‡ªå·±é–‰ã˜ã‚¿ã‚°ã®å ´åˆã¯çµ‚äº†
        if (isVoidElement) {
          return result
        }

        // å­è¦ç´ ã‚’å‡¦ç†
        let hasElementChildren = false
        let hasTextContent = false
        const childrenResults: string[] = []
        let textContent = ''

        for (let i = 0; i < element.childNodes.length; i++) {
          const child = element.childNodes[i]

          if (child.nodeType === 1) {
            // Element node
            hasElementChildren = true
            const childElement = child as Element
            const childResult = formatElement(
              childElement,
              depth + 1,
              isInlineElement || parentIsInline
            )
            childrenResults.push(childResult)
          } else if (child.nodeType === 3) {
            // Text node
            const text = child.textContent?.replace(/\s+/g, ' ').trim()
            if (text) {
              hasTextContent = true
              textContent += text
            }
          }
        }

        // å†…å®¹ã®å‡¦ç†
        if (hasElementChildren && hasTextContent) {
          // ãƒ†ã‚­ã‚¹ãƒˆã¨è¦ç´ ã®ä¸¡æ–¹ãŒã‚ã‚‹å ´åˆï¼ˆã‚¤ãƒ³ãƒ©ã‚¤ãƒ³ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ï¼‰
          if (isInlineElement || parentIsInline) {
            result += textContent + childrenResults.join('')
          } else {
            result += textContent + '\n' + childrenResults.join('\n')
            if (childrenResults.length > 0) result += '\n' + indent
          }
        } else if (hasElementChildren) {
          // è¦ç´ ã®ã¿ã®å ´åˆ
          if (isInlineElement || parentIsInline) {
            result += childrenResults.join('')
          } else {
            result += '\n' + childrenResults.join('\n')
            if (childrenResults.length > 0) result += '\n' + indent
          }
        } else if (hasTextContent) {
          // ãƒ†ã‚­ã‚¹ãƒˆã®ã¿ã®å ´åˆ
          result += textContent
        }

        // çµ‚äº†ã‚¿ã‚°ã‚’è¿½åŠ 
        result += `</${tagName}>`

        return result
      }

      // rootElementã®å­è¦ç´ ã‚’ãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
      let formattedHtml = ''
      for (let i = 0; i < rootElement.childNodes.length; i++) {
        const child = rootElement.childNodes[i]
        if (child.nodeType === 1) {
          // Element node
          formattedHtml += formatElement(child as Element) + '\n'
        }
      }

      return formattedHtml
        .replace(/\n{3,}/g, '\n\n') // 3å›žä»¥ä¸Šã®é€£ç¶šæ”¹è¡Œã‚’2å›žã«åˆ¶é™
        .replace(/\n\s*\n\s*</g, '\n<') // ç©ºè¡Œã¨è¦ç´ é–“ã®ç„¡é§„ãªæ”¹è¡Œã‚’é™¤åŽ»
        .trim()
    } catch (error) {
      // ãƒ•ã‚©ãƒ¼ãƒ«ãƒãƒƒã‚¯: ã‚ˆã‚Šç°¡æ½”ãªãƒ•ã‚©ãƒ¼ãƒžãƒƒãƒˆ
      return html
        .replace(/>\s*</g, '>\n<')
        .split('\n')
        .filter(line => line.trim()) // ç©ºè¡Œã‚’é™¤åŽ»
        .map((line, index, array) => {
          const trimmed = line.trim()
          if (!trimmed) return ''

          let depth = 0
          for (let i = 0; i < index; i++) {
            const prevLine = array[i].trim()
            if (prevLine.match(/<[^\/!][^>]*[^\/]>$/)) depth++
            if (prevLine.match(/<\/[^>]+>$/)) depth--
          }
          if (trimmed.startsWith('</')) depth--

          return '  '.repeat(Math.max(0, depth)) + trimmed
        })
        .join('\n')
    }
  }

  // ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆã‚’å–å¾—ã™ã‚‹é–¢æ•°
  const getTemplate = (type: string, moduleName: string): string => {
    if (!Array.isArray(props.parts)) return ''

    const section = props.parts.find((s: PartSection) => s.type === type)
    if (!section) return ''

    const module = section.module.find((m: Part) => m.title === moduleName)
    return module ? module.body : ''
  }

  // ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼å°‚ç”¨ï¼šç·¨é›†UIã‚’å«ã¾ãªã„ã‚¯ãƒªãƒ¼ãƒ³ãªHTMLã‚’ç”Ÿæˆï¼ˆè¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ï¼‰
  const renderedPageHtml = computed((): string => {
    if (!Array.isArray(props.pageData) || props.pageData.length === 0) {
      return '<div class="zero-code-no-content">ãƒšãƒ¼ã‚¸ã«ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ãŒã‚ã‚Šã¾ã›ã‚“</div>'
    }

    let pageHtml = '<div class="zero-code-page-container">'

    props.pageData.forEach((pageComponent) => {
      const componentHtml = renderCleanComponent(pageComponent)

      if (!componentHtml || componentHtml.trim() === '') {
        pageHtml += `<div class="zero-code-error-component">ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“: ${pageComponent.type}/${pageComponent.module_name}</div>`
      } else {
        pageHtml += componentHtml
      }
    })

    pageHtml += '</div>'
    
    return pageHtml
  })

  // ã‚¯ãƒªãƒ¼ãƒ³ãªã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ï¼ˆç·¨é›†UIè¦ç´ ãªã—ï¼‰
  const renderCleanComponent = (component: Component): string => {
    // ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼ãƒ¢ãƒ¼ãƒ‰ã‚’ä½¿ç”¨ï¼ˆç·¨é›†UIè¦ç´ ãªã—ã®ã‚¯ãƒªãƒ¼ãƒ³ãªãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ï¼‰
    const html = templateProcessor.renderComponentOptimized(component, getTemplate, { isPreviewMode: true })
    // ç”»åƒURLã‚’è§£æ±º
    return resolveImageUrls(html)
  }

  // ãƒã‚¹ãƒˆã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã®ã‚¯ãƒªãƒ¼ãƒ³ãªãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ï¼ˆç·¨é›†UIè¦ç´ ãªã—ï¼‰
  const renderNestedComponentsClean = (components: Component[]): string => {
    if (!components || components.length === 0) {
      return ''
    }

    let html = ''

    components.forEach((child: Component) => {
      const componentHtml = renderCleanComponent(child)

      if (componentHtml && componentHtml.trim() !== '') {
        html += componentHtml
      }
    })

    return html
  }

  // æ—§ç‰ˆï¼ˆç·¨é›†ç”¨UIã‚‚å«ã‚€ç‰ˆï¼‰- å¾Œæ–¹äº’æ›æ€§ã®ãŸã‚æ®‹ã™
  const renderNestedComponentsFromParts = (components: unknown[]): string => {
    if (!components || components.length === 0) {
      return ''
    }

    let html = ''

    components.forEach((child: unknown) => {
      const childComponent = child as Component
      const componentHtml = renderCleanComponent(childComponent)

      if (componentHtml && componentHtml.trim() !== '') {
        html += componentHtml
      }
    })

    return html
  }
</script>
