import { reactive, computed, toRefs, watch } from 'vue'
import type { EditorEventBusType } from '@shared/utils/eventBus'
import type { Component, PartSection, DataTabType, TabType, EditingTextState } from '@types'

// ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼ä¸­æ ¸çŠ¶æ…‹
interface EditorCoreState {
  // ãƒ‡ãƒ¼ã‚¿ç®¡ç†
  editablePageData: Component[]
  localParts: PartSection[]
  localCommonParts: PartSection[]
  localIndividualParts: PartSection[]

  // UIçŠ¶æ…‹
  activeTab: TabType
  dataTab: DataTabType
  showEditor: boolean
  showPartSelector: boolean
  showNestedPartSelector: boolean

  // ç·¨é›†çŠ¶æ…‹
  selectedComponent: Component | null
  selectedComponentIndex: number | string | null
  editingText: EditingTextState | null

  // ä½ç½®æƒ…å ±
  insertPosition: number
  nestedInsertPath: string
  nestedInsertPosition: number
}

// Propsåž‹å®šç¾©
interface Props {
  parts?: PartSection[]
  commonParts?: PartSection[]
  individualParts?: PartSection[]
  page?: Component[]
}

export function useEditorCore(props: Props, editorEventBus: EditorEventBusType) {
  // ãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãªçŠ¶æ…‹ç®¡ç†
  const state = reactive<EditorCoreState>({
    // ãƒ‡ãƒ¼ã‚¿ç®¡ç†
    editablePageData: [],
    localParts: [],
    localCommonParts: [],
    localIndividualParts: [],

    // UIçŠ¶æ…‹
    activeTab: 'editor',
    dataTab: 'rendered',
    showEditor: false,
    showPartSelector: false,
    showNestedPartSelector: false,

    // ç·¨é›†çŠ¶æ…‹
    selectedComponent: null,
    selectedComponentIndex: null,
    editingText: null,

    // ä½ç½®æƒ…å ±
    insertPosition: 0,
    nestedInsertPath: '',
    nestedInsertPosition: 0,
  })

  // propsã®ç›£è¦–ã¨editablePageDataã®åˆæœŸåŒ–
  watch(
    () => props.page,
    (newPage) => {
      if (Array.isArray(newPage) && newPage.length > 0) {
        state.editablePageData = [...newPage];
      }
    },
    { immediate: true, deep: true }
  )

  // partsã®ç›£è¦–ã¨åˆæœŸåŒ–
  watch(
    () => props.parts,
    (newParts) => {
      if (Array.isArray(newParts)) {
        state.localParts = [...newParts];
      }
    },
    { immediate: true, deep: true }
  )

  // è¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£
  const computedValues = {
    // ç¾åœ¨é¸æŠžã•ã‚Œã¦ã„ã‚‹ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã®ã‚¤ãƒ³ãƒ‡ãƒƒã‚¯ã‚¹
    currentComponentIndex: computed(() => {
      if (state.selectedComponentIndex === null) return -1
      return typeof state.selectedComponentIndex === 'number'
        ? state.selectedComponentIndex
        : parseInt(state.selectedComponentIndex.toString())
    }),

    // ç·¨é›†å¯èƒ½ãªãƒšãƒ¼ã‚¸ãƒ‡ãƒ¼ã‚¿ã®é•·ã•
    pageDataLength: computed(() => state.editablePageData.length),

    // é¸æŠžã•ã‚ŒãŸã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãŒå­˜åœ¨ã™ã‚‹ã‹ã©ã†ã‹
    hasSelectedComponent: computed(() => state.selectedComponent !== null),

    // ç¾åœ¨ç·¨é›†ä¸­ã‹ã©ã†ã‹
    isEditing: computed(() => state.showEditor && state.selectedComponent !== null),

    // Tiptapç·¨é›†ä¸­ã‹ã©ã†ã‹
    isTiptapEditing: computed(() => state.editingText !== null),
  }

  // ã‚¿ãƒ–ç®¡ç†
  const tabActions = {
    switchToTab: (tab: TabType) => {
      state.activeTab = tab
      // ã‚¨ãƒ‡ã‚£ã‚¿ã‚¿ãƒ–ã«åˆ‡ã‚Šæ›¿ãˆãŸæ™‚ã®ã¿ãƒ‡ãƒ¼ã‚¿ã‚¿ãƒ–ã‚’ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°çµæžœã«æˆ»ã™
      if (tab === 'editor') {
        state.dataTab = 'rendered'
      }
    },

    setDataTab: (tab: DataTabType) => {
      state.dataTab = tab
    },
  }

  // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼çŠ¶æ…‹ç®¡ç†
  const editorActions = {
    openEditor: (component: Component, index: number | string) => {
      state.selectedComponent = component
      state.selectedComponentIndex = index
      state.showEditor = true
    },

    closeEditor: () => {
      // ã‚¨ãƒ‡ã‚£ã‚¿ãƒ¼é–‰éŽ–ã‚¤ãƒ™ãƒ³ãƒˆã‚’ç™ºç«ï¼ˆã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—å‡¦ç†ã®ãŸã‚ï¼‰
      editorEventBus.emit('editorClosed', {
        componentId: state.selectedComponent?.id,
        componentIndex: state.selectedComponentIndex
      })

      state.showEditor = false
      state.selectedComponent = null
      state.selectedComponentIndex = null
    },

    openPartSelector: (position: number) => {
      state.insertPosition = position
      state.showPartSelector = true
    },

    closePartSelector: () => {
      state.showPartSelector = false
    },

    openNestedPartSelector: (path: string, position: number) => {
      state.nestedInsertPath = path
      state.nestedInsertPosition = position
      state.showNestedPartSelector = true
    },

    closeNestedPartSelector: () => {
      state.showNestedPartSelector = false
    },
  }

  // Tiptapç·¨é›†çŠ¶æ…‹ç®¡ç†
  const tiptapActions = {
    startTiptapEdit: (
      componentId: string,
      paramName: string,
      hierarchyPath: number[] = [],
      depth: number = 0,
      arrayName?: string,
      itemIndex?: number
    ) => {
      state.editingText = {
        componentId,
        paramName,
        hierarchyPath,
        depth,
        arrayName,
        itemIndex,
      }
    },

    stopTiptapEdit: () => {
      state.editingText = null
    },
  }

  // ãƒ‡ãƒ¼ã‚¿åˆæœŸåŒ–
  const initializeData = () => {
    state.editablePageData = Array.isArray(props.page) ? [...props.page] : []
    state.localParts = Array.isArray(props.parts) ? [...props.parts] : []
    state.localCommonParts = Array.isArray(props.commonParts) ? [...props.commonParts] : []
    state.localIndividualParts = Array.isArray(props.individualParts) ? [...props.individualParts] : []
  }

  // propsã®ç›£è¦–ï¼ˆå³åº§å®Ÿè¡Œã‚‚å«ã‚€ï¼‰
  watch(
    () => props.page,
    newPage => {
      if (Array.isArray(newPage)) {
        state.editablePageData = [...newPage]
      }
    },
    { deep: true, immediate: true }
  )

  watch(
    () => props.parts,
    newParts => {
      if (Array.isArray(newParts)) {
        state.localParts = [...newParts]
      }
    },
    { deep: true, immediate: true }
  )

  watch(
    () => props.commonParts,
    newCommonParts => {
      if (Array.isArray(newCommonParts)) {
        state.localCommonParts = [...newCommonParts]
      }
    },
    { deep: true, immediate: true }
  )

  watch(
    () => props.individualParts,
    newIndividualParts => {
      if (Array.isArray(newIndividualParts)) {
        state.localIndividualParts = [...newIndividualParts]
      }
    },
    { deep: true, immediate: true }
  )

  // åˆæœŸåŒ–å®Ÿè¡Œ
  initializeData()

  return {
    // çŠ¶æ…‹
    ...toRefs(state),

    // è¨ˆç®—ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£
    ...computedValues,

    // ã‚¢ã‚¯ã‚·ãƒ§ãƒ³
    ...tabActions,
    ...editorActions,
    ...tiptapActions,

    // åˆæœŸåŒ–
    initializeData,
  }
}
