<template>
  <Teleport :to="safeModalContainer">
    <div class="zero-code-modal-overlay" @click="$emit('close')">
      <div class="zero-code-modal-content" @click.stop>
        <div class="zero-code-modal-header">
          <h4>
            {{ props.isNested ? 'ãƒã‚¹ãƒˆãƒ‘ãƒ¼ãƒ„ã‚’é¸æŠž' : 'ãƒ‘ãƒ¼ãƒ„ã‚’é¸æŠž' }}
          </h4>
          <button class="zero-code-close-button" @click="$emit('close')">âœ–ï¸</button>
        </div>

        <!-- ãƒ‘ãƒ¼ãƒ„ã‚¿ã‚¤ãƒ—ã‚¿ãƒ– -->
        <div class="zero-code-data-tabs">
          <div class="zero-code-tab-buttons">
            <button
              v-for="tab in partsTabs"
              :key="tab"
              :class="['zero-code-data-tab-button', { active: activePartsTab === tab }]"
              @click="activePartsTab = tab"
            >
              {{ getPartsTabLabel(tab) }}
            </button>
          </div>
        </div>

        <div class="zero-code-modal-body">
          <!-- å…±é€šãƒ‘ãƒ¼ãƒ„ã‚¿ãƒ– -->
          <div v-if="activePartsTab === 'common'" class="zero-code-parts-section">
            <div v-if="commonParts.length > 0" class="zero-code-parts-list">
              <div
                v-for="(partSection, sectionIndex) in commonParts"
                :key="sectionIndex"
                class="zero-code-part-section"
              >
                <h5 class="zero-code-part-section-title">
                  {{ partSection.type }}
                </h5>
                <div class="zero-code-modules-grid">
                  <div
                    v-for="(module, moduleIndex) in partSection.module"
                    :key="moduleIndex"
                    class="zero-code-module-card"
                    @click="selectPart(partSection.type, module)"
                  >
                    <div class="zero-code-module-title">
                      {{ module.title }}
                    </div>
                    <!-- eslint-disable-next-line vue/no-v-html -->
                    <div class="zero-code-module-preview" v-html="renderPartPreview(module.body)" />
                  </div>
                </div>
              </div>
            </div>
            <div v-else class="zero-code-empty-state">
              <p>å…±é€šãƒ‘ãƒ¼ãƒ„ãŒç™»éŒ²ã•ã‚Œã¦ã„ã¾ã›ã‚“ã€‚</p>
            </div>
          </div>

          <!-- å€‹åˆ¥ãƒ‘ãƒ¼ãƒ„ã‚¿ãƒ– -->
          <div v-if="activePartsTab === 'individual'" class="zero-code-parts-section">
            <div v-if="individualParts.length > 0" class="zero-code-parts-list">
              <div
                v-for="(partSection, sectionIndex) in individualParts"
                :key="sectionIndex"
                class="zero-code-part-section"
              >
                <h5 class="zero-code-part-section-title">
                  {{ partSection.type }}
                </h5>
                <div class="zero-code-modules-grid">
                  <div
                    v-for="(module, moduleIndex) in partSection.module"
                    :key="moduleIndex"
                    class="zero-code-module-card"
                    @click="selectPart(partSection.type, module)"
                  >
                    <div class="zero-code-module-title">
                      {{ module.title }}
                    </div>
                    <!-- eslint-disable-next-line vue/no-v-html -->
                    <div class="zero-code-module-preview" v-html="renderPartPreview(module.body)" />
                  </div>
                </div>
              </div>
            </div>
            <div v-else class="zero-code-empty-state">
              <p>å€‹åˆ¥ãƒ‘ãƒ¼ãƒ„ãŒç™»éŒ²ã•ã‚Œã¦ã„ã¾ã›ã‚“ã€‚</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'
  import type { PartSection, Part } from '@types'

  type PartsTab = 'common' | 'individual'

  // Propså®šç¾©
  interface Props {
    parts: PartSection[]
    isNested?: boolean
    modalContainer?: string
  }

  const props = withDefaults(defineProps<Props>(), {
    isNested: false,
    modalContainer: 'body'
  })

  // ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚³ãƒ³ãƒ†ãƒŠã®æœ‰åŠ¹æ€§ã‚’ãƒã‚§ãƒƒã‚¯
  const isModalContainerValid = computed(() => {
    if (!props.modalContainer || props.modalContainer === 'body') {
      return true // bodyã¯å¸¸ã«æœ‰åŠ¹
    }
    
    // DOMã®å­˜åœ¨ç¢ºèªã¯å®Ÿéš›ã«TeleportãŒå®Ÿè¡Œã•ã‚Œã‚‹æ™‚ã«è¡Œã†
    return typeof document !== 'undefined'
  })

  // å®‰å…¨ãªãƒ¢ãƒ¼ãƒ€ãƒ«ã‚³ãƒ³ãƒ†ãƒŠã‚’å–å¾—ï¼ˆãƒ•ã‚©ãƒ¼ãƒ«ãƒãƒƒã‚¯ä»˜ãï¼‰
  const safeModalContainer = computed(() => {
    if (!props.modalContainer || props.modalContainer === 'body') {
      return 'body'
    }
    
    // DOMè¦ç´ ã®å­˜åœ¨ç¢ºèªï¼ˆclient-sideã§ã®ã¿ï¼‰
    if (typeof document !== 'undefined') {
      const container = document.querySelector(props.modalContainer)
      return container ? props.modalContainer : 'body'
    }
    
    return 'body'
  })

  // ãƒªã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãƒ‡ãƒ¼ã‚¿
  const activePartsTab = ref<PartsTab>('common')

  // ã‚¿ãƒ–ã®ä¸€è¦§
  const partsTabs: PartsTab[] = ['common', 'individual']

  // ã‚¿ãƒ–ã®ãƒ©ãƒ™ãƒ«ã‚’å–å¾—
  const getPartsTabLabel = (tab: PartsTab): string => {
    const labels: Record<PartsTab, string> = {
      common: 'å…±é€šãƒ‘ãƒ¼ãƒ„',
      individual: 'å€‹åˆ¥ãƒ‘ãƒ¼ãƒ„',
    }
    return labels[tab]
  }

  // å…±é€šãƒ‘ãƒ¼ãƒ„ã‚’å–å¾—ï¼ˆcategoryãŒcommonã¾ãŸã¯æœªè¨­å®šã®ã‚‚ã®ï¼‰
  const commonParts = computed(() => {
    return props.parts.map(section => ({
      ...section,
      module: section.module.filter(part => {
        const extendedPart = part as any
        return !extendedPart.category || extendedPart.category === 'common'
      })
    })).filter(section => section.module.length > 0)
  })

  // å€‹åˆ¥ãƒ‘ãƒ¼ãƒ„ã‚’å–å¾—ï¼ˆcategoryãŒindividualã®ã‚‚ã®ï¼‰
  const individualParts = computed(() => {
    return props.parts.map(section => ({
      ...section,
      module: section.module.filter(part => {
        const extendedPart = part as any
        return extendedPart.category === 'individual'
      })
    })).filter(section => section.module.length > 0)
  })

  // Emitså®šç¾©
  const emit = defineEmits<{
    close: []
    'select-part': [type: string, module: Part]
  }>()

  // ãƒ‘ãƒ¼ãƒ„ã‚’é¸æŠž
  const selectPart = (type: string, module: Part): void => {
    emit('select-part', type, module)
  }

  // ãƒ‘ãƒ¼ãƒ„ã®ãƒ—ãƒ¬ãƒ“ãƒ¥ãƒ¼ã‚’ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ï¼ˆå®Ÿéš›ã®ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã«è¿‘ã„å½¢ã§ï¼‰
  const renderPartPreview = (body: string): string => {
    if (!body) return ''

    try {
      let preview = body

      // å„ç¨®è¨˜æ³•ã‚’ã‚µãƒ³ãƒ—ãƒ«ãƒ‡ãƒ¼ã‚¿ã§ç½®ãæ›ãˆ

      // 1. @slotè¨˜æ³•ã¯å»ƒæ­¢ã•ã‚ŒãŸãŸã‚ã€å‰Šé™¤å‡¦ç†ã®ã¿
      preview = preview.replace(/@slot\(\$[^)]+\)@/g, '')

        // 1. ãƒ†ã‚­ã‚¹ãƒˆè¨˜æ³• {$param:default} ã‚’ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆå€¤å„ªå…ˆã§ç½®ãæ›ãˆ
      preview = preview.replace(/\{\$([^:]+):([^}]*)\}/g, (match, paramName, defaultValue) => {
        // ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆå€¤ãŒæŒ‡å®šã•ã‚Œã¦ã„ã‚‹å ´åˆã¯å„ªå…ˆã€ãªã‘ã‚Œã°ã‚µãƒ³ãƒ—ãƒ«å€¤ã‚’ä½¿ç”¨
        if (defaultValue && defaultValue.trim()) {
          return defaultValue
        }
        const sampleValues: Record<string, string> = {
          title: 'ã‚µãƒ³ãƒ—ãƒ«ã‚¿ã‚¤ãƒˆãƒ«',
          text: 'ã‚µãƒ³ãƒ—ãƒ«ãƒ†ã‚­ã‚¹ãƒˆã§ã™ã€‚',
          image: '/sample-image.jpg',
          link: '#',
          url: 'https://example.com',
          alt: 'ã‚µãƒ³ãƒ—ãƒ«ç”»åƒ',
          content: 'ã‚µãƒ³ãƒ—ãƒ«ã‚³ãƒ³ãƒ†ãƒ³ãƒ„',
          description: 'ã‚µãƒ³ãƒ—ãƒ«èª¬æ˜Žæ–‡ã§ã™ã€‚',
        }

        return sampleValues[paramName] || `[${paramName}]`
      })

      // 3. é¸æŠžè¨˜æ³• ($param:option1|option2|option3) ã‚’æœ€åˆã®ã‚ªãƒ—ã‚·ãƒ§ãƒ³ã§ç½®ãæ›ãˆ
      preview = preview.replace(/\(\$([^:]+):([^)]+)\)/g, (match, paramName, options) => {
        const optionList = options.split('|').filter((opt: string) => opt.trim())
        return optionList[0] || ''
      })

      // 4. æ¡ä»¶ä»˜ãè¨˜æ³• ($param?:value) ã‚’ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆå€¤ã§ç½®ãæ›ãˆ
      preview = preview.replace(/\(\$([^:?]+)\?:([^)]*)\)/g, (match, paramName, value) => {
        return value || ''
      })

      // 5. z-forè¨˜æ³•ã‚’ã‚µãƒ³ãƒ—ãƒ«ã‚¢ã‚¤ãƒ†ãƒ ã§ç½®ãæ›ãˆ
      preview = preview.replace(
        /z-for-wrapper="[^"]*"/g,
        ''
      )
      preview = preview.replace(
        /z-for="[^"]*"/g,
        ''
      )
      preview = preview.replace(
        /z-slot="[^"]*"/g,
        ''
      )

      // 6. æ®‹ã£ãŸå˜ç´”ãªãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿è¨˜æ³•ã‚’å‡¦ç†
      preview = preview.replace(/\$(\w+)/g, (match, paramName) => {
        const sampleValues: Record<string, string> = {
          title: 'ã‚¿ã‚¤ãƒˆãƒ«',
          text: 'ãƒ†ã‚­ã‚¹ãƒˆ',
          content: 'ã‚³ãƒ³ãƒ†ãƒ³ãƒ„',
        }
        return sampleValues[paramName] || `[${paramName}]`
      })

      return preview
    } catch (error) {
      return body // ã‚¨ãƒ©ãƒ¼ã®å ´åˆã¯å…ƒã®HTMLã‚’è¿”ã™
    }
  }
</script>

