// ã€æ–°è¦ã€‘å‹•çš„ãªæ–°è¦ã‚¢ã‚¤ãƒ†ãƒ åç”Ÿæˆãƒ˜ãƒ«ãƒ‘ãƒ¼é–¢æ•°ï¼ˆãƒãƒ¼ãƒ‰ã‚³ãƒ¼ãƒ‰é™¤åŽ»ï¼‰
function generateNewItemName(originalValue: string, fieldName: string, itemIndex: number): string {
  // 1. æ—¢å­˜ã®ãƒ‘ã‚¿ãƒ¼ãƒ³è§£æž
  const isDebugItem = originalValue.includes('[DEBUG]')
  const hasNumber = /\d+/.test(originalValue)
  const baseValue = originalValue.replace(/\[DEBUG\]|\d+/g, '').trim()
  
  // 2. ç”»åƒURLãƒ»ãƒ‘ã‚¹ç³»ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ã®ç‰¹åˆ¥å‡¦ç†
  if (fieldName.toLowerCase().includes('url') || 
      fieldName.toLowerCase().includes('image') || 
      fieldName.toLowerCase().includes('icon') ||
      originalValue.startsWith('/') || 
      originalValue.includes('.jpg') || 
      originalValue.includes('.png') || 
      originalValue.includes('.gif') || 
      originalValue.includes('.svg')) {
    // ç”»åƒãƒ‘ã‚¹ã®å ´åˆã¯å…ƒã®å€¤ã‚’ä¿æŒï¼ˆé©åˆ‡ãªãƒ‡ãƒ•ã‚©ãƒ«ãƒˆç”»åƒã‚’æä¾›ï¼‰
    return originalValue
  }
  
  // 3. ãƒ•ã‚£ãƒ¼ãƒ«ãƒ‰ååˆ¥ã®é©åˆ‡ãªåå‰ç”Ÿæˆ
  let newName: string
  
  if (fieldName === 'name' && originalValue.includes('ãƒã‚¹ãƒˆ')) {
    newName = `æ–°ã—ã„ãƒã‚¹ãƒˆé …ç›®${itemIndex}`
  } else if (fieldName === 'title' && originalValue.includes('ã‚¢ã‚¤ãƒ†ãƒ ')) {
    newName = `æ–°ã—ã„ã‚¢ã‚¤ãƒ†ãƒ ${itemIndex}`
  } else if (hasNumber) {
    // æ—¢å­˜å€¤ã«ç•ªå·ãŒã‚ã‚‹å ´åˆã¯æ¬¡ã®ç•ªå·ã‚’å‰²ã‚Šå½“ã¦
    newName = `${baseValue}${itemIndex}`
  } else {
    // ä¸€èˆ¬çš„ãªã‚±ãƒ¼ã‚¹
    newName = originalValue.startsWith('æ–°ã—ã„') ? 
      `${originalValue}${itemIndex}` : 
      `æ–°ã—ã„${fieldName}${itemIndex}`
  }
  
  // 4. ãƒ‡ãƒãƒƒã‚°ãƒ¢ãƒ¼ãƒ‰ã®å ´åˆã¯ [DEBUG] ã‚’è¿½åŠ 
  if (isDebugItem) {
    newName += ' [DEBUG]'
  }
  
  return newName
}

// ãƒã‚¹ãƒˆz-forå°‚ç”¨ã®ãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆæŠ½å‡ºãƒ˜ãƒ«ãƒ‘ãƒ¼é–¢æ•°
function extractNestedZForTemplate(template: string, arrayName: string): string | null {
  console.log('ðŸ” [DEBUG] extractNestedZForTemplateé–‹å§‹:', { arrayName, templateLength: template.length })
  
  try {
    // arrayNameã«å¯¾å¿œã™ã‚‹z-forè¦ç´ ã‚’æŽ¢ã™
    const zForPattern = new RegExp(`<[^>]*z-for="${arrayName}"[^>]*>`, 'g')
    const match = template.match(zForPattern)
    
    if (!match || match.length === 0) {
      console.log('ðŸ” [DEBUG] z-forè¦ç´ ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“:', arrayName)
      return null
    }

    // æœ€åˆã«è¦‹ã¤ã‹ã£ãŸz-forè¦ç´ ã®é–‹å§‹ä½ç½®ã‚’å–å¾—
    const startTag = match[0]
    const startIndex = template.indexOf(startTag)
    
    if (startIndex === -1) {
      console.log('ðŸ” [DEBUG] é–‹å§‹ã‚¿ã‚°ã®ä½ç½®ç‰¹å®šã«å¤±æ•—')
      return null
    }

    // é–‹å§‹ã‚¿ã‚°ã®å¾Œã‹ã‚‰å¯¾å¿œã™ã‚‹çµ‚äº†ã‚¿ã‚°ã‚’æŽ¢ã™
    const tagName = startTag.match(/<(\w+)/)?.[1]
    if (!tagName) {
      console.log('ðŸ” [DEBUG] ã‚¿ã‚°åã®æŠ½å‡ºã«å¤±æ•—')
      return null
    }

    // é–‹å§‹ã‚¿ã‚°ã®çµ‚äº†ä½ç½®
    const startTagEnd = startIndex + startTag.length
    
    // å¯¾å¿œã™ã‚‹çµ‚äº†ã‚¿ã‚°ã‚’æŽ¢ã™ï¼ˆãƒã‚¹ãƒˆè€ƒæ…®ï¼‰
    let depth = 1
    let currentPos = startTagEnd
    const openPattern = new RegExp(`<${tagName}\\b[^>]*>`, 'g')
    const closePattern = new RegExp(`</${tagName}>`, 'g')
    
    while (depth > 0 && currentPos < template.length) {
      openPattern.lastIndex = currentPos
      closePattern.lastIndex = currentPos
      
      const nextOpen = openPattern.exec(template)
      const nextClose = closePattern.exec(template)
      
      if (!nextClose) {
        console.log('ðŸ” [DEBUG] çµ‚äº†ã‚¿ã‚°ãŒè¦‹ã¤ã‹ã‚Šã¾ã›ã‚“')
        return null
      }
      
      if (nextOpen && nextOpen.index < nextClose.index) {
        depth++
        currentPos = nextOpen.index + nextOpen[0].length
      } else {
        depth--
        currentPos = nextClose.index + nextClose[0].length
        
        if (depth === 0) {
          // å†…éƒ¨ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã‚’æŠ½å‡º
          const innerContent = template.slice(startTagEnd, nextClose.index)
          console.log('ðŸ” [DEBUG] ãƒã‚¹ãƒˆãƒ†ãƒ³ãƒ—ãƒ¬ãƒ¼ãƒˆæŠ½å‡ºæˆåŠŸ:', {
            arrayName,
            innerContentLength: innerContent.length,
            innerContentPreview: innerContent.slice(0, 100) + (innerContent.length > 100 ? '...' : '')
          })
          return innerContent.trim()
        }
      }
    }
    
    console.log('ðŸ” [DEBUG] ãƒã‚¹ãƒˆè§£æžä¸­ã«ã‚¨ãƒ©ãƒ¼: depth=' + depth)
    return null
    
  } catch (error) {
    console.error('ðŸ” [DEBUG] extractNestedZForTemplateå‡¦ç†ã‚¨ãƒ©ãƒ¼:', error)
    return null
  }
}

