        // "div_nested_zfor_001_outer_items_0" ã®å ´åˆ:
        // - æœ€å¾ŒãŒæ•°å­—ã‚¤ãƒ³ãƒ‡ãƒƒã‚¯ã‚¹: "0"  
        // - ãã®å‰ã® "outer_items" ãŒé…åˆ—å
        // - ã•ã‚‰ã«ãã®å‰ã® "div_nested_zfor_001" ãŒã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆID
        
        const parentIndex = parseInt(parts[parts.length - 1])
        
        // å‹•çš„ã«é…åˆ—åã‚’æŠ½å‡ºï¼ˆãƒãƒ¼ãƒ‰ã‚³ãƒ¼ãƒ‡ã‚£ãƒ³ã‚°ã‚’é™¤åŽ»ï¼‰
        let parentArrayName = ''
