/**
 * HTMLã‚¨ãƒ³ãƒ†ã‚£ãƒ†ã‚£ã‚’ãƒ‡ã‚³ãƒ¼ãƒ‰ã™ã‚‹é–¢æ•°
 * @param str HTMLã‚¨ãƒ³ãƒ†ã‚£ãƒ†ã‚£ã‚’å«ã‚€æ–‡å­—åˆ—
 * @returns ãƒ‡ã‚³ãƒ¼ãƒ‰ã•ã‚ŒãŸæ–‡å­—åˆ—
 */
export function decodeHTMLEntities(str: string): string {
  if (!str) return ''
  return str
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&amp;/g, '&')
    .replace(/&quot;/g, '"')
    .replace(/&#39;/g, "'")
    .replace(/&nbsp;/g, ' ')
}

/**
 * JavaScriptæ–‡å­—åˆ—å†…ã§å®‰å…¨ã«ä½¿ç”¨ã™ã‚‹ãŸã‚ã®ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—å‡¦ç†
 * @param str ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã™ã‚‹æ–‡å­—åˆ—
 * @returns ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã•ã‚ŒãŸæ–‡å­—åˆ—
 */
export function escapeJSString(str: string): string {
  if (!str) return ''
  return str
    .replace(/\\/g, '\\\\') // ãƒãƒƒã‚¯ã‚¹ãƒ©ãƒƒã‚·ãƒ¥
    .replace(/'/g, "\\'") // ã‚·ãƒ³ã‚°ãƒ«ã‚¯ã‚©ãƒ¼ãƒˆ
    .replace(/"/g, '\\"') // ãƒ€ãƒ–ãƒ«ã‚¯ã‚©ãƒ¼ãƒˆ
    .replace(/\n/g, '\\n') // æ”¹è¡Œ
    .replace(/\r/g, '\\r') // ã‚­ãƒ£ãƒªãƒƒã‚¸ãƒªã‚¿ãƒ¼ãƒ³
    .replace(/\t/g, '\\t') // ã‚¿ãƒ–
}

/**
 * HTMLã‚¿ã‚°ã‚’å®‰å…¨ã«å‰Šé™¤ã™ã‚‹é–¢æ•°
 * @param str HTMLã‚¿ã‚°ã‚’å«ã‚€æ–‡å­—åˆ—
 * @returns ãƒ—ãƒ¬ãƒ¼ãƒ³ãƒ†ã‚­ã‚¹ãƒˆ
 */
export function stripHtmlTags(str: string): string {
  if (!str) return ''
  return str.replace(/<[^>]*>/g, '')
}

/**
 * HTMLã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã®ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—é–¢æ•°
 * @param html ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ã™ã‚‹HTMLæ–‡å­—åˆ—
 * @returns ã‚¯ãƒªãƒ¼ãƒ³ã‚¢ãƒƒãƒ—ã•ã‚ŒãŸHTMLæ–‡å­—åˆ—
 */
export function cleanupHtmlContent(html: string): string {
  if (!html) return html

  // æœ«å°¾ã®<br>ã‚¿ã‚°ã‚’å‰Šé™¤ï¼ˆè¤‡æ•°ã®é€£ç¶šã™ã‚‹<br>ã«ã‚‚å¯¾å¿œï¼‰
  let cleaned = html.replace(/(<br\s*\/?>)+$/gi, '')

  // æœ«å°¾ã®ç©ºç™½æ–‡å­—ã‚‚å‰Šé™¤
  cleaned = cleaned.replace(/\s+$/, '')

  return cleaned
}