/**
 * Z-Directiveå‡¦ç†ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£
 * processZDirectivesé–¢æ•°ã‹ã‚‰åˆ†å‰²ã•ã‚ŒãŸæ©Ÿèƒ½åˆ¥ã®å‡¦ç†ãƒ¢ã‚¸ãƒ¥ãƒ¼ãƒ«
 */

import type { Component, ArrayItem } from '@types'
import { logger } from '@shared/utils/logger'

/**
 * JavaScriptæ–‡å­—åˆ—å†…ã§å®‰å…¨ã«ä½¿ç”¨ã™ã‚‹ãŸã‚ã®ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—å‡¦ç†
 * @param str ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã™ã‚‹æ–‡å­—åˆ—
 * @returns ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã•ã‚ŒãŸæ–‡å­—åˆ—
 */
function escapeJSString(str: string): string {
  if (!str) return ''
  return str
    .replace(/\\/g, '\\\\') // ãƒãƒƒã‚¯ã‚¹ãƒ©ãƒƒã‚·ãƒ¥
    .replace(/'/g, "\\'") // ã‚·ãƒ³ã‚°ãƒ«ã‚¯ã‚©ãƒ¼ãƒˆ
    .replace(/"/g, '\\"') // ãƒ€ãƒ–ãƒ«ã‚¯ã‚©ãƒ¼ãƒˆ
    .replace(/\n/g, '\\n') // æ”¹è¡Œ
    .replace(/\r/g, '\\r') // ã‚­ãƒ£ãƒªãƒƒã‚¸ãƒªã‚¿ãƒ¼ãƒ³
    .replace(/\t/g, '\\t') // ã‚¿ãƒ–
}


// é…åˆ—ã‚¢ã‚¤ãƒ†ãƒ å‡¦ç†é–¢æ•°ã‚’å‚ç…§ï¼ˆå¾ªç’°ã‚¤ãƒ³ãƒãƒ¼ãƒˆã‚’é¿ã‘ã‚‹ãŸã‚åž‹ã®ã¿å®šç¾©ï¼‰
let processArrayItemTextReplacement: (
  itemHtml: string,
  item: ArrayItem,
  component: Component,
  template: string,
  arrayName: string
) => string

let processArrayItemParameterReplacement: (itemHtml: string, item: ArrayItem) => string

// é–¢æ•°ã®åˆæœŸåŒ–ï¼ˆuseTemplateProcessorã‹ã‚‰æ³¨å…¥ï¼‰
export function injectArrayProcessors(
  textReplacement: typeof processArrayItemTextReplacement,
  parameterReplacement: typeof processArrayItemParameterReplacement
) {
  processArrayItemTextReplacement = textReplacement
  processArrayItemParameterReplacement = parameterReplacement
}

/**
 * z-ifæ¡ä»¶å‡¦ç†
 */
export function processZIf(doc: Document, component: Component): void {
  const ifElements = doc.querySelectorAll('[z-if]')

  ifElements.forEach((element: Element) => {
    const condition = element.getAttribute('z-if')
    if (condition) {
      const shouldShow = component[condition]
      if (!shouldShow) {
        element.remove()
        // z-ifè¦ç´ ã‚’éžè¡¨ç¤º
      } else {
        // æ¡ä»¶ãŒæº€ãŸã•ã‚ŒãŸå ´åˆã€z-ifå±žæ€§ã‚’é™¤åŽ»
        element.removeAttribute('z-if')
        // z-ifè¦ç´ ã‚’è¡¨ç¤º
      }
    }
  })
}

/**
 * z-for-wrapperå‡¦ç†
 */
export function processZForWrapper(doc: Document, component: Component): void {
  const wrapperElements = doc.querySelectorAll('[z-for-wrapper]')
  if (wrapperElements.length > 0) {
    wrapperElements.forEach((element: Element) => {
      const arrayName = element.getAttribute('z-for-wrapper')
      if (arrayName) {
        const arrayData = component[arrayName]
        if (!Array.isArray(arrayData) || arrayData.length === 0) {
          element.removeAttribute('z-for-wrapper')
        } else {
          element.removeAttribute('z-for-wrapper')
        }
      }
    })
  }
}

