                slotContent += '</div>'
                // å­è¦ç´ ä¿æŒ: æ—¢å­˜ã®é™çš„ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã®ã¿ã‚’ä¿æŒã—ã€å‹•çš„ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã¯ç½®æ›
                const existingContent = slotElement.innerHTML
                // zero-code-* ã‚¯ãƒ©ã‚¹ã®å‹•çš„è¦ç´ ã‚’é™¤å¤–ã—ã¦é™çš„ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã®ã¿å–å¾—
                const tempDiv = document.createElement('div')
